import {Uri} from "@swim/uri";
import {Model, ModelFastener, CompoundModel} from "@swim/model";
import {StatusTrait, IndicatedTrait, EntityGroup} from "@swim/lens";
import {PulseTrait} from "./PulseTrait";
import {MetaMeshEntity} from "./MetaMeshEntity";
import {MetaEdgeEntity} from "./MetaEdgeEntity";

export class FabricGroup extends EntityGroup {
  constructor() {
    super();
    Object.defineProperty(this, "sorted", {
      value: false,
      enumerable: true,
      configurable: true,
    });
    this.initChildModels();
  }

  protected initChildModels(): void {
    this.mesh.injectModel();
    this.edge.injectModel();
  }

  protected createMesh(metaMeshUri: Uri): Model | null {
    const meshModel = new CompoundModel();

    const entityTrait = new MetaMeshEntity(metaMeshUri);
    entityTrait.setTitle("Partitions");
    meshModel.setTrait("entity", entityTrait);

    meshModel.setTrait("status", new StatusTrait());
    meshModel.setTrait("indicated", new IndicatedTrait());
    meshModel.setTrait("pulse", new PulseTrait(metaMeshUri));

    return meshModel;
  }

  protected initMesh(meshModel: Model): void {
    // hook
  }

  protected createEdge(metaEdgeUri: Uri): Model | null {
    const edgeModel = new CompoundModel();

    const entityTrait = new MetaEdgeEntity(metaEdgeUri);
    entityTrait.setTitle("Connections");
    edgeModel.setTrait("entity", entityTrait);

    edgeModel.setTrait("status", new StatusTrait());
    edgeModel.setTrait("indicated", new IndicatedTrait());
    edgeModel.setTrait("pulse", new PulseTrait(metaEdgeUri));

    return edgeModel;
  }

  protected initEdge(edgeModel: Model): void {
    // hook
  }

  @ModelFastener<FabricGroup, Model>({
    type: Model,
    onSetModel(meshModel: Model | null): void {
      if (meshModel !== null) {
        this.owner.initMesh(meshModel);
      }
    },
    createModel(): Model | null {
      const metaMeshUri = Uri.parse("swim:meta:mesh");
      return this.owner.createMesh(metaMeshUri);
    },
  })
  readonly mesh!: ModelFastener<this, Model>;

  @ModelFastener<FabricGroup, Model>({
    type: Model,
    onSetModel(edgeModel: Model | null): void {
      if (edgeModel !== null) {
        this.owner.initEdge(edgeModel);
      }
    },
    createModel(): Model | null {
      const metaEdgeUri = Uri.parse("swim:meta:edge");
      return this.owner.createEdge(metaEdgeUri);
    },
  })
  readonly edge!: ModelFastener<this, Model>;
}
