import {ModelContextType, ModelFlags, Model, CompoundModel} from "@swim/model";
import {StatusVector} from "./StatusVector";
import {StatusTrait} from "./StatusTrait";

export class StatusGroup extends CompoundModel {
  /** @hidden */
  protected aggregateStatus(statusVector: StatusVector): void {
    // hook
  }

  override needsAnalyze(analyzeFlags: ModelFlags, modelContext: ModelContextType<this>): ModelFlags {
    if ((this.modelFlags & Model.NeedsAggregate) === 0) {
      analyzeFlags &= ~Model.NeedsAggregate;
    }
    return analyzeFlags;
  }

  protected override analyzeChildModels(analyzeFlags: ModelFlags, modelContext: ModelContextType<this>,
                                        analyzeChildModel: (this: this, childModel: Model, analyzeFlags: ModelFlags,
                                                            modelContext: ModelContextType<this>) => void): void {
    if ((analyzeFlags & Model.NeedsAggregate) !== 0) {
      this.aggregateChildStatuses(analyzeFlags, modelContext, analyzeChildModel);
    } else {
      super.analyzeChildModels(analyzeFlags, modelContext, analyzeChildModel);
    }
  }

  protected aggregateChildStatuses(analyzeFlags: ModelFlags, modelContext: ModelContextType<this>,
                                   analyzeChildModel: (this: this, childModel: Model, analyzeFlags: ModelFlags,
                                                       modelContext: ModelContextType<this>) => void): void {
    // Accumulate status vectors when aggregating child models.
    let statusVector = StatusVector.empty();
    let statusCount = 0;

    type self = this;
    function aggregateChildStatus(this: self, childModel: Model, analyzeFlags: ModelFlags,
                                  modelContext: ModelContextType<self>): void {
      analyzeChildModel.call(this, childModel, analyzeFlags, modelContext);
      const childStatusTrait = childModel.getTrait(StatusTrait);
      if (childStatusTrait !== null) {
        const childStatus = childStatusTrait.statusVector;
        if (childStatus.isDefined()) {
          statusVector = statusVector.plus(childStatus);
          statusCount += 1;
        }
      }
    }
    super.analyzeChildModels(analyzeFlags, modelContext, aggregateChildStatus);

    if (statusCount !== 0) {
      statusVector = statusVector.times(1 / statusCount);
    }
    this.aggregateStatus(statusVector);
  }

  static override mountFlags: ModelFlags = CompoundModel.mountFlags | Model.NeedsAggregate;
  static override insertChildFlags: ModelFlags = CompoundModel.insertChildFlags | Model.NeedsAggregate;
  static override removeChildFlags: ModelFlags = CompoundModel.removeChildFlags | Model.NeedsAggregate;
}
