import {HtmlView} from "@swim/dom";
import {ComponentModel, ComponentView, ComponentFastener, CompositeComponent} from "@swim/component";
import {ListItem} from "@swim/grid";
import {DrawerView} from "@swim/window";
import {DomainGroup} from "@swim/lens";
import {SurfaceView, MirrorComponent, MirrorPlugin} from "@swim/prism";
import type {ReflectionComponentObserver} from "./ReflectionComponentObserver";

export class ReflectionComponent extends CompositeComponent {
  constructor(plugin: MirrorPlugin) {
    super();
    Object.defineProperty(this, "plugin", {
      value: plugin,
      enumerable: true,
    });
    this.initReflection();
  }

  protected initReflection(): void {
    this.mirror.injectComponent();
  }

  readonly plugin!: MirrorPlugin;

  override readonly componentObservers!: ReadonlyArray<ReflectionComponentObserver>;

  protected initMirror(mirrorComponent: MirrorComponent): void {
    const domainGroup = this.domains.model;
    if (domainGroup !== null) {
      mirrorComponent.domains.setModel(domainGroup);
    }
  }

  protected initListItem(listItem: ListItem): void {
    listItem.icon(this.plugin.icon)
            .label(this.plugin.title);
  }

  protected attachSurface(surfaceView: SurfaceView): void {
    const mirrorComponent = this.mirror.component;
    if (mirrorComponent !== null) {
      surfaceView.setFullBleed(mirrorComponent.isFullBleed());
      mirrorComponent.surface.setView(surfaceView);
    }
  }

  protected detachSurface(surfaceView: SurfaceView): void {
    const mirrorComponent = this.mirror.component;
    if (mirrorComponent !== null) {
      mirrorComponent.surface.setView(null);
    }
  }

  protected attachToolbar(toolbarView: HtmlView): void {
    const mirrorComponent = this.mirror.component;
    if (mirrorComponent !== null) {
      mirrorComponent.toolbar.setView(toolbarView);
    }
  }

  protected detachToolbar(toolbarView: HtmlView): void {
    const mirrorComponent = this.mirror.component;
    if (mirrorComponent !== null) {
      mirrorComponent.toolbar.setView(null);
    }
  }

  protected attachDrawer(drawerView: DrawerView): void {
    const mirrorComponent = this.mirror.component;
    if (mirrorComponent !== null) {
      mirrorComponent.drawer.setView(drawerView);
    }
  }

  protected detachDrawer(drawerView: DrawerView): void {
    const mirrorComponent = this.mirror.component;
    if (mirrorComponent !== null) {
      mirrorComponent.drawer.setView(null);
    }
  }

  activate(): void {
    this.willActivate();
    this.onActivate();
    this.didActivate();
  }

  protected willActivate(): void {
    const componentObservers = this.componentObservers;
    for (let i = 0, n = componentObservers.length; i < n; i += 1) {
      const componentObserver = componentObservers[i]!;
      if (componentObserver.reflectionWillActivate !== void 0) {
        componentObserver.reflectionWillActivate(this);
      }
    }
  }

  protected onActivate(): void {
    // hook
  }

  protected didActivate(): void {
    const componentObservers = this.componentObservers;
    for (let i = 0, n = componentObservers.length; i < n; i += 1) {
      const componentObserver = componentObservers[i]!;
      if (componentObserver.reflectionDidActivate !== void 0) {
        componentObserver.reflectionDidActivate(this);
      }
    }
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const mirrorComponent = this.mirror.component;
    if (mirrorComponent !== null) {
      mirrorComponent.domains.setModel(domainGroup);
    }
  }

  @ComponentFastener<ReflectionComponent, MirrorComponent>({
    type: MirrorComponent,
    onSetComponent(mirrorComponent: MirrorComponent | null): void {
      if (mirrorComponent !== null) {
        this.owner.initMirror(mirrorComponent);
      }
    },
    createComponent(): MirrorComponent | null {
      return this.owner.plugin.createComponent();
    },
  })
  readonly mirror!: ComponentFastener<this, MirrorComponent>;

  @ComponentView<ReflectionComponent, ListItem>({
    type: ListItem,
    observe: true,
    onSetView(listItem: ListItem | null): void {
      if (listItem !== null) {
        this.owner.initListItem(listItem);
      }
    },
    listItemDidPress(listItem: ListItem): void {
      listItem.modalService.dismissModals();
      this.owner.activate();
    },
  })
  readonly listItem!: ComponentView<this, ListItem>;

  @ComponentView<ReflectionComponent, SurfaceView>({
    type: SurfaceView,
    onSetView(newSurfaceView: SurfaceView | null, oldSurfaceView: SurfaceView | null): void {
      if (oldSurfaceView !== null) {
        this.owner.detachSurface(oldSurfaceView);
      }
      if (newSurfaceView !== null) {
        this.owner.attachSurface(newSurfaceView);
      }
    },
  })
  readonly surface!: ComponentView<this, SurfaceView>;

  @ComponentView<ReflectionComponent, HtmlView>({
    type: HtmlView,
    onSetView(newToolbarView: HtmlView | null, oldToolbarView: HtmlView | null): void {
      if (oldToolbarView !== null) {
        this.owner.detachToolbar(oldToolbarView);
      }
      if (newToolbarView !== null) {
        this.owner.attachToolbar(newToolbarView);
      }
    },
  })
  readonly toolbar!: ComponentView<this, HtmlView>;

  @ComponentView<ReflectionComponent, DrawerView>({
    type: DrawerView,
    onSetView(newDrawerView: DrawerView | null, oldDrawerView: DrawerView | null): void {
      if (oldDrawerView !== null) {
        this.owner.detachDrawer(oldDrawerView);
      }
      if (newDrawerView !== null) {
        this.owner.attachDrawer(newDrawerView);
      }
    },
  })
  readonly drawer!: ComponentView<this, DrawerView>;

  @ComponentModel<ReflectionComponent, DomainGroup>({
    type: DomainGroup,
    onSetModel(domainGroup: DomainGroup | null): void {
      if (domainGroup !== null) {
        this.owner.initDomains(domainGroup);
      }
    },
  })
  readonly domains!: ComponentModel<this, DomainGroup>;
}
