import {Look, Feel} from "@swim/theme";
import {View} from "@swim/view";
import {ComponentTrait} from "@swim/component";
import {LinePlotView, LinePlotTrait, LinePlotComponent} from "@swim/chart";
import {Status, StatusVector, StatusTrait} from "@swim/lens";

export class ChartGadgetLinePlotComponent<X, Y> extends LinePlotComponent<X, Y> {
  protected override attachPlotTrait(plotTrait: LinePlotTrait<X, Y>): void {
    super.attachPlotTrait(plotTrait);
    this.status.setTrait(plotTrait.getTrait(StatusTrait));
  }

  protected override detachPlotTrait(plotTrait: LinePlotTrait<X, Y>): void {
    super.detachPlotTrait(plotTrait);
    this.status.setTrait(null);
  }

  protected override initPlotView(plotView: LinePlotView<X, Y>): void {
    super.initPlotView(plotView);
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      plotView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, plotView);
    }
    const plotTrait = this.plot.trait;
    if (plotTrait === null || plotTrait.stroke === null) {
      plotView.stroke.setLook(Look.accentColor, View.Intrinsic);
    }
    if (plotTrait === null || plotTrait.strokeWidth === null) {
      plotView.strokeWidth.setState(2, View.Intrinsic);
    }
  }

  protected initStatusTrait(statusTrait: StatusTrait): void {
    const plotView = this.plot.view;
    if (plotView !== null) {
      plotView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, plotView);
    }
  }

  protected onSetStatusVector(statusVector: StatusVector): void {
    const plotView = this.plot.view;
    if (plotView !== null) {
      this.applyStatusVector(statusVector, plotView);
    }
  }

  protected applyStatusVector(statusVector: StatusVector, plotView: LinePlotView<X, Y>): void {
    const alert = statusVector.get(Status.alert) || 0;
    const warning = statusVector.get(Status.warning) || 0;
    const inactive = statusVector.get(Status.inactive) || 0;
    if (alert !== 0) {
      plotView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
    } else if (warning !== 0) {
      plotView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
    } else {
      plotView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
    }
    if (inactive !== 0) {
      plotView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
    } else {
      plotView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
    }
  }

  @ComponentTrait<ChartGadgetLinePlotComponent<X, Y>, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(newStatusTrait: StatusTrait | null, oldStatusTrait: StatusTrait | null): void {
      if (newStatusTrait !== null) {
        this.owner.initStatusTrait(newStatusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatusVector(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;
}
