import type {Model, Trait} from "@swim/model";
import type {Component} from "@swim/component";
import type {GadgetComponent} from "@swim/widget";
import {PrismPlugin} from "../manager/PrismPlugin";

export abstract class GadgetPlugin extends PrismPlugin {
  abstract createGadget(gadgetTrait: Trait): GadgetComponent | null;

  abstract detectGadgetModel(model: Model): Trait | null;

  abstract detectGadgetComponent(component: Component): GadgetComponent | null;
}
