import {Look, Feel} from "@swim/theme";
import {View} from "@swim/view";
import {GraphicsView, TypesetView} from "@swim/graphics";
import {ComponentTrait} from "@swim/component";
import {SliceView, SliceTrait, SliceComponent} from "@swim/pie";
import {Status, StatusVector, StatusTrait} from "@swim/lens";

export class PieGadgetSliceComponent extends SliceComponent {
  protected override attachSliceTrait(sliceTrait: SliceTrait): void {
    super.attachSliceTrait(sliceTrait);
    this.status.setTrait(sliceTrait.getTrait(StatusTrait));
  }

  protected override detachSliceTrait(sliceTrait: SliceTrait): void {
    super.detachSliceTrait(sliceTrait);
    this.status.setTrait(null);
  }

  protected override initSliceView(sliceView: SliceView): void {
    super.initSliceView(sliceView);
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      sliceView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, sliceView);
    }
    sliceView.sliceColor.setLook(Look.accentColor, View.Intrinsic);
    sliceView.tickColor.setLook(Look.accentColor, View.Intrinsic);
  }

  protected override initLabelView(labelView: GraphicsView): void {
    super.initLabelView(labelView);
    const sliceView = this.slice.view;
    if (sliceView !== null && TypesetView.is(labelView)) {
      labelView.textColor.setLook(Look.backgroundColor, View.Intrinsic);
    }
  }

  protected override initLegendView(legendView: GraphicsView): void {
    super.initLegendView(legendView);
    const sliceView = this.slice.view;
    if (sliceView !== null && TypesetView.is(legendView)) {
      legendView.textColor.setLook(Look.accentColor, View.Intrinsic);
    }
  }

  protected initStatusTrait(statusTrait: StatusTrait): void {
    const sliceView = this.slice.view;
    if (sliceView !== null) {
      sliceView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, sliceView);
    }
  }

  protected onSetStatusVector(statusVector: StatusVector): void {
    const sliceView = this.slice.view;
    if (sliceView !== null) {
      this.applyStatusVector(statusVector, sliceView);
    }
  }

  protected applyStatusVector(statusVector: StatusVector, sliceView: SliceView): void {
    const alert = statusVector.get(Status.alert) || 0;
    const warning = statusVector.get(Status.warning) || 0;
    const inactive = statusVector.get(Status.inactive) || 0;
    if (alert !== 0) {
      sliceView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
    } else if (warning !== 0) {
      sliceView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
    } else {
      sliceView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
    }
    if (inactive !== 0) {
      sliceView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
    } else {
      sliceView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
    }
  }

  @ComponentTrait<PieGadgetSliceComponent, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(newStatusTrait: StatusTrait | null, oldStatusTrait: StatusTrait | null): void {
      if (newStatusTrait !== null) {
        this.owner.initStatusTrait(newStatusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatusVector(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;
}
