import {Equals} from "@swim/util";
import type {GeoShape} from "@swim/geo";
import {Model, TraitModel, GenericTrait} from "@swim/model";
import {StatusTrait, EntityGroup} from "@swim/lens";
import type {DistrictTraitObserver} from "./DistrictTraitObserver";

export abstract class DistrictTrait extends GenericTrait {
  constructor() {
    super();
    Object.defineProperty(this, "minZoom", {
      value: -Infinity,
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "maxZoom", {
      value: Infinity,
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "boundary", {
      value: null,
      enumerable: true,
      configurable: true,
    });
  }

  override readonly traitObservers!: ReadonlyArray<DistrictTraitObserver>;

  readonly minZoom!: number;

  readonly maxZoom!: number;

  setZoomRange(minZoom: number, maxZoom: number): void {
    if (this.minZoom !== minZoom || this.maxZoom !== maxZoom) {
      this.willSetZoomRange(minZoom, maxZoom);
      Object.defineProperty(this, "minZoom", {
        value: minZoom,
        enumerable: true,
        configurable: true,
      });
      Object.defineProperty(this, "maxZoom", {
        value: maxZoom,
        enumerable: true,
        configurable: true,
      });
      this.onSetZoomRange(minZoom, maxZoom);
      this.didSetZoomRange(minZoom, maxZoom);
    }
  }

  protected willSetZoomRange(minZoom: number, maxZoom: number): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.districtWillSetZoomRange !== void 0) {
        traitObserver.districtWillSetZoomRange(minZoom, maxZoom, this);
      }
    }
  }

  protected onSetZoomRange(minZoom: number, maxZoom: number): void {
    if (this.model !== null) {
      this.requireUpdate(Model.NeedsValidate);
    }
  }

  protected didSetZoomRange(minZoom: number, maxZoom: number): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.districtDidSetZoomRange !== void 0) {
        traitObserver.districtDidSetZoomRange(minZoom, maxZoom, this);
      }
    }
  }

  readonly boundary!: GeoShape | null;

  setBoundary(newBoundary: GeoShape | null): void {
    const oldBoundary = this.boundary;
    if (!Equals(newBoundary, oldBoundary)) {
      this.willSetBoundary(newBoundary, oldBoundary);
      Object.defineProperty(this, "boundary", {
        value: newBoundary,
        enumerable: true,
        configurable: true,
      });
      this.onSetBoundary(newBoundary, oldBoundary);
      this.didSetBoundary(newBoundary, oldBoundary);
    }
  }

  protected willSetBoundary(newBoundary: GeoShape | null, oldBoundary: GeoShape | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.districtWillSetBoundary !== void 0) {
        traitObserver.districtWillSetBoundary(newBoundary, oldBoundary, this);
      }
    }
  }

  protected onSetBoundary(newBoundary: GeoShape | null, oldBoundary: GeoShape | null): void {
    if (this.model !== null) {
      this.requireUpdate(Model.NeedsValidate);
    }
  }

  protected didSetBoundary(newBoundary: GeoShape | null, oldBoundary: GeoShape | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.districtDidSetBoundary !== void 0) {
        traitObserver.districtDidSetBoundary(newBoundary, oldBoundary, this);
      }
    }
  }

  protected createSubdistricts(): EntityGroup | null {
    const entityGroup = new EntityGroup();
    entityGroup.setTrait("status", new StatusTrait());
    return entityGroup;
  }

  protected initSubdistricts(entityGroup: EntityGroup): void {
    // hook
  }

  @TraitModel<DistrictTrait, EntityGroup>({
    type: EntityGroup,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initSubdistricts(entityGroup);
      }
    },
    createModel(): EntityGroup | null {
      return this.owner.createSubdistricts();
    }
  })
  readonly subdistricts!: TraitModel<this, EntityGroup>;
}
