import {ModelService} from "@swim/model";
import {StatusGroup} from "../status/StatusGroup";
import type {EntityTrait} from "../entity/EntityTrait";
import {SelectionManager} from "./SelectionManager";
import {SelectionService} from "./SelectionService";
import type {SelectionModelObserver} from "./SelectionModelObserver";

export class SelectionModel extends StatusGroup {
  override readonly modelObservers!: ReadonlyArray<SelectionModelObserver>;

  protected willSelectEntity(entityTrait: EntityTrait, index: number): void {
    const modelObservers = this.modelObservers;
    for (let i = 0, n = modelObservers.length; i < n; i += 1) {
      const modelObserver = modelObservers[i]!;
      if (modelObserver.modelWillSelectEntity !== void 0) {
        modelObserver.modelWillSelectEntity(entityTrait, index, this);
      }
    }
  }

  protected didSelectEntity(entityTrait: EntityTrait, index: number): void {
    const modelObservers = this.modelObservers;
    for (let i = 0, n = modelObservers.length; i < n; i += 1) {
      const modelObserver = modelObservers[i]!;
      if (modelObserver.modelDidSelectEntity !== void 0) {
        modelObserver.modelDidSelectEntity(entityTrait, index, this);
      }
    }
  }

  protected willUnselectEntity(entityTrait: EntityTrait): void {
    const modelObservers = this.modelObservers;
    for (let i = 0, n = modelObservers.length; i < n; i += 1) {
      const modelObserver = modelObservers[i]!;
      if (modelObserver.modelWillUnselectEntity !== void 0) {
        modelObserver.modelWillUnselectEntity(entityTrait, this);
      }
    }
  }

  protected didUnselectEntity(entityTrait: EntityTrait): void {
    const modelObservers = this.modelObservers;
    for (let i = 0, n = modelObservers.length; i < n; i += 1) {
      const modelObserver = modelObservers[i]!;
      if (modelObserver.modelDidUnselectEntity !== void 0) {
        modelObserver.modelDidUnselectEntity(entityTrait, this);
      }
    }
  }

  unselectAll(): void {
    this.selectionService.manager.unselectAll();
  }

  @ModelService<SelectionModel, SelectionManager>({
    extends: SelectionService,
    type: SelectionManager,
    selectionManagerWillSelectEntity(entityTrait: EntityTrait, index: number): void {
      this.owner.willSelectEntity(entityTrait, index);
    },
    selectionManagerDidSelectEntity(entityTrait: EntityTrait, index: number): void {
      this.owner.didSelectEntity(entityTrait, index);
    },
    selectionManagerWillUnselectEntity(entityTrait: EntityTrait): void {
      this.owner.willUnselectEntity(entityTrait);
    },
    selectionManagerDidUnselectEntity(entityTrait: EntityTrait): void {
      this.owner.didUnselectEntity(entityTrait)
    },
  })
  readonly selectionService!: SelectionService<this>;
}
