import type {Value} from "@swim/structure";
import type {Uri} from "@swim/uri";
import {CompoundModel} from "@swim/model";
import type {LaneModelObserver} from "./LaneModelObserver";

export abstract class LaneModel extends CompoundModel {
  /** @hidden */
  ownNodeUri: Uri;
  /** @hidden */
  ownLaneUri: Uri;
  /** @hidden */
  info: Value;

  constructor(nodeUri: Uri, laneUri: Uri, info: Value) {
    super();
    this.ownNodeUri = nodeUri;
    this.ownLaneUri = laneUri;
    this.info = info;
  }

  override readonly modelObservers!: ReadonlyArray<LaneModelObserver>;

  setInfo(info: Value): void {
    this.info = info;
  }

  protected onEvent(value: Value): void {
    const modelObservers = this.modelObservers;
    for (let i = 0, n = modelObservers.length; i < n; i += 1) {
      const modelObserver = modelObservers[i]!;
      if (modelObserver.onLaneEvent !== void 0) {
        modelObserver.onLaneEvent(value, this);
      }
    }
  }
}
