import {Model, ModelManagerService} from "@swim/model";
import type {EntityTrait} from "../entity/EntityTrait";
import {SelectionManager} from "./SelectionManager";

/** @hidden */
export abstract class SelectionService<M extends Model> extends ModelManagerService<M, SelectionManager<M>> {
  get selections(): ReadonlyArray<EntityTrait> {
    return this.manager.selections;
  }

  override initManager(): SelectionManager<M> {
    return SelectionManager.global();
  }
}
