import {Look, Feel} from "@swim/theme";
import {View} from "@swim/view";
import {GraphicsView, TypesetView} from "@swim/graphics";
import {ComponentTrait} from "@swim/component";
import {DialView, DialTrait, DialComponent} from "@swim/gauge";
import {Status, StatusVector, StatusTrait} from "@swim/lens";

export class GaugeGadgetDialComponent extends DialComponent {
  protected override attachDialTrait(dialTrait: DialTrait): void {
    super.attachDialTrait(dialTrait);
    this.status.setTrait(dialTrait.getTrait(StatusTrait));
  }

  protected override detachDialTrait(dialTrait: DialTrait): void {
    super.detachDialTrait(dialTrait);
    this.status.setTrait(null);
  }

  protected override initDialView(dialView: DialView): void {
    super.initDialView(dialView);
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      dialView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, dialView);
    }
    dialView.dialColor.setLook(Look.neutralColor, View.Intrinsic);
    dialView.meterColor.setLook(Look.accentColor, View.Intrinsic);
    dialView.tickColor.setLook(Look.accentColor, View.Intrinsic);
  }

  protected override initLabelView(labelView: GraphicsView): void {
    super.initLabelView(labelView);
    const dialView = this.dial.view;
    if (dialView !== null && TypesetView.is(labelView)) {
      labelView.textColor.setLook(Look.neutralColor, View.Intrinsic);
    }
  }

  protected override initLegendView(legendView: GraphicsView): void {
    super.initLegendView(legendView);
    const dialView = this.dial.view;
    if (dialView !== null && TypesetView.is(legendView)) {
      legendView.textColor.setLook(Look.accentColor, View.Intrinsic);
    }
  }

  protected initStatusTrait(statusTrait: StatusTrait): void {
    const dialView = this.dial.view;
    if (dialView !== null) {
      dialView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, dialView);
    }
  }

  protected onSetStatusVector(statusVector: StatusVector): void {
    const dialView = this.dial.view;
    if (dialView !== null) {
      this.applyStatusVector(statusVector, dialView);
    }
  }

  protected applyStatusVector(statusVector: StatusVector, dialView: DialView): void {
    const alert = statusVector.get(Status.alert) || 0;
    const warning = statusVector.get(Status.warning) || 0;
    const inactive = statusVector.get(Status.inactive) || 0;
    if (alert !== 0) {
      dialView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
    } else if (warning !== 0) {
      dialView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
    } else {
      dialView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
    }
    if (inactive !== 0) {
      dialView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
    } else {
      dialView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
    }
  }

  @ComponentTrait<GaugeGadgetDialComponent, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(newStatusTrait: StatusTrait | null, oldStatusTrait: StatusTrait | null): void {
      if (newStatusTrait !== null) {
        this.owner.initStatusTrait(newStatusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatusVector(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;
}
