import {GenericTrait} from "@swim/model";
import type {EntityTrait} from "../entity/EntityTrait";
import type {DomainTraitObserver} from "./DomainTraitObserver";

export class DomainTrait extends GenericTrait {
  override readonly traitObservers!: ReadonlyArray<DomainTraitObserver>;

  injectEntity(entityModel: EntityTrait): void {
    this.willInjectEntity(entityModel);
    this.onInjectEntity(entityModel);
    this.didInjectEntity(entityModel);
  }

  protected willInjectEntity(entityModel: EntityTrait): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.domainWillInjectEntity !== void 0) {
        traitObserver.domainWillInjectEntity(entityModel, this);
      }
    }
  }

  protected onInjectEntity(entityModel: EntityTrait): void {
    // hook
  }

  protected didInjectEntity(entityModel: EntityTrait): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.domainDidInjectEntity !== void 0) {
        traitObserver.domainDidInjectEntity(entityModel, this);
      }
    }
  }
}
