  @ViewService<ShellView, ViewportManager>({
    type: ViewportManager,
    viewportManagerDidSetViewIdiom(newViewIdiom: ViewIdiom, oldViewIdiom: ViewIdiom): void {
      this.owner.updateViewIdiom(newViewIdiom);
    },
    viewportManagerDidReorient(orientation: OrientationType): void {
      this.owner.updateViewIdiom(this.owner.viewIdiom);
    },
  })
  override readonly viewportService!: ViewportService<this>;

  protected override didMount(): void {
    this.updateViewIdiom(this.viewIdiom);
    const surfaceView = this.surface.view;
    if (surfaceView !== null) {
      this.setFullBleed(surfaceView.fullBleed, surfaceView);
    }
    super.didMount();
  }

  protected override onLayout(viewContext: ViewContextType<this>): void {
    super.onLayout(viewContext);

    const safeArea = viewContext.viewport.safeArea;
    this.edgeInsets.setState({
      insetTop: 0,
      insetRight: safeArea.insetRight,
      insetBottom: safeArea.insetBottom,
      insetLeft: safeArea.insetLeft,
    }, View.Intrinsic);
  }

  protected onSurfaceScroll(event: Event): void {
    const surfaceView = this.surface.view;
    if (surfaceView !== null) {
      surfaceView.requireUpdate(View.NeedsScroll);
    }
  }
