import {Lazy} from "@swim/util";
import type {Model} from "@swim/model";
import {Feel} from "@swim/theme";
import {View} from "@swim/view";
import {Graphics, VectorIcon, HtmlIconView} from "@swim/graphics";
import {
  ComponentService,
  ComponentProperty,
  ComponentTrait,
  ComponentView,
  CompositeComponent,
} from "@swim/component";
import {ListItem} from "@swim/grid";
import {Status, StatusVector, StatusTrait, EntityTrait, DomainTrait} from "@swim/lens";
import {PrismManager, PrismService, EntityPlugin} from "@swim/prism";
import type {CollectionComponentObserver} from "./CollectionComponentObserver";

export class CollectionComponent extends CompositeComponent {
  get domainIcon(): Graphics {
    return CollectionComponent.domainIcon;
  }

  protected initListItem(listItem: ListItem): void {
    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      let icon = entityTrait.icon;
      if (icon === null) {
        icon = this.domainIcon;
      }
      listItem.icon(icon).label(entityTrait.title);
      const iconView = listItem.icon.view;
      if (iconView instanceof HtmlIconView) {
        iconView.iconWidth.setState(24, View.Intrinsic);
        iconView.iconHeight.setState(24, View.Intrinsic);
      }
    }
  }

  protected setFullBleed(fullBleed: boolean): void {
    // hook
  }

  protected initDomain(domainTrait: DomainTrait): void {
    this.entity.setTrait(domainTrait.getTrait(EntityTrait));
    this.status.setTrait(domainTrait.getTrait(StatusTrait));
  }

  protected initEntity(entityTrait: EntityTrait): void {
    const listItem = this.listItem.view;
    if (listItem !== null) {
      listItem.label(entityTrait.title);
    }
  }

  protected initStatus(statusTrait: StatusTrait): void {
    this.onSetStatus(statusTrait.statusVector);
  }

  protected onSetStatus(statusVector: StatusVector): void {
    const listItem = this.listItem.view;
    if (listItem !== null) {
      const alert = statusVector.get(Status.alert) || 0;
      const warning = statusVector.get(Status.warning) || 0;
      const inactive = statusVector.get(Status.inactive) || 0;
      if (alert !== 0) {
        listItem.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
      } else if (warning !== 0) {
        listItem.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
      } else {
        listItem.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
      }
      if (inactive !== 0) {
        listItem.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
      } else {
        listItem.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
      }
    }
  }

  protected injectEntities(model: Model, domainTrait: DomainTrait): void {
    const entityTrait = model.getTrait(EntityTrait);
    if (entityTrait !== null) {
      this.injectEntity(entityTrait, domainTrait);
    }
    type self = this;
    function injectEntity(this: self, childModel: Model): void {
      this.injectEntities(childModel, domainTrait);
    }
    model.forEachChildModel(injectEntity, this);
  }

  protected injectEntity(entittTrait: EntityTrait, domainTrait: DomainTrait): void {
    const plugins = this.prismService.plugins;
    for (let i = 0, n = plugins.length; i < n; i += 1) {
      const plugin = plugins[i];
      if (plugin instanceof EntityPlugin) {
        plugin.injectEntity(entittTrait, domainTrait);
      }
    }
  }

  protected activate(): void {
    this.removeDomain();
  }

  removeDomain(): void {
    const domainTrait = this.domain.trait;
    if (domainTrait !== null) {
      domainTrait.model!.remove();
    }
  }

  override readonly componentObservers!: ReadonlyArray<CollectionComponentObserver>;

  @ComponentProperty<CollectionComponent, boolean>({
    type: Boolean,
    inherit: true,
    didSetState(fullBleed: boolean): void {
      this.owner.setFullBleed(fullBleed);
    },
  })
  readonly fullBleed!: ComponentProperty<this, boolean>;

  @ComponentView<CollectionComponent, ListItem>({
    type: ListItem,
    observe: true,
    onSetView(listItem: ListItem | null): void {
      if (listItem !== null) {
        this.owner.initListItem(listItem);
      }
    },
    listItemDidPress(listItem: ListItem): void {
      this.owner.activate();
    },
  })
  readonly listItem!: ComponentView<this, ListItem>;

  @ComponentTrait<CollectionComponent, DomainTrait>({
    type: DomainTrait,
    observe: true,
    onSetTrait(domainTrait: DomainTrait | null): void {
      if (domainTrait !== null) {
        this.owner.initDomain(domainTrait);
      }
    },
    domainDidInjectEntity(entityTrait: EntityTrait, domainTrait: DomainTrait): void {
      this.owner.injectEntity(entityTrait, domainTrait);
    },
  })
  readonly domain!: ComponentTrait<this, DomainTrait>;

  @ComponentTrait<CollectionComponent, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined, entityTrait: EntityTrait): void {
      const listItem = this.owner.listItem.view;
      if (listItem !== null) {
        listItem.label(title);
      }
    },
    entityDidSetIcon(icon: Graphics | null, entityTrait: EntityTrait): void {
      const listItem = this.owner.listItem.view;
      if (listItem !== null) {
        if (icon === null) {
          icon = this.owner.domainIcon;
        }
        listItem.icon(icon);
      }
    },
  })
  readonly entity!: ComponentTrait<this, EntityTrait>;

  @ComponentTrait<CollectionComponent, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(statusTrait: StatusTrait | null): void {
      if (statusTrait !== null) {
        this.owner.initStatus(statusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatus(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;

  @ComponentService<CollectionComponent, PrismManager>({
    type: PrismManager,
    extends: PrismService,
    observe: false,
  })
  readonly prismService!: PrismService<this>;

  protected override didMount(): void {
    const domainTrait = this.domain.trait;
    if (domainTrait !== null) {
      const model = domainTrait.model;
      if (model !== null) {
        this.injectEntities(model, domainTrait);
      }
    }
    super.didMount();
  }

  @Lazy
  static get domainIcon(): Graphics {
    return VectorIcon.create(24, 24, "M4,4L23,4L20,20L1,20Z");
  }
}
