import {Uri} from "@swim/uri";
import {Model, ModelFastener, CompoundModel} from "@swim/model";
import {StatusTrait, IndicatedTrait, EntityGroup} from "@swim/lens";
import {PulseTrait} from "./PulseTrait";
import {AgentsEntity} from "./AgentsEntity";
import {FabricEntity} from "./FabricEntity";

export class PlaneGroup extends EntityGroup {
  constructor() {
    super();
    Object.defineProperty(this, "sorted", {
      value: false,
      enumerable: true,
      configurable: true,
    });
    this.initChildModels();
  }

  protected initChildModels(): void {
    this.agents.injectModel();
    this.fabric.injectModel();
  }

  protected createAgents(metaAgentsUri: Uri): Model | null {
    const agentsModel = new CompoundModel();

    const entityTrait = new AgentsEntity(metaAgentsUri);
    entityTrait.setTitle("Agents");
    agentsModel.setTrait("entity", entityTrait);

    agentsModel.setTrait("status", new StatusTrait());
    agentsModel.setTrait("indicated", new IndicatedTrait());
    agentsModel.setTrait("pulse", new PulseTrait(metaAgentsUri));

    return agentsModel;
  }

  protected initAgents(agentsModel: Model): void {
    // hook
  }

  protected createFabric(metaEdgeUri: Uri): Model | null {
    const fabricModel = new CompoundModel();

    const entityTrait = new FabricEntity(metaEdgeUri);
    entityTrait.setTitle("Fabric");
    fabricModel.setTrait("entity", entityTrait);

    fabricModel.setTrait("status", new StatusTrait());
    fabricModel.setTrait("indicated", new IndicatedTrait());
    fabricModel.setTrait("pulse", new PulseTrait(metaEdgeUri));

    return fabricModel;
  }

  protected initFabric(fabricModel: Model): void {
    // hook
  }

  @ModelFastener<PlaneGroup, Model>({
    type: Model,
    onSetModel(agentsModel: Model | null): void {
      if (agentsModel !== null) {
        this.owner.initAgents(agentsModel);
      }
    },
    createModel(): Model | null {
      const metaAgentsUri = Uri.parse("swim:meta:mesh");
      return this.owner.createAgents(metaAgentsUri);
    },
  })
  readonly agents!: ModelFastener<this, Model>;

  @ModelFastener<PlaneGroup, Model>({
    type: Model,
    onSetModel(fabricModel: Model | null): void {
      if (fabricModel !== null) {
        this.owner.initFabric(fabricModel);
      }
    },
    createModel(): Model | null {
      const metaEdgeUri = Uri.parse("swim:meta:edge");
      return this.owner.createFabric(metaEdgeUri);
    },
  })
  readonly fabric!: ModelFastener<this, Model>;
}
