import {Lazy} from "@swim/util";
import type {Trait} from "@swim/model";
import {Feel} from "@swim/theme";
import {View} from "@swim/view";
import {HtmlView} from "@swim/dom";
import {Graphics, VectorIcon, HtmlIconView} from "@swim/graphics";
import {
  ComponentFastener,
  ComponentModel,
  ComponentTrait,
  ComponentView,
  CompositeComponent,
} from "@swim/component";
import {DrawerView} from "@swim/window";
import {Status, StatusVector, StatusTrait, EntityTrait, SelectionModel} from "@swim/lens";
import {RefractorComponent} from "../refractor/RefractorComponent";
import {MagnifierHandle} from "./MagnifierHandle";

export class MagnifierComponent extends CompositeComponent {
  constructor() {
    super();
    this.onClickCollapse = this.onClickCollapse.bind(this);
    this.initMagnifier();
  }

  protected initMagnifier(): void {
    this.refractor.injectComponent();
  }

  protected attachPanel(panelView: DrawerView): void {
    this.handle.injectView(panelView);
    this.fixtures.injectView(panelView);
  }

  protected detachPanel(panelView: DrawerView): void {
    panelView.removeAll();
  }

  protected createHandle(): MagnifierHandle | null {
    const handleView = MagnifierHandle.create();
    handleView.flexShrink.setState(0, View.Intrinsic);

    const collapseButton = HtmlIconView.create();
    collapseButton.graphics.setState(MagnifierComponent.collapseIcon, View.Intrinsic);
    collapseButton.cursor.setState("pointer", View.Intrinsic);
    collapseButton.on("click", this.onClickCollapse);
    handleView.accessory.setView(collapseButton);
    handleView.accessory.injectView();

    handleView.modifyMood(Feel.default, [[Feel.primary, 1]]);

    return handleView;
  }

  protected initHandle(handleView: MagnifierHandle): void {
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      this.onSetStatus(statusTrait.statusVector);
    } else {
      this.onSetStatus(StatusVector.empty());
    }
  }

  protected createFixtures(): HtmlView | null {
    const fixturesView = HtmlView.create();
    fixturesView.addClass("fixtures");
    fixturesView.position.setState("relative", View.Intrinsic);
    fixturesView.flexGrow.setState(1, View.Intrinsic);
    fixturesView.overflowX.setState("hidden", View.Intrinsic);
    fixturesView.overflowY.setState("auto", View.Intrinsic);
    fixturesView.pointerEvents.setState("auto", View.Intrinsic);
    return fixturesView;
  }

  protected initFixtures(fixturesView: HtmlView): void {
    const refractorComponent = this.refractor.component;
    if (refractorComponent !== null) {
      refractorComponent.fixtures.setView(fixturesView);
    }
  }

  protected createRefractor(): RefractorComponent | null {
    return new RefractorComponent();
  }

  protected initRefractor(refractorComponent: RefractorComponent): void {
    const fixturesView = this.fixtures.view;
    if (fixturesView !== null) {
      refractorComponent.fixtures.setView(fixturesView);
    }
  }

  protected initSelection(selectionModel: SelectionModel | null): void {
    // hook
  }

  protected onSelectEntity(entityTrait: EntityTrait, selectionModel: SelectionModel): void {
    this.entity.setTrait(entityTrait);
    const panelView = this.panel.view;
    if (panelView !== null) {
      panelView.show();
    }
  }

  protected onUnselectEntity(entityTrait: EntityTrait, selectionModel: SelectionModel): void {
    const selections = selectionModel.selectionService.selections;
    if (selections.length > 0) {
      this.entity.setTrait(selections[selections.length - 1]!);
    } else {
      const panelView = this.panel.view;
      if (panelView !== null) {
        panelView.hide();
      }
      this.entity.setTrait(null);
      this.onSetStatus(StatusVector.empty());
    }
  }

  protected attachEntity(entityTrait: EntityTrait): void {
    this.status.setTrait(entityTrait.getTrait(StatusTrait));
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.label.setView(entityTrait.title);
      handleView.icon.setView(entityTrait.icon);
    }
    const refractorComponent = this.refractor.component;
    if (refractorComponent !== null) {
      refractorComponent.entity.setTrait(entityTrait);
    }
  }

  protected detachEntity(entityTrait: EntityTrait): void {
    const refractorComponent = this.refractor.component;
    if (refractorComponent !== null) {
      refractorComponent.entity.setTrait(null);
    }
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.label.setView(null);
      handleView.icon.setView(null);
    }
    this.status.setTrait(null);
  }

  protected onSetEntityTitle(title: string | undefined): void {
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.label.setView(title);
    }
  }

  protected onSetEntityIcon(icon: Graphics | null): void {
    const handleView = this.handle.view;
    if (handleView !== null) {
      handleView.icon.setView(icon);
    }
  }

  protected initStatus(statusTrait: StatusTrait): void {
    this.onSetStatus(statusTrait.statusVector);
  }

  protected onSetStatus(statusVector: StatusVector): void {
    const handleView = this.handle.view;
    if (handleView !== null) {
      const alert = statusVector.get(Status.alert) || 0;
      const warning = statusVector.get(Status.warning) || 0;
      const inactive = statusVector.get(Status.inactive) || 0;
      if (alert !== 0) {
        handleView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
      } else if (warning !== 0) {
        handleView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
      } else {
        handleView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
      }
      if (inactive !== 0) {
        handleView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
      } else {
        handleView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
      }
    }
  }

  @ComponentView<MagnifierComponent, DrawerView>({
    type: DrawerView,
    onSetView(newPanelView: DrawerView | null, oldPanelView: DrawerView | null): void {
      if (oldPanelView !== null) {
        this.owner.detachPanel(oldPanelView);
      }
      if (newPanelView !== null) {
        this.owner.attachPanel(newPanelView);
      }
    },
  })
  readonly panel!: ComponentView<this, DrawerView>;

  @ComponentView<MagnifierComponent, MagnifierHandle>({
    type: MagnifierHandle,
    onSetView(handleView: MagnifierHandle | null): void {
      if (handleView !== null) {
        this.owner.initHandle(handleView);
      }
    },
    createView(): MagnifierHandle | null {
      return this.owner.createHandle();
    },
  })
  readonly handle!: ComponentView<this, MagnifierHandle>;

  @ComponentView<MagnifierComponent, HtmlView>({
    type: HtmlView,
    onSetView(fixturesView: HtmlView | null): void {
      if (fixturesView !== null) {
        this.owner.initFixtures(fixturesView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createFixtures();
    },
  })
  readonly fixtures!: ComponentView<this, HtmlView>;

  @ComponentFastener<MagnifierComponent, RefractorComponent>({
    type: RefractorComponent,
    onSetComponent(refractorComponent: RefractorComponent | null): void {
      if (refractorComponent !== null) {
        this.owner.initRefractor(refractorComponent);
      }
    },
    createComponent(): RefractorComponent | null {
      return this.owner.createRefractor();
    }
  })
  readonly refractor!: ComponentFastener<this, RefractorComponent>;

  @ComponentTrait<MagnifierComponent, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(newEntityTrait: EntityTrait | null, oldEntityTrait: EntityTrait | null): void {
      if (oldEntityTrait !== null) {
        this.owner.detachEntity(oldEntityTrait);
      }
      if (newEntityTrait !== null) {
        this.owner.attachEntity(newEntityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined, entityTrait: EntityTrait): void {
      this.owner.onSetEntityTitle(title);
    },
    entityDidSetIcon(icon: Graphics | null, entityTrait: EntityTrait): void {
      this.owner.onSetEntityIcon(icon);
    },
    traitDidInsertTrait(memberTrait: Trait, targetTrait: Trait | null): void {
      if (memberTrait instanceof StatusTrait) {
        this.owner.status.setTrait(memberTrait);
      }
    },
  })
  readonly entity!: ComponentTrait<this, EntityTrait>;

  @ComponentTrait<MagnifierComponent, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(statusTrait: StatusTrait | null): void {
      if (statusTrait !== null) {
        this.owner.initStatus(statusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatus(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;

  @ComponentModel<MagnifierComponent, SelectionModel>({
    type: SelectionModel,
    observe: true,
    onSetModel(selectionModel: SelectionModel | null): void {
      if (selectionModel !== null) {
        this.owner.initSelection(selectionModel);
      }
    },
    modelDidSelectEntity(entityTrait: EntityTrait, index: number, selectionModel: SelectionModel): void {
      this.owner.onSelectEntity(entityTrait, selectionModel);
    },
    modelDidUnselectEntity(entityTrait: EntityTrait, selectionModel: SelectionModel): void {
      this.owner.onUnselectEntity(entityTrait, selectionModel);
    },
  })
  readonly selection!: ComponentModel<this, SelectionModel>;

  protected onClickCollapse(event: MouseEvent): void {
    event.stopPropagation();
    //const panelView = this.panel.view;
    //if (panelView !== null) {
    //  panelView.hide();
    //}
    const selectionModel = this.selection.model;
    if (selectionModel !== null) {
      selectionModel.unselectAll();
    }
  }

  @Lazy
  static get collapseIcon(): Graphics {
    return VectorIcon.create(24, 24, "M16.6,8.6L12,13.2L7.4,8.6L6,10L12,16L18,10Z");
  }
}
