import {Lazy} from "@swim/util";
import {Model, ModelManager} from "@swim/model";
import type {EntityTrait} from "../entity/EntityTrait";
import type {SelectionManagerObserver} from "./SelectionManagerObserver";

export interface SelectionOptions {
  multi?: boolean;
}

export class SelectionManager<M extends Model = Model> extends ModelManager<M> {
  constructor() {
    super();
    Object.defineProperty(this, "selections", {
      value: [],
      enumerable: true,
      configurable: true,
    });
  }

  readonly selections!: ReadonlyArray<EntityTrait>;

  selectEntity(entityModel: EntityTrait, options?: SelectionOptions, index?: number): void {
    const selections = this.selections as EntityTrait[];
    if (selections.indexOf(entityModel) < 0) {
      if (options === void 0 || !options.multi) {
        this.unselectAll();
      }
      if (index === void 0) {
        index = selections.length;
      } else {
        if (index < 0) {
          index = selections.length + 1 + index;
        }
        index = Math.min(Math.max(0, index, selections.length));
      }
      this.willSelectEntity(entityModel, index);
      entityModel.willSelect();
      selections.splice(index, 0, entityModel);
      this.onSelectEntity(entityModel, index);
      entityModel.onSelect();
      entityModel.didSelect();
      this.didSelectEntity(entityModel, index);
    }
  }

  protected willSelectEntity(entityModel: EntityTrait, index: number): void {
    this.willObserve(function (modelManagerObserver: SelectionManagerObserver): void {
      if (modelManagerObserver.selectionManagerWillSelectEntity !== void 0) {
        modelManagerObserver.selectionManagerWillSelectEntity(entityModel, index, this);
      }
    });
  }

  protected onSelectEntity(entityModel: EntityTrait, index: number): void {
    // hook
  }

  protected didSelectEntity(entityModel: EntityTrait, index: number): void {
    this.didObserve(function (modelManagerObserver: SelectionManagerObserver): void {
      if (modelManagerObserver.selectionManagerDidSelectEntity !== void 0) {
        modelManagerObserver.selectionManagerDidSelectEntity(entityModel, index, this);
      }
    });
  }

  unselectEntity(entityModel: EntityTrait): void {
    const selections = this.selections as EntityTrait[];
    const index = selections.indexOf(entityModel);
    if (index >= 0) {
      this.willUnselectEntity(entityModel);
      entityModel.willUnselect();
      selections.splice(index, 1);
      this.onUnselectEntity(entityModel);
      entityModel.onUnselect();
      entityModel.didUnselect();
      this.didUnselectEntity(entityModel);
    }
  }

  protected willUnselectEntity(entityModel: EntityTrait): void {
    this.willObserve(function (modelManagerObserver: SelectionManagerObserver): void {
      if (modelManagerObserver.selectionManagerWillUnselectEntity !== void 0) {
        modelManagerObserver.selectionManagerWillUnselectEntity(entityModel, this);
      }
    });
  }

  protected onUnselectEntity(entityModel: EntityTrait): void {
    // hook
  }

  protected didUnselectEntity(entityModel: EntityTrait): void {
    this.didObserve(function (modelManagerObserver: SelectionManagerObserver): void {
      if (modelManagerObserver.selectionManagerDidUnselectEntity !== void 0) {
        modelManagerObserver.selectionManagerDidUnselectEntity(entityModel, this);
      }
    });
  }

  unselectAll(): void {
    const selections = this.selections;
    while (selections.length !== 0) {
      this.unselectEntity(selections[0]!);
    }
  }

  override readonly modelManagerObservers!: ReadonlyArray<SelectionManagerObserver>;

  @Lazy
  static global<M extends Model>(): SelectionManager<M> {
    return new SelectionManager();
  }
}
