import {Angle, Length} from "@swim/math";
import {Trait} from "@swim/model";
import {Look} from "@swim/theme";
import {View} from "@swim/view";
import {HtmlView} from "@swim/dom";
import {GraphicsView, CanvasView} from "@swim/graphics";
import {ComponentView, ComponentViewTrait, ComponentFastener} from "@swim/component";
import {DialView, DialTrait, DialComponent, GaugeView, GaugeTrait, GaugeComponent} from "@swim/gauge";
import type {GadgetComponent} from "../gadget/GadgetComponent";
import {GaugeGadgetDialComponent} from "./GaugeGadgetDialComponent";
import type {GaugeGadgetComponentObserver} from "./GaugeGadgetComponentObserver";

export class GaugeGadgetComponent extends GaugeComponent implements GadgetComponent {
  override readonly componentObservers!: ReadonlyArray<GaugeGadgetComponentObserver>;

  protected override initGaugeView(gaugeView: GaugeView): void {
    super.initGaugeView(gaugeView);
    gaugeView.startAngle.setState(Angle.rad(Math.PI * 3 / 4), View.Intrinsic);
    gaugeView.sweepAngle.setState(Angle.rad(Math.PI * 3 / 2), View.Intrinsic);
    gaugeView.cornerRadius.setState(Length.zero(), View.Intrinsic);
    gaugeView.tickAlign.setState(1, View.Intrinsic);
    gaugeView.tickRadius.setState(Length.pct(40), View.Intrinsic);
    gaugeView.font.setLook(Look.font, View.Intrinsic);
    this.layoutGauge();
  }

  protected layoutGauge(): void {
    const gaugeView = this.gauge.view;
    if (gaugeView !== null) {
      const dialFasteners = gaugeView.dialFasteners;
      const dialCount = dialFasteners.length;
      let hasLegend = false;
      for (let i = 0; i < dialCount; i += 1) {
        const dialView = dialFasteners[i]!.view;
        if (dialView !== null && dialView.legend.view !== null) {
          hasLegend = true;
          break;
        }
      }

      const outerRadius = hasLegend ? 35 : 50;
      gaugeView.outerRadius.setState(Length.pct(outerRadius), View.Intrinsic);
      if (dialCount === 1) {
        gaugeView.innerRadius.setState(Length.pct(outerRadius - 12.5), View.Intrinsic);
      } else {
        gaugeView.innerRadius.setState(Length.pct(outerRadius - 25), View.Intrinsic);
      }
    }
  }

  protected override createDial(dialTrait: DialTrait): DialComponent | null {
    return new GaugeGadgetDialComponent();
  }

  protected override onSetDialView(newDialView: DialView | null, oldDialView: DialView | null,
                                   dialFastener: ComponentFastener<this, DialComponent>): void {
    super.onSetDialView(newDialView, oldDialView, dialFastener);
    this.layoutGauge();
  }

  protected override onSetDialLegendView(newLegendView: GraphicsView | null, oldLegendView: GraphicsView | null,
                                         dialFastener: ComponentFastener<this, DialComponent>): void {
    super.onSetDialLegendView(newLegendView, oldLegendView, dialFastener);
    this.layoutGauge();
  }

  protected initCanvasView(canvasView: CanvasView): void {
    // hook
  }

  protected attachCanvasView(canvasView: CanvasView): void {
    this.gauge.injectView(canvasView);
  }

  protected detachCanvasView(canvasView: CanvasView): void {
    // hook
  }

  protected onSetCanvasView(newCanvasView: CanvasView | null, oldCanvasView: CanvasView | null): void {
    if (oldCanvasView !== null) {
      this.detachCanvasView(oldCanvasView);
    }
    if (newCanvasView !== null) {
      this.attachCanvasView(newCanvasView);
      this.initCanvasView(newCanvasView);
    }
  }

  @ComponentView<GaugeGadgetComponent, CanvasView>({
    type: CanvasView,
    onSetView(newCanvasView: CanvasView | null, oldCanvasView: CanvasView | null): void {
      this.owner.onSetCanvasView(newCanvasView, oldCanvasView);
    },
  })
  readonly canvas!: ComponentView<this, CanvasView>;

  protected initGadgetTrait(gadgetTrait: Trait): void {
    // hook
  }

  protected attachGadgetTrait(gadgetTrait: Trait): void {
    if (gadgetTrait instanceof GaugeTrait) {
      this.gauge.setTrait(gadgetTrait);
    }
  }

  protected detachGadgetTrait(gadgetTrait: Trait): void {
    if (gadgetTrait instanceof GaugeTrait) {
      this.gauge.setTrait(null);
    }
  }

  protected willSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
    const componentObservers = this.componentObservers;
    for (let i = 0, n = componentObservers.length; i < n; i += 1) {
      const componentObserver = componentObservers[i]!;
      if (componentObserver.gadgetWillSetTrait !== void 0) {
        componentObserver.gadgetWillSetTrait(newGadgetTrait, oldGadgetTrait, this);
      }
    }
  }

  protected onSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
    if (oldGadgetTrait !== null) {
      this.detachGadgetTrait(oldGadgetTrait);
    }
    if (newGadgetTrait !== null) {
      this.attachGadgetTrait(newGadgetTrait);
      this.initGadgetTrait(newGadgetTrait);
    }
  }

  protected didSetGadgetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
    const componentObservers = this.componentObservers;
    for (let i = 0, n = componentObservers.length; i < n; i += 1) {
      const componentObserver = componentObservers[i]!;
      if (componentObserver.gadgetDidSetTrait !== void 0) {
        componentObserver.gadgetDidSetTrait(newGadgetTrait, oldGadgetTrait, this);
      }
    }
  }

  createGadgetView(): HtmlView {
    return HtmlView.create();
  }

  protected initGadgetView(gadgetView: HtmlView): void {
    gadgetView.height.setState(140, View.Intrinsic);
    gadgetView.marginLeft.setState(18, View.Intrinsic);
    gadgetView.marginRight.setState(18, View.Intrinsic);
  }

  protected attachGadgetView(gadgetView: HtmlView): void {
    this.canvas.injectView(gadgetView);
  }

  protected detachGadgetView(gadgetView: HtmlView): void {
    // hook
  }

  protected willSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
    const componentObservers = this.componentObservers;
    for (let i = 0, n = componentObservers.length; i < n; i += 1) {
      const componentObserver = componentObservers[i]!;
      if (componentObserver.gadgetWillSetView !== void 0) {
        componentObserver.gadgetWillSetView(newGadgetView, oldGadgetView, this);
      }
    }
  }

  protected onSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
    if (oldGadgetView !== null) {
      this.detachGadgetView(oldGadgetView);
    }
    if (newGadgetView !== null) {
      this.attachGadgetView(newGadgetView);
      this.initGadgetView(newGadgetView);
    }
  }

  protected didSetGadgetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
    const componentObservers = this.componentObservers;
    for (let i = 0, n = componentObservers.length; i < n; i += 1) {
      const componentObserver = componentObservers[i]!;
      if (componentObserver.gadgetDidSetView !== void 0) {
        componentObserver.gadgetDidSetView(newGadgetView, oldGadgetView, this);
      }
    }
  }

  @ComponentViewTrait<GaugeGadgetComponent, HtmlView, Trait>({
    viewType: HtmlView,
    willSetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.willSetGadgetView(newGadgetView, oldGadgetView);
    },
    onSetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.onSetGadgetView(newGadgetView, oldGadgetView);
    },
    didSetView(newGadgetView: HtmlView | null, oldGadgetView: HtmlView | null): void {
      this.owner.didSetGadgetView(newGadgetView, oldGadgetView);
    },
    createView(): HtmlView | null {
      return this.owner.createGadgetView();
    },
    traitType: Trait,
    willSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.willSetGadgetTrait(newGadgetTrait, oldGadgetTrait);
    },
    onSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.onSetGadgetTrait(newGadgetTrait, oldGadgetTrait);
    },
    didSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null): void {
      this.owner.didSetGadgetTrait(newGadgetTrait, oldGadgetTrait);
    },
  })
  readonly gadget!: ComponentViewTrait<this, HtmlView, Trait>;
}
