import {VectorIcon} from "@swim/graphics";
import {ComponentTrait, ComponentView, CompositeComponent} from "@swim/component";
import {ListItem} from "@swim/grid";
import {EntityTrait} from "@swim/lens";

export class SuggestionComponent extends CompositeComponent {
  protected initListItem(listItem: ListItem): void {
    listItem.height(32);

    listItem.icon(VectorIcon.create(24, 24, "M8,8 L19,8 L16,16 L5,16 Z"));

    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      listItem.label(entityTrait.title);
    }
  }

  protected initEntity(entityTrait: EntityTrait): void {
    const listItem = this.listItem.view;
    if (listItem !== null) {
      listItem.label(entityTrait.title);
    }
  }

  @ComponentView<SuggestionComponent, ListItem>({
    type: ListItem,
    onSetView(listItem: ListItem | null): void {
      if (listItem !== null) {
        this.owner.initListItem(listItem);
      }
    },
  })
  readonly listItem!: ComponentView<this, ListItem>;

  @ComponentTrait<SuggestionComponent, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined, entityTrait: EntityTrait): void {
      const listItem = this.owner.listItem.view;
      if (listItem !== null) {
        listItem.label(title);
      }
    },
  })
  readonly entity!: ComponentTrait<this, EntityTrait>;
}
