import type {Timing} from "@swim/mapping";
import {AnyLength, Length} from "@swim/math";
import {Look, MoodVector, ThemeMatrix} from "@swim/theme";
import {
  ViewContextType,
  View,
  ViewAnimator,
  ViewFastener,
  PositionGestureInput,
  PositionGestureDelegate,
} from "@swim/view";
import {HtmlView, HtmlViewController} from "@swim/dom";
import {Graphics, HtmlIconView} from "@swim/graphics";
import {ButtonMembrane} from "@swim/button";
import type {MagnifierHandleObserver} from "./MagnifierHandleObserver";

export class MagnifierHandle extends ButtonMembrane implements PositionGestureDelegate {
  constructor(node: HTMLElement) {
    super(node);
    this.onClick = this.onClick.bind(this);
    this.initHandle();
  }

  protected initHandle(): void {
    this.addClass("magnifier-handle");
    this.position.setState("relative", View.Intrinsic);
    this.flexShrink.setState(0, View.Intrinsic);
    this.marginTop.setState(8, View.Intrinsic);
    this.marginRight.setState(8, View.Intrinsic);
    this.marginBottom.setState(4, View.Intrinsic);
    this.marginLeft.setState(8, View.Intrinsic);
    this.overflowX.setState("hidden", View.Intrinsic);
    this.overflowY.setState("hidden", View.Intrinsic);
    this.pointerEvents.setState("auto", View.Intrinsic);
    this.userSelect.setState("none", View.Intrinsic);
  }

  override readonly viewController!: HtmlViewController & MagnifierHandleObserver | null;

  override readonly viewObservers!: ReadonlyArray<MagnifierHandleObserver>;

  @ViewAnimator({type: Length, inherit: true})
  readonly collapsedWidth!: ViewAnimator<this, Length | undefined, AnyLength | undefined>;

  @ViewAnimator({type: Number, inherit: true, updateFlags: View.NeedsAnimate})
  readonly drawerStretch!: ViewAnimator<this, number | undefined>; // 0 = collapsed; 1 = expanded

  @ViewFastener<MagnifierHandle, HtmlView, Graphics>({
    key: true,
    type: HtmlView,
    onSetView(iconView: HtmlView | null): void {
      if (iconView !== null) {
        this.owner.initIcon(iconView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createIcon();
    },
    insertView(parentView: View, childView: HtmlView, targetView: View | null, key: string | undefined): void {
      parentView.appendChildView(childView, key);
    },
    fromAny(value: HtmlView | Graphics): HtmlView | null {
      if (value instanceof HtmlView) {
        return value;
      } else {
        const iconView = this.owner.createIcon();
        iconView.graphics.setState(value, View.Intrinsic);
        return iconView;
      }
    },
  })
  readonly icon!: ViewFastener<MagnifierHandle, HtmlView, Graphics>;

  protected createIcon(): HtmlIconView {
    return HtmlIconView.create();
  }

  protected initIcon(iconView: HtmlView): void {
    iconView.position.setState("absolute", View.Intrinsic);
    iconView.top.setState(0, View.Intrinsic);
    iconView.right.setState(0, View.Intrinsic);
    iconView.width.setState(this.height.state, View.Intrinsic);
    iconView.height.setState(this.height.state, View.Intrinsic);
    if (iconView instanceof HtmlIconView) {
      iconView.iconWidth.setState(24, View.Intrinsic);
      iconView.iconHeight.setState(24, View.Intrinsic);
      iconView.iconColor.setState(this.getLookOr(Look.backgroundColor, null));
    }
  }

  @ViewFastener<MagnifierHandle, HtmlView, string | undefined>({
    key: true,
    type: HtmlView,
    onSetView(labelView: HtmlView | null): void {
      if (labelView !== null) {
        this.owner.initLabel(labelView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createLabel();
    },
    insertView(parentView: View, childView: HtmlView, targetView: View | null, key: string | undefined): void {
      targetView = this.owner.icon.view;
      parentView.insertChildView(childView, targetView, key);
    },
    fromAny(value: HtmlView | string | undefined): HtmlView | null {
      if (value instanceof HtmlView) {
        return value;
      } else if (typeof value === "string") {
        const labelView = this.owner.createLabel();
        labelView.text(value);
        return labelView;
      } else {
        return null;
      }
    },
  })
  readonly label!: ViewFastener<MagnifierHandle, HtmlView, string | undefined>;

  protected createLabel(): HtmlView {
    const labelView = HtmlView.span.create();
    labelView.display.setState("block", View.Intrinsic);
    labelView.fontFamily.setState("system-ui, 'Open Sans', sans-serif", View.Intrinsic);
    labelView.fontSize.setState(17, View.Intrinsic);
    labelView.whiteSpace.setState("nowrap", View.Intrinsic);
    labelView.textOverflow.setState("ellipsis", View.Intrinsic);
    labelView.overflowX.setState("hidden", View.Intrinsic);
    labelView.overflowY.setState("hidden", View.Intrinsic);
    return labelView;
  }

  protected initLabel(labelView: HtmlView): void {
    labelView.position.setState("absolute", View.Intrinsic);
    labelView.top.setState(0, View.Intrinsic);
    labelView.left.setState(this.height.state, View.Intrinsic);
    labelView.right.setState(this.height.state, View.Intrinsic);
    labelView.textAlign.setState("center", View.Intrinsic);
    if (labelView.color.takesPrecedence(View.Intrinsic)) {
      labelView.color.setState(this.getLookOr(Look.backgroundColor, null), View.Intrinsic);
    }
  }

  @ViewFastener<MagnifierHandle, HtmlView, Graphics>({
    key: true,
    type: HtmlView,
    onSetView(accessoryView: HtmlView | null): void {
      if (accessoryView !== null) {
        this.owner.initAccessory(accessoryView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createAccessory();
    },
    insertView(parentView: View, childView: HtmlView, targetView: View | null, key: string | undefined): void {
      targetView = this.owner.label.view;
      if (targetView === null) {
        targetView = this.owner.icon.view;
      }
      parentView.insertChildView(childView, targetView, key);
    },
    fromAny(value: HtmlView | Graphics): HtmlView | null {
      if (value instanceof HtmlView) {
        return value;
      } else {
        const accessoryView = this.owner.createAccessory();
        accessoryView.graphics.setState(value, View.Intrinsic);
        return accessoryView;
      }
    },
  })
  readonly accessory!: ViewFastener<MagnifierHandle, HtmlView, Graphics>;

  protected createAccessory(): HtmlIconView {
    return HtmlIconView.create();
  }

  protected initAccessory(accessoryView: HtmlView): void {
    accessoryView.position.setState("absolute", View.Intrinsic);
    accessoryView.top.setState(0, View.Intrinsic);
    accessoryView.left.setState(0, View.Intrinsic);
    accessoryView.width.setState(this.height.state, View.Intrinsic);
    accessoryView.height.setState(this.height.state, View.Intrinsic);
    if (accessoryView instanceof HtmlIconView) {
      accessoryView.iconWidth.setState(24, View.Intrinsic);
      accessoryView.iconHeight.setState(24, View.Intrinsic);
      accessoryView.iconColor.setState(this.getLookOr(Look.backgroundColor, null));
    }
  }

  protected override onApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean): void {
    super.onApplyTheme(theme, mood, timing);
    const itemColor = theme.getOr(Look.backgroundColor, mood, null);

    if (this.backgroundColor.takesPrecedence(View.Intrinsic)) {
      this.backgroundColor.setState(theme.getOr(Look.accentColor, mood, null), timing, View.Intrinsic);
    }

    const iconView = this.icon.view;
    if (iconView instanceof HtmlIconView) {
      iconView.iconColor.setState(itemColor, timing, View.Extrinsic);
    }

    const labelView = this.label.view;
    if (labelView !== null) {
      labelView.color.setState(itemColor, timing, View.Intrinsic);
    }

    const accessoryView = this.accessory.view;
    if (accessoryView instanceof HtmlIconView) {
      accessoryView.iconColor.setState(itemColor, timing, View.Extrinsic);
    }
  }

  protected override onMount(): void {
    super.onMount();
    this.on("click", this.onClick);
  }

  protected override onUnmount(): void {
    this.off("click", this.onClick);
    super.onUnmount();
  }

  protected override onAnimate(viewContext: ViewContextType<this>): void {
    super.onAnimate(viewContext);
    this.lineHeight.setState(this.height.state, View.Intrinsic);
    const drawerStretch = this.drawerStretch.value;
    if (drawerStretch !== void 0) {
      const labelView = this.label.view;
      if (labelView !== null) {
        labelView.display.setState(drawerStretch === 0 ? "none" : "block", View.Intrinsic);
        labelView.opacity.setState(drawerStretch, View.Intrinsic);
      }
      const accessoryView = this.accessory.view;
      if (accessoryView !== null) {
        accessoryView.display.setState(drawerStretch === 0 ? "none" : "block", View.Intrinsic);
        accessoryView.opacity.setState(drawerStretch, View.Intrinsic);
      }
    }
  }

  protected override onLayout(viewContext: ViewContextType<this>): void {
    super.onLayout(viewContext);

    if (viewContext.viewIdiom === "mobile") {
      this.height.setState(48, View.Intrinsic);
    } else {
      this.height.setState(36, View.Intrinsic);
    }
    this.lineHeight.setState(this.height.state, View.Intrinsic);

    const iconView = this.icon.view;
    if (iconView !== null) {
      iconView.width.setState(this.height.state, View.Intrinsic);
      iconView.height.setState(this.height.state, View.Intrinsic);
    }

    const labelView = this.label.view;
    if (labelView !== null) {
      labelView.left.setState(this.height.state, View.Intrinsic);
      labelView.right.setState(this.height.state, View.Intrinsic);
    }

    const accessoryView = this.accessory.view;
    if (accessoryView !== null) {
      accessoryView.width.setState(this.height.state, View.Intrinsic);
      accessoryView.height.setState(this.height.state, View.Intrinsic);
    }
  }

  protected override glow(input: PositionGestureInput): void {
    const drawerStretch = this.drawerStretch.getStateOr(0);
    if (drawerStretch === 0) {
      super.glow(input);
    }
  }

  protected onClick(event: MouseEvent): void {
    event.stopPropagation();

    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.magnifierHandleDidPress !== void 0) {
        viewObserver.magnifierHandleDidPress(this);
      }
    }
    const viewController = this.viewController;
    if (viewController !== null && viewController.magnifierHandleDidPress !== void 0) {
      viewController.magnifierHandleDidPress(this);
    }
  }
}
