import {Lazy} from "@swim/util";
import {Feel, MoodMatrix} from "@swim/theme";
import {ViewContext, View} from "@swim/view";
import {HtmlView} from "@swim/dom";
import {Graphics, VectorIcon, EnclosedIcon} from "@swim/graphics";
import {ComponentView, CompositeComponent} from "@swim/component";
import {IconButton} from "@swim/button";
import {ListItem, ListView} from "@swim/grid";
import {DrawerView} from "@swim/window";
import {DeckCard, DeckView, TitleDeckBar, TitleDeckViewController} from "@swim/deck";

export class AtlasLayers extends CompositeComponent {
  protected attachToolbar(toolbarView: HtmlView): void {
    this.layersButton.injectView(toolbarView);
  }

  protected detachToolbar(toolbarView: HtmlView): void {
    this.layersButton.removeView();
  }

  get layersIcon(): Graphics {
    return AtlasLayers.layersIcon;
  }

  protected initLayersButton(layersButton: IconButton): void {
    layersButton.flexShrink.setState(0, View.Intrinsic);
    layersButton.height.setState(null, View.Intrinsic);
    layersButton.pointerEvents.setState("auto", View.Intrinsic);
    layersButton.iconWidth.setState(32, View.Intrinsic);
    layersButton.iconHeight.setState(32, View.Intrinsic);
    layersButton.pushIcon(this.layersIcon);

    layersButton.constraint(layersButton.width.constrain(), "eq", layersButton.height);
  }

  protected onPressLayersButton(layersButton: IconButton): void {
    const drawerView = this.drawer.view;
    if (drawerView !== null) {
      if (drawerView.isHidden()) {
        drawerView.show();
      } else {
        drawerView.hide();
      }
    }
  }

  protected attachDrawer(drawerView: DrawerView): void {
    this.deck.injectView(drawerView);
  }

  protected detachDrawer(drawerView: DrawerView): void {
    drawerView.removeAll();
    drawerView.hide();
  }

  protected createDeck(): DeckView | null {
    const deckView = DeckView.create();
    deckView.outAlign.setState(1, View.Intrinsic);
    deckView.setViewController(new TitleDeckViewController());
    const barView = deckView.bar.view;
    if (barView instanceof TitleDeckBar) {
      barView.backgroundColor.setPrecedence(View.Extrinsic);
      const backButton = barView.backButton.view;
      if (backButton !== null) {
        backButton.closeIcon.setView(barView.createCloseIcon());
        backButton.closeIcon.injectView();
      }
    }
    const cardView = this.card.createView();
    this.card.setView(cardView);
    if (cardView !== null) {
      deckView.pushCard(cardView);
    }
    return deckView;
  }

  protected initDeck(deckView: DeckView): void {
    deckView.flexGrow.setState(1, View.Intrinsic);
  }

  protected createCard(): DeckCard | null {
    const cardView = DeckCard.create();
    cardView.backgroundColor.setPrecedence(View.Extrinsic);
    cardView.cardTitle.setState("Layers");
    return cardView;
  }

  protected initCard(cardView: DeckCard): void {
    this.layerList.injectView(cardView);
  }

  protected initLayerList(layerListView: ListView): void {
    const layer1 = layerListView.append(ListItem)
      .icon(this.layersIcon)
      .label("Layer 1");
    layer1.on("click", (): void => {
      const deckView = this.deck.view;
      if (deckView !== null) {
        const layerCard = DeckCard.create();
        layerCard.backgroundColor.setPrecedence(View.Extrinsic);
        layerCard.cardTitle.setState("Layer 1");
        deckView.pushCard(layerCard);
      }
    });

    const layer2 = layerListView.append(ListItem)
      .icon(this.layersIcon)
      .label("Layer 2");
    layer2.on("click", (): void => {
      const deckView = this.deck.view;
      if (deckView !== null) {
        const layerCard = DeckCard.create();
        layerCard.backgroundColor.setPrecedence(View.Extrinsic);
        layerCard.cardTitle.setState("Layer 2");
        deckView.pushCard(layerCard);
      }
    });

    const layer3 = layerListView.append(ListItem)
      .icon(this.layersIcon)
      .label("Layer 3");
    layer3.on("click", (): void => {
      const deckView = this.deck.view;
      if (deckView !== null) {
        const layerCard = DeckCard.create();
        layerCard.backgroundColor.setPrecedence(View.Extrinsic);
        layerCard.cardTitle.setState("Layer 3");
        deckView.pushCard(layerCard);
      }
    });
  }

  @ComponentView<AtlasLayers, HtmlView>({
    type: HtmlView,
    onSetView(newToolbarView: HtmlView | null, oldToolbarView: HtmlView | null): void {
      if (oldToolbarView !== null) {
        this.owner.detachToolbar(oldToolbarView);
      }
      if (newToolbarView !== null) {
        this.owner.attachToolbar(newToolbarView);
      }
    },
  })
  readonly toolbar!: ComponentView<this, HtmlView>;

  @ComponentView<AtlasLayers, IconButton>({
    type: IconButton,
    observe: true,
    onSetView(layersButton: IconButton | null): void {
      if (layersButton !== null) {
        this.owner.initLayersButton(layersButton);
      }
    },
    buttonDidPress(layersButton: IconButton): void {
      this.owner.onPressLayersButton(layersButton);
    },
  })
  readonly layersButton!: ComponentView<this, IconButton>;

  @ComponentView<AtlasLayers, DrawerView>({
    type: DrawerView,
    onSetView(newDrawerView: DrawerView | null, oldDrawerView: DrawerView | null): void {
      if (oldDrawerView !== null) {
        this.owner.detachDrawer(oldDrawerView);
      }
      if (newDrawerView !== null) {
        this.owner.attachDrawer(newDrawerView);
      }
    },
  })
  readonly drawer!: ComponentView<this, DrawerView>;

  @ComponentView<AtlasLayers, DeckView>({
    type: DeckView,
    observe: true,
    onSetView(deckView: DeckView | null): void {
      if (deckView !== null) {
        this.owner.initDeck(deckView);
      }
    },
    createView(): DeckView | null {
      return this.owner.createDeck();
    },
    viewDidResize(viewContext: ViewContext, deckView: DeckView) {
      const barView = deckView.bar.view;
      if (barView !== null) {
        if (viewContext.viewIdiom === "mobile") {
          barView.barHeight.setState(60, View.Intrinsic);
        } else {
          barView.barHeight.setState(48, View.Intrinsic);
        }
      }
    },
    deckDidPressCloseButton(event: Event | null, deckView: DeckView): void {
      const drawerView = this.owner.drawer.view;
      if (drawerView !== null) {
        drawerView.hide();
      }
    },
  })
  readonly deck!: ComponentView<this, DeckView>;

  @ComponentView<AtlasLayers, DeckCard>({
    type: DeckCard,
    onSetView(cardView: DeckCard | null): void {
      if (cardView !== null) {
        this.owner.initCard(cardView);
      }
    },
    createView(): DeckCard | null {
      return this.owner.createCard();
    },
  })
  readonly card!: ComponentView<this, DeckCard>;

  @ComponentView<AtlasLayers, ListView>({
    type: ListView,
    onSetView(layerListView: ListView | null): void {
      if (layerListView !== null) {
        this.owner.initLayerList(layerListView);
      }
    },
  })
  readonly layerList!: ComponentView<this, ListView>;

  @Lazy
  static get layersIcon(): Graphics {
    const outer = VectorIcon.create(24, 24, "M12,16L19.36,10.27,21,9,12,2,3,9,4.63,10.27Z");
    const inner = VectorIcon.create(24, 24, "M11.99,18.54L4.62,12.81,3,14.07,12,21.07,21,14.07,19.37,12.8Z");
    const innerMoodModifier = MoodMatrix.empty().updatedCol(Feel.default, [[Feel.translucent, 2]], true);
    return EnclosedIcon.create(outer, inner).withInnerMoodModifier(innerMoodModifier);
  }
}
