import {Feel} from "@swim/theme";
import {ComponentTrait} from "@swim/component";
import {CellTrait, CellComponent, RowView, RowTrait, RowComponent} from "@swim/grid";
import {Status, StatusVector, StatusTrait} from "@swim/lens";
import {TableGadgetCellComponent} from "./TableGadgetCellComponent";

export class TableGadgetRowComponent extends RowComponent {
  protected override attachRowTrait(rowTrait: RowTrait): void {
    super.attachRowTrait(rowTrait);
    this.status.setTrait(rowTrait.getTrait(StatusTrait));
  }

  protected override detachRowTrait(rowTrait: RowTrait): void {
    super.detachRowTrait(rowTrait);
    this.status.setTrait(null);
  }

  protected override initRowView(rowView: RowView): void {
    super.initRowView(rowView);
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      rowView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, rowView);
    }
  }

  protected override createCell(cellTrait: CellTrait): CellComponent | null {
    return new TableGadgetCellComponent();
  }

  protected initStatusTrait(statusTrait: StatusTrait): void {
    const rowView = this.row.view;
    if (rowView !== null) {
      rowView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, rowView);
    }
  }

  protected onSetStatusVector(statusVector: StatusVector): void {
    const rowView = this.row.view;
    if (rowView !== null) {
      this.applyStatusVector(statusVector, rowView);
    }
  }

  protected applyStatusVector(statusVector: StatusVector, rowView: RowView): void {
    const alert = statusVector.get(Status.alert) || 0;
    const warning = statusVector.get(Status.warning) || 0;
    const inactive = statusVector.get(Status.inactive) || 0;
    if (alert !== 0) {
      rowView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
    } else if (warning !== 0) {
      rowView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
    } else {
      rowView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
    }
    if (inactive !== 0) {
      rowView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
    } else {
      rowView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
    }
  }

  @ComponentTrait<TableGadgetRowComponent, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(newStatusTrait: StatusTrait | null, oldStatusTrait: StatusTrait | null): void {
      if (newStatusTrait !== null) {
        this.owner.initStatusTrait(newStatusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatusVector(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;
}
