import type {Timing} from "@swim/mapping";
import {Look, Feel, MoodVector, ThemeMatrix} from "@swim/theme";
import {View} from "@swim/view";
import type {HtmlView} from "@swim/dom";
import {WidgetView} from "./WidgetView";

export class WidgetCard extends WidgetView {
  constructor(node: HTMLElement) {
    super(node);
    this.initWidget();
    this.initTheme();
  }

  protected initWidget(): void {
    this.addClass("widget");
    this.position.setState("relative", View.Intrinsic);
    this.borderTopRightRadius.setState(4, View.Intrinsic);
    this.borderBottomRightRadius.setState(4, View.Intrinsic);
    this.borderBottomLeftRadius.setState(4, View.Intrinsic);
    this.borderTopLeftRadius.setState(4, View.Intrinsic);
    this.boxSizing.setState("border-box", View.Intrinsic);
    this.overflowX.setState("hidden", View.Intrinsic);
    this.overflowY.setState("hidden", View.Intrinsic);
    this.pointerEvents.setState("auto", View.Intrinsic);
  }

  protected initTheme(): void {
    this.modifyTheme(Feel.default, [[Feel.raised, 1]]);
  }

  protected override initHeader(headerView: HtmlView): void {
    super.initHeader(headerView);
    headerView.display.setState("flex", View.Intrinsic);
    headerView.justifyContent.setState("space-between", View.Intrinsic);
    headerView.height.setState(36, View.Intrinsic);
    headerView.paddingLeft.setState(18, View.Intrinsic);
    headerView.paddingRight.setState(18, View.Intrinsic);
  }

  protected override initHeaderTitle(titleView: HtmlView): void {
    super.initHeaderTitle(titleView);
    titleView.alignSelf.setState("center", View.Intrinsic);
    if (titleView.color.takesPrecedence(View.Intrinsic)) {
      titleView.color.setState(this.getLookOr(Look.neutralColor, null), View.Intrinsic);
    }
  }

  protected override initHeaderSubtitle(subtitleView: HtmlView): void {
    super.initHeaderSubtitle(subtitleView);
    subtitleView.alignSelf.setState("center", View.Intrinsic);
    if (subtitleView.color.takesPrecedence(View.Intrinsic)) {
      subtitleView.color.setState(this.getLookOr(Look.neutralColor, null), View.Intrinsic);
    }
  }

  protected override initFooter(footerView: HtmlView): void {
    super.initFooter(footerView);
    footerView.display.setState("flex", View.Intrinsic);
    footerView.justifyContent.setState("space-between", View.Intrinsic);
    footerView.height.setState(12, View.Intrinsic);
    footerView.paddingLeft.setState(18, View.Intrinsic);
    footerView.paddingRight.setState(18, View.Intrinsic);
  }

  protected override onApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean): void {
    super.onApplyTheme(theme, mood, timing);

    if (this.backgroundColor.takesPrecedence(View.Intrinsic)) {
      let backgroundColor = theme.getOr(Look.backgroundColor, mood, null);
      if (backgroundColor !== null) {
        backgroundColor = backgroundColor.alpha(0.9);
      }
      this.backgroundColor.setState(backgroundColor, timing, View.Intrinsic);
    }

    const titleView = this.headerTitle.view;
    if (titleView !== null) {
      if (titleView.color.takesPrecedence(View.Intrinsic)) {
        titleView.color.setState(this.getLookOr(Look.neutralColor, null), timing, View.Intrinsic);
      }
    }

    const subtitleView = this.headerSubtitle.view;
    if (subtitleView !== null) {
      if (subtitleView.color.takesPrecedence(View.Intrinsic)) {
        subtitleView.color.setState(this.getLookOr(Look.neutralColor, null), timing, View.Intrinsic);
      }
    }
  }
}
