import {GenericTrait} from "@swim/model";
import type {IndicatorType} from "./IndicatorType";
import type {IndicatorTraitObserver} from "./IndicatorTraitObserver";

export class IndicatorTrait extends GenericTrait {
  constructor(indicatorType: IndicatorType) {
    super();
    Object.defineProperty(this, "indicatorType", {
      value: indicatorType,
      enumerable: true,
      configurable: true,
    });
  }

  override readonly traitObservers!: ReadonlyArray<IndicatorTraitObserver>;

  readonly indicatorType!: IndicatorType;
}
