import type {AnyValue, Value} from "@swim/structure";
import {ModelValueDownlink} from "@swim/model";
import {LocationTrait} from "./LocationTrait";

export class DownlinkLocationTrait extends LocationTrait {
  protected downlinkDidSet(value: Value, oldValue: Value): void {
    // hook
  }

  @ModelValueDownlink<DownlinkLocationTrait, Value, AnyValue>({
    consume: true,
    didSet(newValue: Value, oldValue: Value): void {
      this.owner.downlinkDidSet(newValue, oldValue);
    },
  })
  readonly downlink!: ModelValueDownlink<this, Value, AnyValue>;
}
