import {HtmlView} from "@swim/dom";
import {ComponentModel, ComponentView, ComponentFastener} from "@swim/component";
import {DrawerView} from "@swim/window";
import type {GeoView} from "@swim/map"
import {DomainGroup} from "@swim/lens";
import {SurfaceView, MirrorComponent} from "@swim/prism";
import {AtlasEntityDistrict} from "./AtlasEntityDistrict";
import {AtlasMap} from "./AtlasMap";
import {AtlasLayers} from "./AtlasLayers";

export class AtlasComponent<M extends AtlasMap = AtlasMap> extends MirrorComponent {
  override isFullBleed(): boolean {
    return true;
  }

  protected initRoot(entityDistrict: AtlasEntityDistrict): void {
    this.layers.injectComponent();
  }

  protected initLayers(layersComponent: AtlasLayers): void {
    const toolbarView = this.toolbar.view;
    if (toolbarView !== null) {
      layersComponent.toolbar.setView(toolbarView);
    }
    const drawerView = this.drawer.view;
    if (drawerView !== null) {
      layersComponent.drawer.setView(drawerView);
    }
  }

  protected initLayer(layerView: GeoView): void {
    // hook
  }

  protected initMap(mapComponent: M): void {
    // hook
  }

  protected attachSurface(surfaceView: SurfaceView): void {
    const mapComponent = this.map.injectComponent();
    if (mapComponent !== null) {
      mapComponent.surface.setView(surfaceView);
      const mapView = mapComponent.base.view;
      if (mapView !== null) {
        const domainGroup = this.domains.model;
        if (domainGroup !== null) {
          const entityDistrict = this.root.injectComponent();
          if (entityDistrict !== null) {
            const layerView = entityDistrict.layer.injectView(mapView);
            if (layerView !== null) {
              this.initLayer(layerView);
            }
            entityDistrict.entities.setModel(domainGroup);
          }
        }
      }
    }
  }

  protected detachSurface(surfaceView: SurfaceView): void {
    const mapComponent = this.map.injectComponent();
    if (mapComponent !== null) {
      mapComponent.surface.setView(null);
    }
    this.historyService.replaceHistory({
      permanent: {
        lng: void 0,
        lat: void 0,
        zoom: void 0,
      },
    });
  }

  protected attachToolbar(toolbarView: HtmlView): void {
    const layersComponent = this.layers.component;
    if (layersComponent !== null) {
      layersComponent.toolbar.setView(toolbarView);
    }
  }

  protected detachToolbar(toolbarView: HtmlView): void {
    const layersComponent = this.layers.component;
    if (layersComponent !== null) {
      layersComponent.toolbar.setView(null);
    }
  }

  protected attachDrawer(drawerView: DrawerView): void {
    const layersComponent = this.layers.component;
    if (layersComponent !== null) {
      layersComponent.drawer.setView(drawerView);
    }
  }

  protected detachDrawer(drawerView: DrawerView): void {
    const layersComponent = this.layers.component;
    if (layersComponent !== null) {
      layersComponent.drawer.setView(null);
    }
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const mapComponent = this.map.component;
    if (mapComponent !== null) {
      const mapView = mapComponent.base.view;
      if (mapView !== null) {
        const entityDistrict = this.root.injectComponent();
        if (entityDistrict !== null) {
          const layerView = entityDistrict.layer.injectView(mapView);
          if (layerView !== null) {
            this.initLayer(layerView);
          }
          entityDistrict.entities.setModel(domainGroup);
        }
      }
    }
  }

  @ComponentFastener<AtlasComponent, AtlasEntityDistrict>({
    type: AtlasEntityDistrict,
    onSetComponent(entityDistrict: AtlasEntityDistrict | null): void {
      if (entityDistrict !== null) {
        this.owner.initRoot(entityDistrict);
      }
    },
    createComponent(): AtlasEntityDistrict | null {
      return new AtlasEntityDistrict();
    },
  })
  readonly root!: ComponentFastener<this, AtlasEntityDistrict>;

  @ComponentFastener<AtlasComponent, AtlasLayers>({
    type: AtlasLayers,
    onSetComponent(layersComponent: AtlasLayers | null): void {
      if (layersComponent !== null) {
        this.owner.initLayers(layersComponent);
      }
    },
    createComponent(): AtlasLayers | null {
      return new AtlasLayers();
    },
  })
  readonly layers!: ComponentFastener<this, AtlasLayers>;

  @ComponentFastener<AtlasComponent, AtlasMap>({
    type: AtlasMap,
    onSetComponent(mapComponent: AtlasMap | null): void {
      if (mapComponent !== null) {
        this.owner.initMap(mapComponent);
      }
    },
  })
  readonly map!: ComponentFastener<this, M>;

  @ComponentView<AtlasComponent, SurfaceView>({
    type: SurfaceView,
    onSetView(newSurfaceView: SurfaceView | null, oldSurfaceView: SurfaceView | null): void {
      if (oldSurfaceView !== null) {
        this.owner.detachSurface(oldSurfaceView);
      }
      if (newSurfaceView !== null) {
        this.owner.attachSurface(newSurfaceView);
      }
    },
  })
  override readonly surface!: ComponentView<this, SurfaceView>;

  @ComponentView<AtlasComponent, HtmlView>({
    type: HtmlView,
    onSetView(newToolbarView: HtmlView | null, oldToolbarView: HtmlView | null): void {
      if (oldToolbarView !== null) {
        this.owner.detachToolbar(oldToolbarView);
      }
      if (newToolbarView !== null) {
        this.owner.attachToolbar(newToolbarView);
      }
    },
  })
  override readonly toolbar!: ComponentView<this, HtmlView>;

  @ComponentView<AtlasComponent, DrawerView>({
    type: DrawerView,
    onSetView(newDrawerView: DrawerView | null, oldDrawerView: DrawerView | null): void {
      if (oldDrawerView !== null) {
        this.owner.detachDrawer(oldDrawerView);
      }
      if (newDrawerView !== null) {
        this.owner.attachDrawer(newDrawerView);
      }
    },
  })
  override readonly drawer!: ComponentView<this, DrawerView>;

  @ComponentModel<AtlasComponent, DomainGroup>({
    type: DomainGroup,
    onSetModel(domainGroup: DomainGroup | null): void {
      if (domainGroup !== null) {
        this.owner.initDomains(domainGroup);
      }
    },
  })
  override readonly domains!: ComponentModel<this, DomainGroup>;
}
