  @ViewFastener<WidgetView, HtmlView, string>({
    key: "title",
    type: HtmlView,
    child: false,
    fromAny(value: HtmlView | string): HtmlView | null {
      if (value instanceof HtmlView) {
        return value;
      } else if (typeof value === "string" && this.view instanceof HtmlView) {
        this.view.text(value);
        return this.view;
      } else {
        return this.owner.createHeaderTitle(value);
      }
    },
    willSetView(newTitleView: HtmlView | null, oldTitleView: HtmlView | null): void {
      this.owner.willSetHeaderTitle(newTitleView, oldTitleView);
    },
    onSetView(newTitleView: HtmlView | null, oldTitleView: HtmlView | null): void {
      this.owner.onSetHeaderTitle(newTitleView, oldTitleView);
    },
    didSetView(newTitleView: HtmlView | null, oldTitleView: HtmlView | null): void {
      this.owner.didSetHeaderTitle(newTitleView, oldTitleView);
    },
    get parentView(): View | null {
      return this.owner.header.view;
    },
    createView(): HtmlView | null {
      return this.owner.createHeaderTitle();
    },
  })
  readonly headerTitle!: ViewFastener<this, HtmlView, string>;

