import {View, ViewEdgeInsets, ViewProperty} from "@swim/view";
import {HtmlView, HtmlViewController} from "@swim/dom";
import type {SurfaceViewObserver} from "./SurfaceViewObserver";

export class SurfaceView extends HtmlView {
  constructor(node: HTMLElement) {
    super(node);
    Object.defineProperty(this, "fullBleed", {
      value: false,
      enumerable: true,
      configurable: true,
    });
  }

  override readonly viewController!: (HtmlViewController & SurfaceViewObserver) | null;

  override readonly viewObservers!: ReadonlyArray<SurfaceViewObserver>;

  @ViewProperty<SurfaceView, ViewEdgeInsets | null>({type: Object, state: null, inherit: true})
  readonly edgeInsets!: ViewProperty<this, ViewEdgeInsets | null>;

  readonly fullBleed!: boolean;

  setFullBleed(fullBleed: boolean): void {
    if (this.fullBleed !== fullBleed) {
      this.willSetFullBleed(fullBleed);
      Object.defineProperty(this, "fullBleed", {
        value: fullBleed,
        enumerable: true,
        configurable: true,
      });
      this.onSetFullBleed(fullBleed);
      this.didSetFullBleed(fullBleed);
    }
  }

  protected willSetFullBleed(fullBleed: boolean): void {
    const viewController = this.viewController;
    if (viewController !== null && viewController.surfaceViewWillSetFullBleed !== void 0) {
      viewController.surfaceViewWillSetFullBleed(fullBleed, this);
    }
    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.surfaceViewWillSetFullBleed !== void 0) {
        viewObserver.surfaceViewWillSetFullBleed(fullBleed, this);
      }
    }
  }

  protected onSetFullBleed(fullBleed: boolean): void {
    this.requireUpdate(View.NeedsResize | View.NeedsLayout);
  }

  protected didSetFullBleed(fullBleed: boolean): void {
    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.surfaceViewDidSetFullBleed !== void 0) {
        viewObserver.surfaceViewDidSetFullBleed(fullBleed, this);
      }
    }
    const viewController = this.viewController;
    if (viewController !== null && viewController.surfaceViewDidSetFullBleed !== void 0) {
      viewController.surfaceViewDidSetFullBleed(fullBleed, this);
    }
  }
}
