import {Feel} from "@swim/theme";
import {ComponentTrait} from "@swim/component";
import {CellView, CellTrait, CellComponent} from "@swim/grid";
import {Status, StatusVector, StatusTrait} from "@swim/lens";

export class TableGadgetCellComponent extends CellComponent {
  protected override attachCellTrait(cellTrait: CellTrait): void {
    super.attachCellTrait(cellTrait);
    this.status.setTrait(cellTrait.getTrait(StatusTrait));
  }

  protected override detachCellTrait(cellTrait: CellTrait): void {
    super.detachCellTrait(cellTrait);
    this.status.setTrait(null);
  }

  protected override initCellView(cellView: CellView): void {
    super.initCellView(cellView);
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      cellView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, cellView);
    }
  }

  protected initStatusTrait(statusTrait: StatusTrait): void {
    const cellView = this.cell.view;
    if (cellView !== null) {
      cellView.modifyMood(Feel.default, [[Feel.primary, 1]]);
      this.applyStatusVector(statusTrait.statusVector, cellView);
    }
  }

  protected onSetStatusVector(statusVector: StatusVector): void {
    const cellView = this.cell.view;
    if (cellView !== null) {
      this.applyStatusVector(statusVector, cellView);
    }
  }

  protected applyStatusVector(statusVector: StatusVector, cellView: CellView): void {
    const alert = statusVector.get(Status.alert) || 0;
    const warning = statusVector.get(Status.warning) || 0;
    const inactive = statusVector.get(Status.inactive) || 0;
    if (alert !== 0) {
      cellView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
    } else if (warning !== 0) {
      cellView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
    } else {
      cellView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
    }
    if (inactive !== 0) {
      cellView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
    } else {
      cellView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
    }
  }

  @ComponentTrait<TableGadgetCellComponent, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(newStatusTrait: StatusTrait | null, oldStatusTrait: StatusTrait | null): void {
      if (newStatusTrait !== null) {
        this.owner.initStatusTrait(newStatusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatusVector(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;
}
