import type {Value} from "@swim/structure";
import {Recon} from "@swim/recon";
import {HtmlView} from "@swim/dom";
import {PolygonIcon} from "@swim/graphics";
import {ComponentModel, ComponentView, CompositeComponent} from "@swim/component";
import {TreeCell, IconTreeCell, TreeLeaf, TreeLimb} from "@swim/grid";
import {LaneModel} from "./LaneModel";
import type {LaneComponentObserver} from "./LaneComponentObserver";

export class LaneComponent extends CompositeComponent {
  override readonly componentObservers!: ReadonlyArray<LaneComponentObserver>;

  protected initLimb(treeLimb: TreeLimb): void {
    this.leaf.injectView(treeLimb);
  }

  protected initLeaf(treeLeaf: TreeLeaf): void {
    this.iconCell.injectView(treeLeaf);
    this.nameCell.injectView(treeLeaf);
    this.kindCell.injectView(treeLeaf);
  }

  protected createIconCell(): IconTreeCell {
    const iconCell = IconTreeCell.create()
      .iconWidth(36)
      .graphics(PolygonIcon.create(4, Math.PI / 4));
    return iconCell;
  }

  protected initIconCell(iconCell: IconTreeCell): void {
    // hook
  }

  protected initNameCell(nameCell: TreeCell): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      nameCell.text(laneModel.ownLaneUri.toString());
    }
  }

  protected initKindCell(kindCell: TreeCell): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      kindCell.text(laneModel.info.get("laneType").stringValue(""));
    }
  }

  protected initLane(laneModel: LaneModel): void {
    const nameCell = this.nameCell.view;
    if (nameCell !== null) {
      nameCell.text(laneModel.ownLaneUri.toString());
    }

    const kindCell = this.kindCell.view;
    if (kindCell !== null) {
      kindCell.text(laneModel.info.get("laneType").stringValue(""));
    }
  }

  protected didPressLeaf(): void {
    const componentObservers = this.componentObservers;
    for (let i = 0, n = componentObservers.length; i < n; i += 1) {
      const componentObserver = componentObservers[i]!;
      if (componentObserver.laneComponentDidActivate !== void 0) {
        componentObserver.laneComponentDidActivate(this);
      }
    }
  }

  select(): void {
    const leafView = this.leaf.view;
    if (leafView !== null) {
      leafView.highlight();
    }
  }

  unselect(): void {
    const leafView = this.leaf.view;
    if (leafView !== null) {
      leafView.unhighlight();
    }
  }

  protected attachDetail(detailView: HtmlView | null): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      laneModel.addModelConsumer(this);
    }
  }

  protected detachDetail(detailView: HtmlView | null): void {
    const laneModel = this.lane.model;
    if (laneModel !== null) {
      laneModel.removeModelConsumer(this);
    }
  }

  protected onLaneEvent(value: Value): void {
    const detailView = this.detail.view;
    if (detailView !== null) {
      detailView.append("p").text(Recon.toString(value));
    }
  }

  @ComponentView<LaneComponent, TreeLimb>({
    type: TreeLimb,
    onSetView(treeLimb: TreeLimb | null): void {
      if (treeLimb !== null) {
        this.owner.initLimb(treeLimb);
      }
    },
  })
  readonly limb!: ComponentView<this, TreeLimb>;

  @ComponentView<LaneComponent, TreeLeaf>({
    key: true,
    type: TreeLeaf,
    observe: true,
    onSetView(treeLeaf: TreeLeaf | null): void {
      if (treeLeaf !== null) {
        this.owner.initLeaf(treeLeaf);
      }
    },
    leafDidPress(): void {
      this.owner.didPressLeaf();
    },
  })
  readonly leaf!: ComponentView<this, TreeLeaf>;

  @ComponentView<LaneComponent, IconTreeCell>({
    key: "icon",
    type: IconTreeCell,
    onSetView(iconCell: IconTreeCell | null): void {
      if (iconCell !== null) {
        this.owner.initIconCell(iconCell);
      }
    },
    createView(): IconTreeCell | null {
      return this.owner.createIconCell();
    },
  })
  readonly iconCell!: ComponentView<this, IconTreeCell>;

  @ComponentView<LaneComponent, TreeCell>({
    key: "name",
    type: TreeCell,
    onSetView(nameCell: TreeCell | null): void {
      if (nameCell !== null) {
        this.owner.initNameCell(nameCell);
      }
    },
  })
  readonly nameCell!: ComponentView<this, TreeCell>;

  @ComponentView<LaneComponent, TreeCell>({
    key: "kind",
    type: TreeCell,
    onSetView(kindCell: TreeCell | null): void {
      if (kindCell !== null) {
        this.owner.initKindCell(kindCell);
      }
    },
  })
  readonly kindCell!: ComponentView<this, TreeCell>;

  @ComponentView<LaneComponent, HtmlView>({
    key: true,
    type: HtmlView,
    onSetView(newDetailView: HtmlView | null, oldDetailView: HtmlView | null): void {
      if (oldDetailView !== null) {
        this.owner.detachDetail(oldDetailView);
      }
      if (newDetailView !== null) {
        this.owner.attachDetail(newDetailView);
      }
    },
  })
  readonly detail!: ComponentView<this, HtmlView>;

  @ComponentModel<LaneComponent, LaneModel>({
    type: LaneModel,
    observe: true,
    onSetModel(laneModel: LaneModel | null): void {
      if (laneModel !== null) {
        this.owner.initLane(laneModel);
      }
    },
    onLaneEvent(value: Value, laneModel: LaneModel): void {
      this.owner.onLaneEvent(value);
    },
  })
  readonly lane!: ComponentModel<this, LaneModel>;
}
