import {Equals} from "@swim/util";
import type {IndicatorType} from "./IndicatorType";
import {IndicatorTrait} from "./IndicatorTrait";
import type {ValueIndicatorTraitObserver} from "./ValueIndicatorTraitObserver";

export class ValueIndicatorTrait<T> extends IndicatorTrait {
  constructor(indicatorType: IndicatorType) {
    super(indicatorType);
    Object.defineProperty(this, "value", {
      value: void 0,
      enumerable: true,
      configurable: true,
    });
  }

  override readonly traitObservers!: ReadonlyArray<ValueIndicatorTraitObserver<T>>;

  readonly value!: T | undefined;

  setValue(newValue: T | undefined): void {
    const oldValue = this.value;
    if (!Equals(oldValue, newValue)) {
      this.willSetValue(newValue, oldValue);
      Object.defineProperty(this, "value", {
        value: newValue,
        enumerable: true,
        configurable: true,
      });
      this.onSetValue(newValue, oldValue);
      this.didSetValue(newValue, oldValue);
    }
  }

  protected willSetValue(newValue: T | undefined, oldValue: T | undefined): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.indicatorWillSetValue !== void 0) {
        traitObserver.indicatorWillSetValue(newValue, oldValue, this);
      }
    }
  }

  protected onSetValue(newValue: T | undefined, oldValue: T | undefined): void {
    // hook
  }

  protected didSetValue(newValue: T | undefined, oldValue: T | undefined): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.indicatorDidSetValue !== void 0) {
        traitObserver.indicatorDidSetValue(newValue, oldValue, this);
      }
    }
  }

  get formattedValue(): string {
    let formatted: string | undefined;
    const value = this.value;
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.formatIndicator !== void 0) {
        formatted = traitObserver.formatIndicator(value, this) as string | undefined;
        if (formatted !== void 0) {
          break;
        }
      }
    }
    if (formatted === void 0) {
      formatted = "" + value;
    }
    return formatted;
  }
}
