import {Lazy} from "@swim/util";
import type {Model} from "@swim/model";
import {Feel} from "@swim/theme";
import {HtmlView} from "@swim/dom";
import {Graphics, VectorIcon} from "@swim/graphics";
import {ComponentModel, ComponentTrait, ComponentView, ComponentFastener} from "@swim/component";
import {IconButton} from "@swim/button";
import {TreeRoot, TreeSeed, TreeView} from "@swim/grid";
import {EntityTrait} from "@swim/lens";
import {ActivityComponent} from "@swim/prism";
import type {MetaNodeEntity} from "@swim/fabric";
import {LaneModel} from "./LaneModel";
import {ManifestModel} from "./ManifestModel";
import {LaneComponent} from "./LaneComponent";
import {InventoryView} from "./InventoryView";

export class InventoryComponent extends ActivityComponent {
  constructor() {
    super();
    Object.defineProperty(this, "selectedItem", {
      value: null,
      enumerable: true,
      configurable: true,
    });
  }

  /** @hidden */
  readonly selectedItem!: LaneComponent | null;

  protected initRoot(rootView: InventoryView): void {
    rootView.position("absolute")
            .top(0)
            .right(0)
            .bottom(0)
            .left(0);

    this.masterTree.setView(rootView.masterTree.view);
    this.detail.setView(rootView.detail.view);
    this.closeButton.injectView(rootView);
  }

  protected initMasterTree(masterTreeView: TreeView): void {
    masterTreeView.modifyTheme(Feel.default, [[Feel.primary, 1]]);
    masterTreeView.seed.setState(this.createMasterSeed());

    //const masterTreeStem = TreeStem.create();
    //masterTreeView.setStem(masterTreeStem);
    //this.updateMasterStem(masterTreeStem);

    const childComponents = this.childComponents;
    for (let i = 0; i < childComponents.length; i += 1) {
      const childComponent = childComponents[i]!;
      if (childComponent instanceof LaneComponent) {
        this.onInsertItem(childComponent);
      }
    }
  }

  protected initDetail(detailView: HtmlView): void {
    // hook
  }

  protected createMasterSeed(): TreeSeed {
    const roots = new Array<TreeRoot>();
    roots.push(TreeRoot.create("icon", 0, 0, 64));
    roots.push(TreeRoot.create("name", 1, 1, 200));
    roots.push(TreeRoot.create("kind", 0, 0, 64));
    return TreeSeed.create(roots);
  }

  //protected updateMasterStem(treeStem: TreeStem): void {
  //  let nameVein = treeStem.getChildView("name") as TreeVein | null;
  //  if (nameVein === null) {
  //    nameVein = TreeVein.create().text("Name");
  //    if (nameVein !== null) {
  //      treeStem.append(nameVein, "name");
  //    }
  //  }
  //  let kindVein = treeStem.getChildView("kind") as TreeVein | null;
  //  if (kindVein === null) {
  //    kindVein = TreeVein.create().text("Kind");
  //    if (kindVein !== null) {
  //      treeStem.append(kindVein, "kind");
  //    }
  //  }
  //}

  protected initCloseButton(closeButton: IconButton): void {
    closeButton.position("absolute")
               .top(0)
               .left(0)
               .width(InventoryView.BarHeight)
               .height(InventoryView.BarHeight);
    closeButton.pushIcon(InventoryComponent.closeIcon);
  }

  protected didPressCloseButton(): void {
    const windowView = this.window.view;
    if (windowView !== null) {
      windowView.modalService.dismissModals();
    }
  }

  protected attachWindow(windowView: HtmlView): void {
    this.root.injectView(windowView);
  }

  protected detachWindow(windowView: HtmlView): void {
    windowView.removeAll();
    const manifestModel = this.manifest.model;
    if (manifestModel !== null) {
      manifestModel.remove();
    }
  }

  protected attachEntity(entityTrait: EntityTrait): void {
    const model = entityTrait.model;
    if (model !== null) {
      this.manifest.injectModel(model);
    }
  }

  protected detachEntity(entityTrait: EntityTrait): void {
    this.manifest.removeModel();
  }

  protected initManifest(manifestModel: ManifestModel): void {
    // hook
  }

  protected onInsertLane(childEntity: LaneModel, targetEntity: LaneModel | null): void {
    const id = childEntity.ownLaneUri.toString();
    let componentFastener = this.getComponentFastener(id) as ComponentFastener<this, LaneComponent> | null;
    if (componentFastener === null) {
      componentFastener = new InventoryComponent.ItemFastener(this, id, "item") as ComponentFastener<this, LaneComponent>;
      this.setComponentFastener(id, componentFastener);
      const itemComponent = new LaneComponent();
      itemComponent.lane.setModel(childEntity);
      const targetComponent = targetEntity !== null ? this.getChildComponent(targetEntity.ownLaneUri.toString()) : null;
      this.insertChildComponent(itemComponent, targetComponent, id);
    }
  }

  protected onRemoveLane(childEntity: LaneModel): void {
    const id = childEntity.ownLaneUri.toString();
    const componentFastener = this.getComponentFastener(id) as ComponentFastener<this, LaneComponent> | null;
    if (componentFastener !== null) {
      componentFastener.removeComponent();
      this.setComponentFastener(id, null);
    }
  }

  protected onInsertItem(item: LaneComponent): void {
    const masterTreeView = this.masterTree.view;
    if (masterTreeView !== null) {
      const id = item.lane.model!.ownLaneUri.toString();
      const treeLimb = item.limb.createView();
      if (treeLimb !== null) {
        const targetItem = this.nextChildComponent(item);
        const targetView = targetItem instanceof LaneComponent ? targetItem.limb.view : null;
        masterTreeView.insertChildView(treeLimb, targetView, id);
        item.limb.setView(treeLimb);
      }
    }
  }

  protected onRemoveItem(item: LaneComponent): void {
    const treeLimb = item.limb.view;
    if (treeLimb !== null) {
      treeLimb.remove();
    }
  }

  protected onSelectItem(newItem: LaneComponent): void {
    const oldItem = this.selectedItem;
    if (oldItem !== null) {
      oldItem.unselect();
      oldItem.detail.setView(null);
    }
    const detailView = this.detail.view;
    if (detailView !== null) {
      detailView.removeAll();
      Object.defineProperty(this, "selectedItem", {
        value: newItem,
        enumerable: true,
        configurable: true,
      });
      newItem.select();
      newItem.detail.setView(this.detail.view);
    }
  }

  @ComponentView<InventoryComponent, InventoryView>({
    type: InventoryView,
    onSetView(rootView: InventoryView | null): void {
      if (rootView !== null) {
        this.owner.initRoot(rootView);
      }
    },
  })
  readonly root!: ComponentView<this, InventoryView>;

  @ComponentView<InventoryComponent, TreeView>({
    type: TreeView,
    onSetView(masterTreeView: TreeView | null): void {
      if (masterTreeView !== null) {
        this.owner.initMasterTree(masterTreeView);
      }
    },
  })
  readonly masterTree!: ComponentView<this, TreeView>;

  @ComponentView<InventoryComponent, HtmlView>({
    type: HtmlView,
    onSetView(detailView: HtmlView | null): void {
      if (detailView !== null) {
        this.owner.initDetail(detailView);
      }
    },
  })
  readonly detail!: ComponentView<this, HtmlView>;

  @ComponentView<InventoryComponent, IconButton>({
    type: IconButton,
    observe: true,
    onSetView(closeButton: IconButton | null): void {
      if (closeButton !== null) {
        this.owner.initCloseButton(closeButton);
      }
    },
    buttonDidPress(closeButton: IconButton): void {
      this.owner.didPressCloseButton();
    },
  })
  readonly closeButton!: ComponentView<this, IconButton>;

  static ItemFastener = ComponentFastener.define<InventoryComponent, LaneComponent>({
    type: LaneComponent,
    observe: true,
    onSetComponent(newItem: LaneComponent | null, oldItem: LaneComponent | null): void {
      if (oldItem !== null) {
        this.owner.onRemoveItem(oldItem);
      }
      if (newItem !== null) {
        this.owner.onInsertItem(newItem);
      }
    },
    laneComponentDidActivate(item: LaneComponent): void {
      this.owner.onSelectItem(item);
    },
  });

  @ComponentView<InventoryComponent, HtmlView>({
    type: HtmlView,
    onSetView(newWindowView: HtmlView | null, oldWindowView: HtmlView | null): void {
      if (oldWindowView !== null) {
        this.owner.detachWindow(oldWindowView);
      }
      if (newWindowView !== null) {
        this.owner.attachWindow(newWindowView);
      }
    },
  })
  override readonly window!: ComponentView<this, HtmlView>;

  @ComponentTrait<InventoryComponent, EntityTrait>({
    type: EntityTrait,
    onSetTrait(newEntityTrait: EntityTrait | null, oldEntityTrait: EntityTrait | null): void {
      if (oldEntityTrait !== null) {
        this.owner.detachEntity(oldEntityTrait);
      }
      if (newEntityTrait !== null) {
        this.owner.attachEntity(newEntityTrait);
      }
    },
  })
  override readonly entity!: ComponentTrait<this, EntityTrait>;

  @ComponentModel<InventoryComponent, ManifestModel>({
    type: ManifestModel,
    observe: true,
    onSetModel(manifestModel: ManifestModel | null): void {
      if (manifestModel !== null) {
        this.owner.initManifest(manifestModel);
      }
    },
    createModel(): ManifestModel | null {
      const entityTrait = this.owner.entity.trait! as MetaNodeEntity;
      return new ManifestModel(entityTrait.metaNodeUri);
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null): void {
      if (childModel instanceof LaneModel) {
        this.owner.onInsertLane(childModel, targetModel instanceof LaneModel ? targetModel : null);
      }
    },
    modelWillRemoveChildModel(childModel: Model): void {
      if (childModel instanceof LaneModel) {
        this.owner.onRemoveLane(childModel);
      }
    },
  })
  readonly manifest!: ComponentModel<this, ManifestModel>;

  @Lazy
  static get closeIcon(): Graphics {
    return VectorIcon.create(24, 24, "M19,6.4L17.6,5L12,10.6L6.4,5L5,6.4L10.6,12L5,17.6L6.4,19L12,13.4L17.6,19L19,17.6L13.4,12Z");
  }
}
