import type {Timing} from "@swim/mapping";
import {Look, Feel, MoodVector, ThemeMatrix} from "@swim/theme";
import {View} from "@swim/view";
import {ComponentTrait, ComponentView, CompositeComponent} from "@swim/component";
import {TreeCell} from "@swim/grid";
import {
  Status,
  StatusVector,
  StatusTrait,
  IndicatorTrait,
  ValueIndicatorTrait,
  ValueIndicatorTraitObserver,
} from "@swim/lens";

export class CatalogIndicatorCell extends CompositeComponent {
  protected initCell(treeCell: TreeCell): void {
    const indicatorTrait = this.indicator.trait;
    if (indicatorTrait instanceof ValueIndicatorTrait) {
      treeCell.text(indicatorTrait.formattedValue);
      const statusTrait = indicatorTrait.getTrait(StatusTrait);
      if (statusTrait !== null) {
        this.onSetStatus(statusTrait.statusVector);
      }
    }
  }

  protected initIndicator(indicatorTrait: IndicatorTrait): void {
    const treeCell = this.cell.view;
    if (treeCell !== null && indicatorTrait instanceof ValueIndicatorTrait) {
      treeCell.text(indicatorTrait.formattedValue);
    }
    const statusTrait = indicatorTrait.getTrait(StatusTrait);
    if (statusTrait !== null) {
      this.status.setTrait(statusTrait);
    }
  }

  protected updateIndicator(indicatorTrait: IndicatorTrait): void {
    const treeCell = this.cell.view;
    if (treeCell !== null && indicatorTrait instanceof ValueIndicatorTrait) {
      treeCell.text(indicatorTrait.formattedValue);
    }
  }

  protected initStatus(statusTrait: StatusTrait): void {
    this.onSetStatus(statusTrait.statusVector);
  }

  protected onSetStatus(statusVector: StatusVector): void {
    const treeCell = this.cell.view;
    if (treeCell !== null) {
      const alert = statusVector.get(Status.alert) || 0;
      const warning = statusVector.get(Status.warning) || 0;
      const inactive = statusVector.get(Status.inactive) || 0;
      if (alert !== 0) {
        treeCell.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
      } else if (warning !== 0) {
        treeCell.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, 0]]);
      } else {
        treeCell.modifyMood(Feel.default, [[Feel.warning, 0], [Feel.alert, 0]]);
      }
      if (inactive !== 0) {
        treeCell.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
      } else {
        treeCell.modifyMood(Feel.default, [[Feel.inactive, 0]]);
      }
    }
  }

  /** @hidden */
  protected startConsumingIndicator(): void {
    const indicatorTrait = this.indicator.trait;
    if (indicatorTrait !== null) {
      indicatorTrait.addTraitConsumer(this);
      const indicatorModel = indicatorTrait.model;
      if (indicatorModel !== null) {
        indicatorModel.addModelConsumer(this);
      }
    }
  }

  /** @hidden */
  protected stopConsumingIndicator(): void {
    const indicatorTrait = this.indicator.trait;
    if (indicatorTrait !== null) {
      indicatorTrait.removeTraitConsumer(this);
      const indicatorModel = indicatorTrait.model;
      if (indicatorModel !== null) {
        indicatorModel.removeModelConsumer(this);
      }
    }
  }

  @ComponentView<CatalogIndicatorCell, TreeCell>({
    type: TreeCell,
    observe: true,
    onSetView(treeCell: TreeCell | null): void {
      if (treeCell !== null) {
        this.owner.initCell(treeCell);
      }
    },
    createView(): TreeCell | null {
      return TreeCell.create();
    },
    viewDidMount(treeCell: TreeCell): void {
      if (!treeCell.isCulled()) {
        this.owner.startConsumingIndicator();
      }
    },
    viewWillUnmount(treeCell: TreeCell): void {
      this.owner.stopConsumingIndicator();
    },
    viewDidCull(treeCell: TreeCell): void {
      this.owner.stopConsumingIndicator();
    },
    viewWillUncull(treeCell: TreeCell): void {
      this.owner.startConsumingIndicator();
    },
    viewDidApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean, treeCell: TreeCell): void {
      treeCell.color.setState(theme.getOr(Look.statusColor, mood, null), timing, View.Intrinsic);
    },
  })
  readonly cell!: ComponentView<this, TreeCell>;

  @ComponentTrait<CatalogIndicatorCell, IndicatorTrait, IndicatorTrait, ValueIndicatorTraitObserver<unknown>>({
    extends: void 0,
    type: IndicatorTrait,
    observe: true,
    onSetTrait(indicatorTrait: IndicatorTrait | null): void {
      if (indicatorTrait !== null) {
        this.owner.initIndicator(indicatorTrait);
      }
    },
    indicatorDidSetValue(newValue: unknown, oldValue: unknown, indicatorTrait: IndicatorTrait): void {
      this.owner.updateIndicator(indicatorTrait);
    },
  })
  readonly indicator!: ComponentTrait<this, IndicatorTrait>;

  @ComponentTrait<CatalogIndicatorCell, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(statusTrait: StatusTrait | null): void {
      if (statusTrait !== null) {
        this.owner.initStatus(statusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatus(newStatusVector);
    },
  })
  readonly status!: ComponentTrait<this, StatusTrait>;
}
