import {Lazy} from "@swim/util";
import {Component, ComponentManager} from "@swim/component";
import type {PrismPlugin} from "./PrismPlugin";
import type {PrismManagerObserver} from "./PrismManagerObserver";

export class PrismManager<C extends Component = Component> extends ComponentManager<C> {
  constructor() {
    super();
    Object.defineProperty(this, "plugins", {
      value: [],
      enumerable: true,
    });
  }

  readonly plugins!: ReadonlyArray<PrismPlugin>;

  insertPlugin(plugin: PrismPlugin, index?: number): void {
    const plugins = this.plugins as PrismPlugin[];
    if (plugins.indexOf(plugin) >= 0) {
      return;
    }
    if (index === void 0) {
      index = plugins.length;
    } else {
      if (index < 0) {
        index = plugins.length + 1 + index;
      }
      index = Math.min(Math.max(0, index, plugins.length));
    }
    this.willInsertPlugin(plugin, index);
    plugins.splice(index, 0, plugin);
    this.onInsertPlugin(plugin, index);
    this.didInsertPlugin(plugin, index);
  }

  protected willInsertPlugin(plugin: PrismPlugin, index: number): void {
    this.willObserve(function (componentManagerObserver: PrismManagerObserver): void {
      if (componentManagerObserver.prismManagerWillInsertPlugin !== void 0) {
        componentManagerObserver.prismManagerWillInsertPlugin(plugin, index, this);
      }
    });
  }

  protected onInsertPlugin(plugin: PrismPlugin, index: number): void {
    // hook
  }

  protected didInsertPlugin(plugin: PrismPlugin, index: number): void {
    this.didObserve(function (componentManagerObserver: PrismManagerObserver): void {
      if (componentManagerObserver.prismManagerDidInsertPlugin !== void 0) {
        componentManagerObserver.prismManagerDidInsertPlugin(plugin, index, this);
      }
    });
  }

  removePlugin(plugin: PrismPlugin): void {
    const plugins = this.plugins as PrismPlugin[];
    const index = plugins.indexOf(plugin);
    if (index >= 0) {
      this.willRemovePlugin(plugin);
      plugins.splice(index, 1);
      this.onRemovePlugin(plugin);
      this.didRemovePlugin(plugin);
    }
  }

  protected willRemovePlugin(plugin: PrismPlugin): void {
    this.willObserve(function (componentManagerObserver: PrismManagerObserver): void {
      if (componentManagerObserver.prismManagerWillRemovePlugin !== void 0) {
        componentManagerObserver.prismManagerWillRemovePlugin(plugin, this);
      }
    });
  }

  protected onRemovePlugin(plugin: PrismPlugin): void {
    // hook
  }

  protected didRemovePlugin(plugin: PrismPlugin): void {
    this.didObserve(function (componentManagerObserver: PrismManagerObserver): void {
      if (componentManagerObserver.prismManagerDidRemovePlugin !== void 0) {
        componentManagerObserver.prismManagerDidRemovePlugin(plugin, this);
      }
    });
  }

  override readonly componentManagerObservers!: ReadonlyArray<PrismManagerObserver>;

  @Lazy
  static global<C extends Component>(): PrismManager<C> {
    return new PrismManager();
  }

  static get plugins(): ReadonlyArray<PrismPlugin> {
    return PrismManager.global().plugins;
  }

  static insertPlugin(plugin: PrismPlugin, index?: number): void {
    PrismManager.global().insertPlugin(plugin, index);
  }

  static removePlugin(plugin: PrismPlugin): void {
    PrismManager.global().removePlugin(plugin);
  }
}
