import {ConstraintGroup} from "@swim/constraint";
import {View, ViewPropertyConstraint, ViewFastener} from "@swim/view";
import {HtmlView} from "@swim/dom";
import {TreeView} from "@swim/grid";

export class InventoryView extends HtmlView {
  constructor(node: HTMLElement) {
    super(node);
    Object.defineProperty(this, "masterLayout", {
      value: new ConstraintGroup(this).constrain(),
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "mobileLayout", {
      value: new ConstraintGroup(this),
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "desktopLayout", {
      value: new ConstraintGroup(this),
      enumerable: true,
      configurable: true,
    });

    this.onMasterPaneScroll = this.onMasterPaneScroll.bind(this);

    this.initInventory();
    this.initMasterLayout(this.masterLayout);
    this.initMobileLayout(this.mobileLayout);
    this.initDesktopLayout(this.desktopLayout);
  }

  protected initInventory(): void {
    this.masterBar.injectView();
    this.masterPane.injectView();
    this.detail.injectView();
  }

  /** @hidden */
  readonly masterLayout!: ConstraintGroup;

  protected initMasterLayout(layout: ConstraintGroup): void {
    layout.constraint(this.barHeight.constrain(), "eq", InventoryView.BarHeight);
    layout.constraint(this.masterWidth.constrain(), "eq", InventoryView.MasterWidth);
  }

  /** @hidden */
  readonly mobileLayout!: ConstraintGroup;

  protected initMobileLayout(layout: ConstraintGroup): void {
    // hook
  }

  /** @hidden */
  readonly desktopLayout!: ConstraintGroup;

  protected initDesktopLayout(layout: ConstraintGroup): void {
    // hook
  }

  protected initMasterBar(masterBarView: HtmlView): void {
    masterBarView.position("absolute")
                 .display("flex")
                 .justifyContent("center")
                 .alignItems("center")
                 .top(0)
                 .left(0)
                 .userSelect("none")
                 .text("Lanes");

    this.masterTree.constraint(masterBarView.width.constrain(), "eq", this.masterWidth);
    this.masterTree.constraint(masterBarView.height.constrain(), "eq", this.barHeight);
  }

  protected initMasterPane(masterPaneView: HtmlView): void {
    masterPaneView.position("absolute")
                  .bottom(0)
                  .left(0)
                  .overflowY("auto");

    this.masterPane.constraint(masterPaneView.top.constrain(), "eq", this.barHeight);
    this.masterPane.constraint(masterPaneView.width.constrain(), "eq", this.masterWidth);

    this.masterTree.injectView(masterPaneView);

    masterPaneView.on("scroll", this.onMasterPaneScroll);
  }

  protected initMasterTree(masterTreeView: TreeView): void {
    // hook
  }

  protected initDetail(detailView: HtmlView): void {
    detailView.position("absolute")
              .top(0)
              .right(0)
              .bottom(0)
              .padding(10)
              .overflowY("auto");

    this.detail.constraint(detailView.left.constrain(), "eq", this.masterWidth);
  }

  @ViewPropertyConstraint({type: Number, constrain: true, strength: "strong"})
  readonly barHeight!: ViewPropertyConstraint<this, number>;

  @ViewPropertyConstraint({type: Number, constrain: true, strength: "strong"})
  readonly masterWidth!: ViewPropertyConstraint<this, number>;

  @ViewFastener<InventoryView, HtmlView>({
    key: true,
    type: HtmlView,
    onSetView(masterBarView: HtmlView | null): void {
      if (masterBarView !== null) {
        this.owner.initMasterBar(masterBarView);
      }
    },
  })
  readonly masterBar!: ViewFastener<this, HtmlView>;

  @ViewFastener<InventoryView, HtmlView>({
    key: true,
    type: HtmlView,
    onSetView(masterPaneView: HtmlView | null): void {
      if (masterPaneView !== null) {
        this.owner.initMasterPane(masterPaneView);
      }
    },
  })
  readonly masterPane!: ViewFastener<this, HtmlView>;

  @ViewFastener<InventoryView, TreeView>({
    key: true,
    type: TreeView,
    child: false,
    onSetView(masterTreeView: TreeView | null): void {
      if (masterTreeView !== null) {
        this.owner.initMasterTree(masterTreeView);
      }
    },
  })
  readonly masterTree!: ViewFastener<this, TreeView>;

  @ViewFastener<InventoryView, HtmlView>({
    key: true,
    type: HtmlView,
    onSetView(detailView: HtmlView | null): void {
      if (detailView !== null) {
        this.owner.initDetail(detailView);
      }
    },
  })
  readonly detail!: ViewFastener<this, HtmlView>;

  protected onMasterPaneScroll(event: Event): void {
    const masterTreeView = this.masterTree.view;
    if (masterTreeView !== null) {
      masterTreeView.requireUpdate(View.NeedsScroll);
    }
  }

  static BarHeight: number = 44;
  static MasterWidth: number = 320;
}
