import type {HtmlView} from "@swim/dom";
import {ComponentModel, ComponentView, CompositeComponent} from "@swim/component";
import type {DrawerView} from "@swim/window";
import type {DomainGroup} from "@swim/lens";
import type {SurfaceView} from "../view/SurfaceView";

export abstract class MirrorComponent extends CompositeComponent {
  abstract isFullBleed(): boolean;

  abstract readonly surface: ComponentView<this, SurfaceView>;

  abstract readonly toolbar: ComponentView<this, HtmlView>;

  abstract readonly drawer: ComponentView<this, DrawerView>;

  abstract readonly domains: ComponentModel<this, DomainGroup>;
}
