import {HtmlView} from "@swim/dom";
import {
  ComponentService,
  HistoryState,
  HistoryManager,
  HistoryService,
  ComponentModel,
  ComponentView,
  ComponentFastener,
  CompositeComponent,
} from "@swim/component";
import {ListView} from "@swim/grid";
import {DrawerView} from "@swim/window";
import {DomainGroup} from "@swim/lens";
import {PrismPlugin, PrismManager, PrismService, MirrorPlugin, SurfaceView} from "@swim/prism";
import {ReflectionComponent} from "./ReflectionComponent";

export class ReflectorComponent extends CompositeComponent {
  constructor() {
    super();
    Object.defineProperty(this, "activeReflection", {
      value: null,
      enumerable: true,
      configurable: true,
    });
  }

  protected initSurface(surfaceView: SurfaceView): void {
    const activeReflection = this.activeReflection;
    if (activeReflection !== null) {
      activeReflection.surface.setView(surfaceView);
    }
  }

  protected initToolbar(toolbarView: HtmlView): void {
    const activeReflection = this.activeReflection;
    if (activeReflection !== null) {
      activeReflection.toolbar.setView(toolbarView);
    }
  }

  protected initDrawer(drawerView: DrawerView): void {
    const activeReflection = this.activeReflection;
    if (activeReflection !== null) {
      activeReflection.drawer.setView(drawerView);
    }
  }

  protected initMirrorList(listView: ListView): void {
    const plugins = this.prismService.plugins;
    for (let i = 0, n = plugins.length; i < n; i += 1) {
      const plugin = plugins[i];
      if (plugin instanceof MirrorPlugin) {
        this.insertPlugin(plugin);
      }
    }
  }

  protected insertPlugin(plugin: MirrorPlugin): void {
    let componentFastener = this.getComponentFastener(plugin.id) as ComponentFastener<this, ReflectionComponent> | null;
    if (componentFastener === null) {
      componentFastener = new ReflectorComponent.ReflectionFastener(this, plugin.id, "reflection");
      this.setComponentFastener(plugin.id, componentFastener);
      const reflectionComponent = new ReflectionComponent(plugin);
      componentFastener.setComponent(reflectionComponent);

      const fragment = this.historyService.historyState.fragment;
      if (this.activeReflection === null && (fragment === void 0 || fragment === plugin.id)) {
        this.setActiveReflection(reflectionComponent);
        if (fragment === void 0) {
          this.historyService.replaceHistory({fragment: reflectionComponent.plugin.id});
        }
      }
    }
  }

  protected removePlugin(plugin: MirrorPlugin): void {
    const componentFastener = this.getComponentFastener(plugin.id) as ComponentFastener<this, ReflectionComponent> | null;
    if (componentFastener !== null) {
      componentFastener.removeComponent();
      this.setComponentFastener(plugin.id, null);
    }
  }

  protected onInsertReflection(reflectionComponent: ReflectionComponent): void {
    const listView = this.mirrorList.view;
    if (listView !== null) {
      reflectionComponent.listItem.injectView(listView, void 0, void 0, reflectionComponent.plugin.id);
    }

    const domainGroup = this.domains.model;
    if (domainGroup !== null) {
      reflectionComponent.domains.setModel(domainGroup);
    }
  }

  protected onRemoveReflection(reflectionComponent: ReflectionComponent): void {
    if (reflectionComponent === this.activeReflection) {
      this.setActiveReflection(null);
    }
    const listItem = reflectionComponent.listItem.view;
    if (listItem !== null) {
      listItem.remove();
    }
  }

  readonly activeReflection!: ReflectionComponent | null;

  setActiveReflection(newReflectionComponent: ReflectionComponent | null): void {
    const oldReflectionComponent = this.activeReflection;
    if (newReflectionComponent !== oldReflectionComponent) {
      if (oldReflectionComponent !== null) {
        this.deactivateReflection(oldReflectionComponent);
      }
      Object.defineProperty(this, "activeReflection", {
        value: newReflectionComponent,
        enumerable: true,
        configurable: true,
      });
      if (newReflectionComponent !== null) {
        this.activateReflection(newReflectionComponent);
      }
    }
  }

  protected activateReflection(reflectionComponent: ReflectionComponent): void {
    const listItem = reflectionComponent.listItem.view;
    if (listItem !== null) {
      listItem.highlight(false);
    }
    const surfaceView = this.surface.view;
    if (surfaceView !== null) {
      reflectionComponent.surface.setView(surfaceView);
    }
    const toolbarView = this.toolbar.view;
    if (toolbarView !== null) {
      reflectionComponent.toolbar.setView(toolbarView);
    }
    const drawerView = this.drawer.view;
    if (drawerView !== null) {
      reflectionComponent.drawer.setView(drawerView);
    }
  }

  protected deactivateReflection(reflectionComponent: ReflectionComponent): void {
    reflectionComponent.drawer.setView(null);
    reflectionComponent.toolbar.setView(null);
    reflectionComponent.surface.setView(null);
    const listItem = reflectionComponent.listItem.view;
    if (listItem !== null) {
      listItem.unhighlight(true);
    }
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const childComponents = this.childComponents;
    for (let i = 0; i < childComponents.length; i += 1) {
      const childComponent = childComponents[i];
      if (childComponent instanceof ReflectionComponent) {
        childComponent.domains.setModel(domainGroup);
      }
    }
  }

  @ComponentView<ReflectorComponent, SurfaceView>({
    type: SurfaceView,
    onSetView(surfaceView: SurfaceView | null): void {
      if (surfaceView !== null) {
        this.owner.initSurface(surfaceView);
      }
    },
  })
  readonly surface!: ComponentView<this, SurfaceView>;

  @ComponentView<ReflectorComponent, HtmlView>({
    type: HtmlView,
    onSetView(toolbarView: HtmlView | null): void {
      if (toolbarView !== null) {
        this.owner.initToolbar(toolbarView);
      }
    },
  })
  readonly toolbar!: ComponentView<this, HtmlView>;

  @ComponentView<ReflectorComponent, DrawerView>({
    type: DrawerView,
    onSetView(drawerView: DrawerView | null): void {
      if (drawerView !== null) {
        this.owner.initDrawer(drawerView);
      }
    },
  })
  readonly drawer!: ComponentView<this, DrawerView>;

  @ComponentView<ReflectorComponent, ListView>({
    type: ListView,
    onSetView(listView: ListView | null): void {
      if (listView !== null) {
        this.owner.initMirrorList(listView);
      }
    },
  })
  readonly mirrorList!: ComponentView<this, ListView>;

  static ReflectionFastener = ComponentFastener.define<ReflectorComponent, ReflectionComponent>({
    type: ReflectionComponent,
    observe: true,
    onSetComponent(newReflectionComponent: ReflectionComponent | null,
                   oldReflectionComponent: ReflectionComponent | null): void {
      if (oldReflectionComponent !== null) {
        this.owner.onRemoveReflection(oldReflectionComponent);
      }
      if (newReflectionComponent !== null) {
        this.owner.onInsertReflection(newReflectionComponent);
      }
    },
    reflectionDidActivate(reflectionComponent: ReflectionComponent): void {
      this.owner.historyService.pushHistory({fragment: reflectionComponent.plugin.id});
      this.owner.setActiveReflection(reflectionComponent);
    },
  });

  @ComponentModel<ReflectorComponent, DomainGroup>({
    type: DomainGroup,
    onSetModel(domainGroup: DomainGroup | null): void {
      if (domainGroup !== null) {
        this.owner.initDomains(domainGroup);
      }
    },
  })
  readonly domains!: ComponentModel<this, DomainGroup>;

  @ComponentService<ReflectorComponent, PrismManager>({
    type: PrismManager,
    extends: PrismService,
    prismManagerDidInsertPlugin(plugin: PrismPlugin, index: number): void {
      if (plugin instanceof MirrorPlugin) {
        this.owner.insertPlugin(plugin);
      }
    },
    prismManagerWillRemovePlugin(plugin: PrismPlugin): void {
      if (plugin instanceof MirrorPlugin) {
        this.owner.removePlugin(plugin);
      }
    },
  })
  readonly prismService!: PrismService<this>;

  @ComponentService<ReflectorComponent, HistoryManager>({
    type: HistoryManager,
    historyManagerDidPopHistory(historyState: HistoryState): void {
      this.owner.updateHistoryState(historyState);
    },
  })
  override readonly historyService!: HistoryService<this>;

  protected override onMount(): void {
    super.onMount();
    this.updateHistoryState(this.historyService.historyState);
  }

  protected updateHistoryState(historyState: HistoryState): void {
    const fragment = historyState.fragment;
    if (fragment !== void 0) {
      const reflectionComponent = this.getChildComponent(fragment) as ReflectionComponent | null;
      if (reflectionComponent !== null) {
        this.setActiveReflection(reflectionComponent);
      }
    }
  }
}
