export class IndicatorType {
  constructor(key: string, name: string) {
    Object.defineProperty(this, "key", {
      value: key,
      enumerable: true,
    });
    Object.defineProperty(this, "name", {
      value: name,
      enumerable: true,
    });
  }

  readonly key!: string;

  readonly name!: string;
}
