import {Uri, UriPath} from "@swim/uri";
import {TraitModel} from "@swim/model";
import {StatusTrait, EntityTrait, EntityGroup} from "@swim/lens";
import {MetaNodeGroup} from "./MetaNodeGroup";

export class MetaNodeEntity extends EntityTrait {
  constructor(nodeUri: Uri, metaNodeUri: Uri, metaHostUri: Uri) {
    super(nodeUri);
    Object.defineProperty(this, "metaNodeUri", {
      value: metaNodeUri,
      enumerable: true,
    });
    Object.defineProperty(this, "metaHostUri", {
      value: metaHostUri,
      enumerable: true,
    });
  }

  get nodeUri(): Uri {
    return this.uri;
  }

  get nodePath(): UriPath {
    return this.uri.path;
  }

  readonly metaNodeUri!: Uri;

  readonly metaHostUri!: Uri;

  protected override createSubentities(): EntityGroup | null {
    let nodePath = this.nodePath;
    if (!nodePath.foot().isAbsolute()) {
      nodePath = nodePath.appendedSlash();
    }
    const nodeUri = Uri.path(nodePath);
    const entityGroup = new MetaNodeGroup(nodeUri, this.metaHostUri);
    entityGroup.setTrait("status", new StatusTrait());
    return entityGroup;
  }

  protected override initSubentities(entityGroup: EntityGroup): void {
    // hook
  }

  @TraitModel<MetaNodeEntity, EntityGroup>({
    key: true,
    type: EntityGroup,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initSubentities(entityGroup);
      }
    },
    createModel(): EntityGroup | null {
      return this.owner.createSubentities();
    },
  })
  override readonly subentities!: TraitModel<this, EntityGroup>;
}
