import {Format} from "@swim/codec";
import type {AnyValue, Value} from "@swim/structure";
import type {Uri} from "@swim/uri";
import {
  TraitModelType,
  TraitModel,
  TraitFastener,
  ModelValueDownlink,
  CompoundModel,
  GenericTrait,
} from "@swim/model";
import {
  Status,
  StatusFactor,
  StatusVector,
  StatusTrait,
  IndicatorType,
  ValueIndicatorTrait,
  IndicatorGroup,
} from "@swim/lens";

export class PulseTrait extends GenericTrait {
  constructor(metaNodeUri: Uri) {
    super();
    Object.defineProperty(this, "metaNodeUri", {
      value: metaNodeUri,
      enumerable: true,
    });
    Object.defineProperty(this, "fabricIndicators", {
      value: true,
      enumerable: true,
      configurable: true,
    });
  }

  protected override onSetModel(newModel: TraitModelType<this> | null,
                                oldModel: TraitModelType<this> | null): void {
    super.onSetModel(newModel, oldModel);
    if (newModel !== null) {
      this.indicators.injectModel();
    }
  }

  readonly metaNodeUri!: Uri;

  /** @hidden */
  readonly fabricIndicators!: boolean;

  protected initIndicators(indicatorGroup: IndicatorGroup): void {
    // hook
  }

  protected initPartCountIndicator(partCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected onSetPartCount(partCount: number, partCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected initHostCountIndicator(hostCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected onSetHostCount(hostCount: number, hostCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected initNodeCountIndicator(nodeCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected onSetNodeCount(nodeCount: number, nodeCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected initAgentCountIndicator(agentCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected initLinkCountIndicator(linkCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected initMessageRateIndicator(messageRateIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected initExecRateIndicator(execRateIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected initTimerRateIndicator(timerRateIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected onSetAgentCount(agentCount: number, agentCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected onSetLinkCount(linkCount: number, linkCountIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  //protected onSetMessageRate(messageRate: number, messageRateIndicator: ValueIndicatorTrait<number>): void {
  //  // hook
  //}

  protected onSetMessageRate(messageRate: number, messageRateIndicator: ValueIndicatorTrait<number>): void {
    const statusTrait = messageRateIndicator.getTrait(StatusTrait);
    if (statusTrait !== null) {
      const nominalRate = 1000;
      const warningRate = 5000;
      const alertRate = 10000;
      if (messageRate < nominalRate) {
        statusTrait.setStatusFactor("message-rate", null);
      } else if (messageRate < warningRate) {
        const u = (messageRate - nominalRate) / (warningRate - nominalRate);
        statusTrait.setStatusFactor("message-rate", StatusFactor.create("Message Rate", StatusVector.of([Status.warning, 0.5 + 0.5 * u])));
      } else {
        const u = (Math.min(messageRate, alertRate) - warningRate) / (alertRate - warningRate);
        statusTrait.setStatusFactor("message-rate", StatusFactor.create("Message Rate", StatusVector.of([Status.alert, 0.5 + 0.5 * u])));
      }
    }
  }

  //protected onSetExecRate(execRate: number, execRateIndicator: ValueIndicatorTrait<number>): void {
  //  // hook
  //}

  protected onSetExecRate(execRate: number, execRateIndicator: ValueIndicatorTrait<number>): void {
    const statusTrait = execRateIndicator.getTrait(StatusTrait);
    if (statusTrait !== null) {
      const nominalRate = 0.001;
      const warningRate = 0.01;
      const alertRate = 0.1;
      if (execRate < nominalRate) {
        statusTrait.setStatusFactor("exec-rate", null);
      } else if (execRate < warningRate) {
        const u = (execRate - nominalRate) / (warningRate - nominalRate);
        statusTrait.setStatusFactor("exec-rate", StatusFactor.create("Exec Rate", StatusVector.of([Status.warning, 0.5 + 0.5 * u])));
      } else {
        const u = (Math.min(execRate, alertRate) - warningRate) / (alertRate - warningRate);
        statusTrait.setStatusFactor("exec-rate", StatusFactor.create("Exec Rate", StatusVector.of([Status.alert, 0.5 + 0.5 * u])));
      }
    }
  }

  protected onSetTimerRate(timerRate: number, timerRateIndicator: ValueIndicatorTrait<number>): void {
    // hook
  }

  protected castPartCount(value: Value): number | undefined {
    return value.get("partCount").numberValue(void 0);
  }

  protected castHostCount(value: Value): number | undefined {
    return value.get("hostCount").numberValue(void 0);
  }

  protected castNodeCount(value: Value): number | undefined {
    return value.get("nodeCount").numberValue(void 0);
  }

  protected castAgentCount(value: Value): number | undefined {
    return value.get("agents").get("agentCount").numberValue(void 0);
  }

  protected onSetPulse(value: Value): void {
    const indicatorGroup = this.indicators.model;

    if (this.fabricIndicators) {
      const partCount = this.castPartCount(value);
      if (partCount !== void 0) {
        let partCountIndicator = this.partCountIndicator.trait;
        if (partCountIndicator === null && indicatorGroup !== null && partCount !== 0) {
          const partCountModel = new CompoundModel();
          partCountIndicator = this.partCountIndicator.injectTrait(partCountModel);
          partCountModel.setTrait("status", new StatusTrait());
          indicatorGroup.appendChildModel(partCountModel);
        }
        if (partCountIndicator !== null) {
          partCountIndicator.setValue(partCount);
        }
      }

      const hostCount = this.castHostCount(value);
      if (hostCount !== void 0) {
        let hostCountIndicator = this.hostCountIndicator.trait;
        if (hostCountIndicator === null && indicatorGroup !== null && hostCount !== 0) {
          const hostCountModel = new CompoundModel();
          hostCountIndicator = this.hostCountIndicator.injectTrait(hostCountModel);
          hostCountModel.setTrait("status", new StatusTrait());
          indicatorGroup.appendChildModel(hostCountModel);
        }
        if (hostCountIndicator !== null) {
          hostCountIndicator.setValue(hostCount);
        }
      }

      const nodeCount = this.castNodeCount(value);
      if (nodeCount !== void 0) {
        let nodeCountIndicator = this.nodeCountIndicator.trait;
        if (nodeCountIndicator === null && indicatorGroup !== null && nodeCount !== 0) {
          const nodeCountModel = new CompoundModel();
          nodeCountIndicator = this.nodeCountIndicator.injectTrait(nodeCountModel);
          nodeCountModel.setTrait("status", new StatusTrait());
          indicatorGroup.appendChildModel(nodeCountModel);
        }
        if (nodeCountIndicator !== null) {
          nodeCountIndicator.setValue(nodeCount);
        }
      }
    }

    const agentCount = this.castAgentCount(value);
    if (agentCount !== void 0) {
      let agentCountIndicator = this.agentCountIndicator.trait;
      if (agentCountIndicator === null && indicatorGroup !== null && agentCount !== 0) {
        const agentCountModel = new CompoundModel();
        agentCountIndicator = this.agentCountIndicator.injectTrait(agentCountModel);
        agentCountModel.setTrait("status", new StatusTrait());
        indicatorGroup.appendChildModel(agentCountModel);
      }
      if (agentCountIndicator !== null) {
        agentCountIndicator.setValue(agentCount);
      }
    }

    const downlinks = value.get("downlinks");
    const uplinks = value.get("uplinks");
    if (downlinks.isDefined() || uplinks.isDefined()) {
      const downlinkCount = downlinks.get("linkCount").numberValue(0);
      const uplinkCount = uplinks.get("linkCount").numberValue(0);
      const linkCount = downlinkCount + uplinkCount;
      let linkCountIndicator = this.linkCountIndicator.trait;
      if (linkCountIndicator === null && indicatorGroup !== null && linkCount !== 0) {
        const linkCountModel = new CompoundModel();
        linkCountIndicator = this.linkCountIndicator.injectTrait(linkCountModel);
        linkCountModel.setTrait("status", new StatusTrait());
        indicatorGroup.appendChildModel(linkCountModel);
      }
      if (linkCountIndicator !== null) {
        linkCountIndicator.setValue(linkCount);
      }

      const downlinkEventRate = downlinks.get("eventRate").numberValue(0);
      const uplinkEventRate = uplinks.get("eventRate").numberValue(0);
      const eventRate = downlinkEventRate + uplinkEventRate;
      const downlinkCommandRate = downlinks.get("commandRate").numberValue(0);
      const uplinkCommandRate = uplinks.get("commandRate").numberValue(0);
      const commandRate = downlinkCommandRate + uplinkCommandRate;
      const messageRate = eventRate + commandRate;
      let messageRateIndicator = this.messageRateIndicator.trait;
      if (messageRateIndicator === null && indicatorGroup !== null && messageRate !== 0) {
        const messageRateModel = new CompoundModel();
        messageRateIndicator = this.messageRateIndicator.injectTrait(messageRateModel);
        messageRateModel.setTrait("status", new StatusTrait());
        indicatorGroup.appendChildModel(messageRateModel);
      }
      if (messageRateIndicator !== null) {
        messageRateIndicator.setValue(messageRate);
      }
    }

    const agents = value.get("agents");
    if (agents.isDefined()) {
      const execRate = agents.get("execRate").numberValue(0) / 1000000000;
      let execRateIndicator = this.execRateIndicator.trait;
      if (execRateIndicator === null && indicatorGroup !== null && execRate !== 0) {
        const execRateModel = new CompoundModel();
        execRateIndicator = this.execRateIndicator.injectTrait(execRateModel);
        execRateModel.setTrait("status", new StatusTrait());
        indicatorGroup.appendChildModel(execRateModel);
      }
      if (execRateIndicator !== null) {
        execRateIndicator.setValue(execRate);
      }

      const timerRate = agents.get("timerEventRate").numberValue(0);
      let timerRateIndicator = this.timerRateIndicator.trait;
      if (timerRateIndicator === null && indicatorGroup !== null && timerRate !== 0) {
        const timerRateModel = new CompoundModel();
        timerRateIndicator = this.timerRateIndicator.injectTrait(timerRateModel);
        timerRateModel.setTrait("status", new StatusTrait());
        indicatorGroup.appendChildModel(timerRateModel);
      }
      if (timerRateIndicator !== null) {
        timerRateIndicator.setValue(timerRate);
      }
    }
  }

  /** @hidden */
  protected didConnect(): void {
    const statusTrait = this.getTrait(StatusTrait);
    if (statusTrait !== null) {
      statusTrait.setStatusFactor("disconnected", null);
      statusTrait.setStatusFactor("connected", StatusFactor.create("Connected", StatusVector.of([Status.normal, 1])));
    }
  }

  /** @hidden */
  protected didDisconnect(): void {
    const statusTrait = this.getTrait(StatusTrait);
    if (statusTrait !== null) {
      statusTrait.setStatusFactor("connected", null);
      statusTrait.setStatusFactor("disconnected", StatusFactor.create("Disconnected", StatusVector.of([Status.inactive, 1])));
    }
  }

  protected createIndicators(): IndicatorGroup {
    const indicatorGroup = new IndicatorGroup();
    indicatorGroup.setTrait("status", new StatusTrait());
    return indicatorGroup;
  }

  /** @hidden */
  protected startConsumingIndicators(): void {
    this.pulse.addDownlinkConsumer(this);
  }

  /** @hidden */
  protected stopConsumingIndicators(): void {
    this.pulse.removeDownlinkConsumer(this);
  }

  @TraitModel<PulseTrait, IndicatorGroup>({
    key: true,
    type: IndicatorGroup,
    observe: true,
    onSetModel(indicatorGroup: IndicatorGroup | null): void {
      if (indicatorGroup !== null) {
        this.owner.initIndicators(indicatorGroup);
        if (indicatorGroup.isConsuming()) {
          this.owner.startConsumingIndicators();
        }
      }
    },
    createModel(): IndicatorGroup | null {
      return this.owner.createIndicators();
    },
    modelDidStartConsuming(indicatorGroup: IndicatorGroup): void {
      this.owner.startConsumingIndicators();
    },
    modelWillStopConsuming(indicatorGroup: IndicatorGroup): void {
      this.owner.stopConsumingIndicators();
    },
  })
  readonly indicators!: TraitModel<this, IndicatorGroup>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(partCountIndicator: ValueIndicatorTrait<number> | null): void {
      if (partCountIndicator !== null) {
        this.owner.initPartCountIndicator(partCountIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.PartCountIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         partCountIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetPartCount(newValue, partCountIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value);
    },
  })
  readonly partCountIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(hostCountIndicator: ValueIndicatorTrait<number> | null): void {
      if (hostCountIndicator !== null) {
        this.owner.initHostCountIndicator(hostCountIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.HostCountIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         hostCountIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetHostCount(newValue, hostCountIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value);
    },
  })
  readonly hostCountIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(nodeCountIndicator: ValueIndicatorTrait<number> | null): void {
      if (nodeCountIndicator !== null) {
        this.owner.initNodeCountIndicator(nodeCountIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.NodeCountIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         nodeCountIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetNodeCount(newValue, nodeCountIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value);
    },
  })
  readonly nodeCountIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(agentCountIndicator: ValueIndicatorTrait<number> | null): void {
      if (agentCountIndicator !== null) {
        this.owner.initAgentCountIndicator(agentCountIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.AgentCountIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         agentCountIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetAgentCount(newValue, agentCountIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value);
    },
  })
  readonly agentCountIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(linkCountIndicator: ValueIndicatorTrait<number> | null): void {
      if (linkCountIndicator !== null) {
        this.owner.initLinkCountIndicator(linkCountIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.LinkCountIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         linkCountIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetLinkCount(newValue, linkCountIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value);
    },
  })
  readonly linkCountIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(messageRateIndicator: ValueIndicatorTrait<number> | null): void {
      if (messageRateIndicator !== null) {
        this.owner.initMessageRateIndicator(messageRateIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.MessageRateIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         messageRateIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetMessageRate(newValue, messageRateIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value) + "/s";
    },
  })
  readonly messageRateIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(execRateIndicator: ValueIndicatorTrait<number> | null): void {
      if (execRateIndicator !== null) {
        this.owner.initExecRateIndicator(execRateIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.ExecRateIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         execRateIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetExecRate(newValue, execRateIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value, 0) + "s/s";
    },
  })
  readonly execRateIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @TraitFastener<PulseTrait, ValueIndicatorTrait<number>>({
    type: ValueIndicatorTrait,
    observe: true,
    onSetTrait(timerRateIndicator: ValueIndicatorTrait<number> | null): void {
      if (timerRateIndicator !== null) {
        this.owner.initTimerRateIndicator(timerRateIndicator);
      }
    },
    createTrait(): ValueIndicatorTrait<number> | null {
      return new ValueIndicatorTrait<number>(PulseTrait.TimerRateIndicatorType);
    },
    indicatorDidSetValue(newValue: number, oldValue: number,
                         timerRateIndicator: ValueIndicatorTrait<number>): void {
      this.owner.onSetTimerRate(newValue, timerRateIndicator);
    },
    formatIndicator(value: number): string {
      return Format.prefix(value) + "/s";
    },
  })
  readonly timerRateIndicator!: TraitFastener<this, ValueIndicatorTrait<number>>;

  @ModelValueDownlink<PulseTrait, Value, AnyValue>({
    nodeUri(): Uri {
      return this.owner.metaNodeUri;
    },
    laneUri: "pulse",
    didSet(value: Value): void {
      this.owner.onSetPulse(value);
    },
    didConnect(): void {
      this.owner.didConnect();
    },
    didDisconnect(): void {
      this.owner.didDisconnect();
    },
  })
  readonly pulse!: ModelValueDownlink<this, Value, AnyValue>;

  /** @hidden */
  static PartCountIndicatorType = new IndicatorType("partCount", "Parts");

  /** @hidden */
  static HostCountIndicatorType = new IndicatorType("hostCount", "Hosts");

  /** @hidden */
  static NodeCountIndicatorType = new IndicatorType("nodeCount", "Nodes");

  /** @hidden */
  static AgentCountIndicatorType = new IndicatorType("agentCount", "Agents");

  /** @hidden */
  static LinkCountIndicatorType = new IndicatorType("linkCount", "Links");

  /** @hidden */
  static MessageRateIndicatorType = new IndicatorType("messageRate", "Messages");

  /** @hidden */
  static ExecRateIndicatorType = new IndicatorType("execRate", "Compute");

  /** @hidden */
  static TimerRateIndicatorType = new IndicatorType("timerRate", "Timers");
}
