import {ComponentView, CompositeComponent} from "@swim/component";
import type {MapView} from "@swim/map";
import type {SurfaceView} from "@swim/prism";

export type AtlasMapType = "equirectangular" | "mapbox";

export abstract class AtlasMap<V extends MapView = MapView> extends CompositeComponent {
  abstract readonly base: ComponentView<this, V>;

  abstract readonly surface: ComponentView<this, SurfaceView>;
}
