  insertGadgetTrait(gadgetTrait: Trait, targetTrait: Trait | null = null): void {
    const gadgetFasteners = this.gadgetFasteners as ComponentFastener<this, GadgetComponent>[];
    let targetComponent: GadgetComponent | null = null;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetComponent = gadgetFasteners[i]!.component;
      if (gadgetComponent !== null) {
        if (gadgetComponent.gadget.trait === gadgetTrait) {
          return;
        } else if (gadgetComponent.gadget.trait === targetTrait) {
          targetComponent = gadgetComponent;
        }
      }
    }
    const gadgetComponent = this.createGadget(gadgetTrait);
    if (gadgetComponent !== null) {
      gadgetComponent.gadget.setTrait(gadgetTrait);
      this.insertChildComponent(gadgetComponent, targetComponent);
      if (gadgetComponent.gadget.view === null) {
        const gadgetView = this.createGadgetView(gadgetComponent);
        let targetView: HtmlView | null = null;
        if (targetComponent !== null) {
          targetView = targetComponent.gadget.view;
        }
        const widgetView = this.widget.view;
        if (widgetView !== null) {
          if (targetView === null) {
            targetView = widgetView.footer.view;
          }
          gadgetComponent.gadget.injectView(widgetView, gadgetView, targetView);
        } else {
          gadgetComponent.gadget.setView(gadgetView, targetView);
        }
      }
    }
  }

  removeGadgetTrait(gadgetTrait: Trait): void {
    const gadgetFasteners = this.gadgetFasteners as ComponentFastener<this, GadgetComponent>[];
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      const gadgetComponent = gadgetFastener.component;
      if (gadgetComponent !== null && gadgetComponent.gadget.trait === gadgetTrait) {
        gadgetFastener.setComponent(null);
        if (this.isMounted()) {
          gadgetFastener.unmount();
        }
        gadgetFasteners.splice(i, 1);
        gadgetComponent.remove();
        return;
      }
    }
  }

