import type {Uri} from "@swim/uri";
import {TraitModelType, TraitModel} from "@swim/model";
import {StatusTrait, EntityTrait, EntityGroup} from "@swim/lens";
import {MetaPartGroup} from "./MetaPartGroup";

export class MetaMeshEntity extends EntityTrait {
  constructor(uri: Uri) {
    super(uri);
  }

  protected override onSetModel(newModel: TraitModelType<this> | null,
                                oldModel: TraitModelType<this> | null): void {
    super.onSetModel(newModel, oldModel);
    if (newModel !== null) {
      this.subentities.injectModel();
    }
  }

  protected override createSubentities(): EntityGroup | null {
    const entityGroup = new MetaPartGroup(this.uri);
    entityGroup.setTrait("status", new StatusTrait());
    return entityGroup;
  }

  protected override initSubentities(entityGroup: EntityGroup): void {
    // hook
  }

  @TraitModel<MetaMeshEntity, EntityGroup>({
    key: true,
    type: EntityGroup,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initSubentities(entityGroup);
      }
    },
    createModel(): EntityGroup | null {
      return this.owner.createSubentities();
    },
  })
  override readonly subentities!: TraitModel<this, EntityGroup>;
}
