import type {Model, Trait} from "@swim/model";
import {View} from "@swim/view";
import {Component, ComponentTrait} from "@swim/component";
import {TableTrait} from "@swim/grid";
import {GaugeTrait} from "@swim/gauge";
import {PieTrait} from "@swim/pie";
import {ChartTrait} from "@swim/chart";
import {EntityTrait} from "@swim/lens";
import {
  GadgetComponent,
  WidgetView,
  WidgetComponent,
  TableGadgetComponent,
  GaugeGadgetComponent,
  PieGadgetComponent,
  ChartGadgetComponent,
} from "@swim/widget";
import type {RefractionComponentObserver} from "./RefractionComponentObserver";

export class RefractionComponent extends WidgetComponent {
  override readonly componentObservers!: ReadonlyArray<RefractionComponentObserver>;

  protected override initWidgetView(widgetView: WidgetView): void {
    super.initWidgetView(widgetView);
    widgetView.marginTop.setState(4, View.Intrinsic);
    widgetView.marginRight.setState(8, View.Intrinsic);
    widgetView.marginBottom.setState(8, View.Intrinsic);
    widgetView.marginLeft.setState(8, View.Intrinsic);
  }

  protected override createGadget(gadgetTrait: Trait): GadgetComponent | null {
    if (gadgetTrait instanceof TableTrait) {
      return new TableGadgetComponent();
    } else if (gadgetTrait instanceof GaugeTrait) {
      return new GaugeGadgetComponent();
    } else if (gadgetTrait instanceof PieTrait) {
      return new PieGadgetComponent();
    } else if (gadgetTrait instanceof ChartTrait) {
      return new ChartGadgetComponent();
    }
    return null;
  }

  protected override detectGadgetModel(model: Model): Trait | null {
    let gadgetTrait = null;
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(TableTrait);
    }
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(GaugeTrait);
    }
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(PieTrait);
    }
    if (gadgetTrait === null) {
      gadgetTrait = model.getTrait(ChartTrait);
    }
    return gadgetTrait;
  }

  protected override detectGadgetComponent(component: Component): GadgetComponent | null {
    if (component instanceof TableGadgetComponent ||
        component instanceof GaugeGadgetComponent ||
        component instanceof PieGadgetComponent ||
        component instanceof ChartGadgetComponent) {
      return component;
    }
    return null;
  }

  protected initEntity(entityTrait: EntityTrait): void {
    // hook
  }

  @ComponentTrait<RefractionComponent, EntityTrait>({
    type: EntityTrait,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
  })
  readonly entity!: ComponentTrait<this, EntityTrait>;

  protected override onUnmount(): void {
    super.onUnmount();
    this.widget.removeView();
  }
}
