import {Equals} from "@swim/util";
import {Model, TraitModelType, Trait, TraitFastener, GenericTrait} from "@swim/model";
import type {HtmlView} from "@swim/dom";
import type {WidgetTraitObserver} from "./WidgetTraitObserver";

export type WidgetTitle = WidgetTitleFunction | string;
export type WidgetTitleFunction = (widgetTrait: WidgetTrait) => HtmlView | string | null;

export type WidgetSubtitle = WidgetSubtitleFunction | string;
export type WidgetSubtitleFunction = (widgetTrait: WidgetTrait) => HtmlView | string | null;

export class WidgetTrait extends GenericTrait {
  constructor() {
    super();
    Object.defineProperty(this, "title", {
      value: null,
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "subtitle", {
      value: null,
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "gadgetFasteners", {
      value: [],
      enumerable: true,
    });
  }

  override readonly traitObservers!: ReadonlyArray<WidgetTraitObserver>;

  readonly title!: WidgetTitle | null;

  setTitle(newTitle: WidgetTitle | null): void {
    const oldTitle = this.title;
    if (!Equals(newTitle, oldTitle)) {
      this.willSetTitle(newTitle, oldTitle);
      Object.defineProperty(this, "title", {
        value: newTitle,
        enumerable: true,
        configurable: true,
      });
      this.onSetTitle(newTitle, oldTitle);
      this.didSetTitle(newTitle, oldTitle);
    }
  }

  protected willSetTitle(newTitle: WidgetTitle | null, oldTitle: WidgetTitle | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.widgetTraitWillSetTitle !== void 0) {
        traitObserver.widgetTraitWillSetTitle(newTitle, oldTitle, this);
      }
    }
  }

  protected onSetTitle(newTitle: WidgetTitle | null, oldTitle: WidgetTitle | null): void {
    // hook
  }

  protected didSetTitle(newTitle: WidgetTitle | null, oldTitle: WidgetTitle | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.widgetTraitDidSetTitle !== void 0) {
        traitObserver.widgetTraitDidSetTitle(newTitle, oldTitle, this);
      }
    }
  }

  readonly subtitle!: WidgetSubtitle | null;

  setSubtitle(newSubtitle: WidgetSubtitle | null): void {
    const oldSubtitle = this.subtitle;
    if (!Equals(newSubtitle, oldSubtitle)) {
      this.willSetSubtitle(newSubtitle, oldSubtitle);
      Object.defineProperty(this, "subtitle", {
        value: newSubtitle,
        enumerable: true,
        configurable: true,
      });
      this.onSetSubtitle(newSubtitle, oldSubtitle);
      this.didSetSubtitle(newSubtitle, oldSubtitle);
    }
  }

  protected willSetSubtitle(newSubtitle: WidgetSubtitle | null, oldSubtitle: WidgetSubtitle | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.widgetTraitWillSetSubtitle !== void 0) {
        traitObserver.widgetTraitWillSetSubtitle(newSubtitle, oldSubtitle, this);
      }
    }
  }

  protected onSetSubtitle(newSubtitle: WidgetSubtitle | null, oldSubtitle: WidgetSubtitle | null): void {
    // hook
  }

  protected didSetSubtitle(newSubtitle: WidgetSubtitle | null, oldSubtitle: WidgetSubtitle | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.widgetTraitDidSetSubtitle !== void 0) {
        traitObserver.widgetTraitDidSetSubtitle(newSubtitle, oldSubtitle, this);
      }
    }
  }

  insertGadget(gadgetTrait: Trait, targetTrait: Trait | null = null): void {
    const gadgetFasteners = this.gadgetFasteners as TraitFastener<this, Trait>[];
    let targetIndex = gadgetFasteners.length;
    if (targetTrait !== null) {
      for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
        const gadgetFastener = gadgetFasteners[i]!;
        if (gadgetFastener.trait === gadgetTrait) {
          return;
        } else if (gadgetFastener.trait === targetTrait) {
          targetIndex = i;
        }
      }
    }
    const gadgetFastener = this.createGadgetFastener(gadgetTrait);
    gadgetFasteners.splice(targetIndex, 0, gadgetFastener);
    gadgetFastener.setTrait(gadgetTrait, targetTrait);
    if (this.isMounted()) {
      gadgetFastener.mount();
    }
  }

  removeGadget(gadgetTrait: Trait): void {
    const gadgetFasteners = this.gadgetFasteners as TraitFastener<this, Trait>[];
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      if (gadgetFastener.trait === gadgetTrait) {
        gadgetFastener.setTrait(null);
        if (this.isMounted()) {
          gadgetFastener.unmount();
        }
        gadgetFasteners.splice(i, 1);
        break;
      }
    }
  }

  protected initGadget(gadgetTrait: Trait, gadgetFastener: TraitFastener<this, Trait>): void {
    // hook
  }

  protected attachGadget(gadgetTrait: Trait, gadgetFastener: TraitFastener<this, Trait>): void {
    if (this.isConsuming()) {
      gadgetTrait.addTraitConsumer(this);
    }
  }

  protected detachGadget(gadgetTrait: Trait, gadgetFastener: TraitFastener<this, Trait>): void {
    if (this.isConsuming()) {
      gadgetTrait.removeTraitConsumer(this);
    }
  }

  protected willSetGadget(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null,
                          targetTrait: Trait | null, gadgetFastener: TraitFastener<this, Trait>): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.widgetTraitWillSetGadget !== void 0) {
        traitObserver.widgetTraitWillSetGadget(newGadgetTrait, oldGadgetTrait, targetTrait, this);
      }
    }
  }

  protected onSetGadget(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null,
                        targetTrait: Trait | null, gadgetFastener: TraitFastener<this, Trait>): void {
    if (newGadgetTrait !== null) {
      this.initGadget(newGadgetTrait, gadgetFastener);
    }
  }

  protected didSetGadget(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null,
                         targetTrait: Trait | null, gadgetFastener: TraitFastener<this, Trait>): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.widgetTraitDidSetGadget !== void 0) {
        traitObserver.widgetTraitDidSetGadget(newGadgetTrait, oldGadgetTrait, targetTrait, this);
      }
    }
  }

  /** @hidden */
  static GadgetFastener = TraitFastener.define<WidgetTrait, Trait>({
    type: Trait,
    sibling: false,
    willSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null, targetTrait: Trait | null): void {
      this.owner.willSetGadget(newGadgetTrait, oldGadgetTrait, targetTrait, this);
    },
    onSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null, targetTrait: Trait | null): void {
      if (oldGadgetTrait !== null) {
        this.owner.detachGadget(oldGadgetTrait, this);
      }
      this.owner.onSetGadget(newGadgetTrait, oldGadgetTrait, targetTrait, this);
      if (newGadgetTrait !== null) {
        this.owner.attachGadget(newGadgetTrait, this);
      }
    },
    didSetTrait(newGadgetTrait: Trait | null, oldGadgetTrait: Trait | null, targetTrait: Trait | null): void {
      this.owner.didSetGadget(newGadgetTrait, oldGadgetTrait, targetTrait, this);
    },
  });

  protected createGadgetFastener(gadgetTrait: Trait): TraitFastener<this, Trait> {
    return new WidgetTrait.GadgetFastener(this, gadgetTrait.key, "gadget");
  }

  /** @hidden */
  readonly gadgetFasteners!: ReadonlyArray<TraitFastener<this, Trait>>;

  /** @hidden */
  protected mountGadgetFasteners(): void {
    const gadgetFasteners = this.gadgetFasteners;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      gadgetFastener.mount();
    }
  }

  /** @hidden */
  protected unmountGadgetFasteners(): void {
    const gadgetFasteners = this.gadgetFasteners;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetFastener = gadgetFasteners[i]!;
      gadgetFastener.unmount();
    }
  }

  /** @hidden */
  protected startConsumingGadgets(): void {
    const gadgetFasteners = this.gadgetFasteners;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetTrait = gadgetFasteners[i]!.trait;
      if (gadgetTrait !== null) {
        gadgetTrait.addTraitConsumer(this);
      }
    }
  }

  /** @hidden */
  protected stopConsumingGadgets(): void {
    const gadgetFasteners = this.gadgetFasteners;
    for (let i = 0, n = gadgetFasteners.length; i < n; i += 1) {
      const gadgetTrait = gadgetFasteners[i]!.trait;
      if (gadgetTrait !== null) {
        gadgetTrait.removeTraitConsumer(this);
      }
    }
  }

  protected detectGadgetModel(model: Model): Trait | null {
    let gadgetTrait: Trait | null = null;
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.detectGadgetModel !== void 0) {
        gadgetTrait = traitObserver.detectGadgetModel(model, this);
        if (gadgetTrait !== null) {
          break;
        }
      }
    }
    return gadgetTrait;
  }

  protected detectModels(model: TraitModelType<this>): void {
    const childModels = model.childModels;
    for (let i = 0, n = childModels.length; i < n; i += 1) {
      const childModel = childModels[i]!;
      const gadgetTrait = this.detectGadgetModel(childModel);
      if (gadgetTrait !== null) {
        this.insertGadget(gadgetTrait);
      }
    }
  }

  protected override didSetModel(newModel: TraitModelType<this> | null, oldModel: TraitModelType<this> | null): void {
    if (newModel !== null) {
      this.detectModels(newModel);
    }
    super.didSetModel(newModel, oldModel);
  }

  protected override onInsertChildModel(childModel: Model, targetModel: Model | null): void {
    super.onInsertChildModel(childModel, targetModel);
    const gadgetTrait = this.detectGadgetModel(childModel);
    if (gadgetTrait !== null) {
      const targetTrait = targetModel !== null ? this.detectGadgetModel(targetModel) : null;
      this.insertGadget(gadgetTrait, targetTrait);
    }
  }

  protected override onRemoveChildModel(childModel: Model): void {
    super.onRemoveChildModel(childModel);
    const gadgetTrait = this.detectGadgetModel(childModel);
    if (gadgetTrait !== null) {
      this.removeGadget(gadgetTrait);
    }
  }

  /** @hidden */
  protected override mountTraitFasteners(): void {
    super.mountTraitFasteners();
    this.mountGadgetFasteners();
  }

  /** @hidden */
  protected override unmountTraitFasteners(): void {
    this.unmountGadgetFasteners();
    super.unmountTraitFasteners();
  }

  protected override onStartConsuming(): void {
    super.onStartConsuming();
    this.startConsumingGadgets();
  }

  protected override onStopConsuming(): void {
    super.onStopConsuming();
    this.stopConsumingGadgets();
  }
}
