import {HtmlView} from "@swim/dom";
import {ComponentModel, ComponentView, ComponentFastener} from "@swim/component";
import type {TreeView} from "@swim/grid";
import {DrawerView} from "@swim/window";
import {DomainGroup} from "@swim/lens";
import {SurfaceView, MirrorComponent} from "@swim/prism";
import {CatalogEntityTree} from "./CatalogEntityTree";

export class CatalogComponent extends MirrorComponent {
  override isFullBleed(): boolean {
    return false;
  }

  protected initRoot(entityTree: CatalogEntityTree): void {
    // hook
  }

  protected initTree(treeView: TreeView): void {
    treeView.marginLeft(8)
            .marginRight(8)
            .userSelect("none");
  }

  protected attachSurface(surfaceView: SurfaceView): void {
    const domainGroup = this.domains.model;
    if (domainGroup !== null) {
      const entityTree = this.root.injectComponent();
      if (entityTree !== null) {
        const treeView = entityTree.tree.injectView(surfaceView);
        if (treeView !== null) {
          this.initTree(treeView);
        }
        entityTree.entities.setModel(domainGroup);
      }
    }
  }

  protected detachSurface(surfaceView: SurfaceView): void {
    const entityTree = this.root.component;
    if (entityTree !== null) {
      entityTree.tree.removeView();
    }
  }

  protected attachToolbar(toolbarView: HtmlView): void {
    // hook
  }

  protected detachToolbar(toolbarView: HtmlView): void {
    // hook
  }

  protected attachDrawer(drawerView: DrawerView): void {
    // hook
  }

  protected detachDrawer(drawerView: DrawerView): void {
    // hook
  }

  protected initDomains(domainGroup: DomainGroup): void {
    const surfaceView = this.surface.view;
    if (surfaceView !== null) {
      const entityTree = this.root.injectComponent();
      if (entityTree !== null) {
        const treeView = entityTree.tree.injectView(surfaceView);
        if (treeView !== null) {
          this.initTree(treeView);
        }
        entityTree.entities.setModel(domainGroup);
      }
    }
  }

  @ComponentFastener<CatalogComponent, CatalogEntityTree>({
    type: CatalogEntityTree,
    onSetComponent(entityTree: CatalogEntityTree | null): void {
      if (entityTree !== null) {
        this.owner.initRoot(entityTree);
      }
    },
    createComponent(): CatalogEntityTree | null {
      return new CatalogEntityTree();
    },
  })
  readonly root!: ComponentFastener<this, CatalogEntityTree>;

  @ComponentView<CatalogComponent, SurfaceView>({
    type: SurfaceView,
    onSetView(newSurfaceView: SurfaceView | null, oldSurfaceView: SurfaceView | null): void {
      if (oldSurfaceView !== null) {
        this.owner.detachSurface(oldSurfaceView);
      }
      if (newSurfaceView !== null) {
        this.owner.attachSurface(newSurfaceView);
      }
    },
  })
  override readonly surface!: ComponentView<this, SurfaceView>;

  @ComponentView<CatalogComponent, HtmlView>({
    type: HtmlView,
    onSetView(newToolbarView: HtmlView | null, oldToolbarView: HtmlView | null): void {
      if (oldToolbarView !== null) {
        this.owner.detachToolbar(oldToolbarView);
      }
      if (newToolbarView !== null) {
        this.owner.attachToolbar(newToolbarView);
      }
    },
  })
  override readonly toolbar!: ComponentView<this, HtmlView>;

  @ComponentView<CatalogComponent, DrawerView>({
    type: DrawerView,
    onSetView(newDrawerView: DrawerView | null, oldDrawerView: DrawerView | null): void {
      if (oldDrawerView !== null) {
        this.owner.detachDrawer(oldDrawerView);
      }
      if (newDrawerView !== null) {
        this.owner.attachDrawer(newDrawerView);
      }
    },
  })
  override readonly drawer!: ComponentView<this, DrawerView>;

  @ComponentModel<CatalogComponent, DomainGroup>({
    type: DomainGroup,
    onSetModel(newDomainGroup: DomainGroup | null, oldDomainGroup: DomainGroup | null): void {
      if (newDomainGroup !== null) {
        this.owner.initDomains(newDomainGroup);
      }
    },
  })
  override readonly domains!: ComponentModel<this, DomainGroup>;
}
