import type {ModelContext, Model} from "@swim/model";
import {ComponentModel, ComponentView, ComponentFastener, CompositeComponent} from "@swim/component";
import {GeoView, GeoTreeView} from "@swim/map";
import {EntityTrait, EntityGroup} from "@swim/lens";
import {AtlasEntityLocation} from "./AtlasEntityLocation";

export class AtlasEntityDistrict extends CompositeComponent {
  protected initLayer(layerView: GeoView): void {
    const childComponents = this.childComponents;
    for (let i = 0; i < childComponents.length; i += 1) {
      const childComponent = childComponents[i];
      if (childComponent instanceof AtlasEntityLocation) {
        this.onInsertLocation(childComponent);
      }
    }

    const entityGroup = this.entities.model;
    if (entityGroup !== null) {
      this.onAggregateEntities(entityGroup);
    }
  }

  protected initEntities(entityGroup: EntityGroup): void {
    const entities = entityGroup.childModels;
    for (let i = 0, n = entities.length; i < n; i += 1) {
      const entity = entities[i]!;
      const entityTrait = entity.getTrait(EntityTrait);
      if (entityTrait !== null) {
        this.onInsertEntity(entityTrait, null);
      }
    }
    this.onAggregateEntities(entityGroup);
  }

  protected onInsertEntity(childEntity: EntityTrait, targetEntity: EntityTrait | null): void {
    const id = childEntity.uri.toString();
    let componentFastener = this.getComponentFastener(id) as ComponentFastener<this, AtlasEntityLocation> | null;
    if (componentFastener === null) {
      componentFastener = new AtlasEntityDistrict.LocationFastener(this, id, "location") as ComponentFastener<this, AtlasEntityLocation>;
      this.setComponentFastener(id, componentFastener);
      const locationComponent = new AtlasEntityLocation();
      locationComponent.entity.setTrait(childEntity);
      const targetComponent = targetEntity !== null ? this.getChildComponent(targetEntity.uri.toString()) : null;
      this.insertChildComponent(locationComponent, targetComponent, id);
    }
  }

  protected onRemoveEntity(childEntity: EntityTrait): void {
    const id = childEntity.uri.toString();
    const componentFastener = this.getComponentFastener(id) as ComponentFastener<this, AtlasEntityLocation> | null;
    if (componentFastener !== null) {
      componentFastener.removeComponent();
      this.setComponentFastener(id, null);
    }
  }

  protected onInsertLocation(locationComponent: AtlasEntityLocation): void {
    const layerView = this.layer.view;
    if (layerView !== null) {
      locationComponent.layer.setView(layerView);
    }
  }

  protected onRemoveLocation(locationComponent: AtlasEntityLocation): void {
    // hook
  }

  protected onAggregateEntities(entityGroup: EntityGroup): void {
    // hook
  }

  @ComponentView<AtlasEntityDistrict, GeoView>({
    onSetView(layerView: GeoView | null): void {
      if (layerView !== null) {
        this.owner.initLayer(layerView);
      }
    },
    createView(): GeoView | null {
      return GeoTreeView.create();
    },
  })
  readonly layer!: ComponentView<this, GeoView>;

  static LocationFastener = ComponentFastener.define<AtlasEntityDistrict, AtlasEntityLocation>({
    type: AtlasEntityLocation,
    onSetComponent(newLocation: AtlasEntityLocation | null, oldLocation: AtlasEntityLocation | null): void {
      if (oldLocation !== null) {
        this.owner.onRemoveLocation(oldLocation);
      }
      if (newLocation !== null) {
        this.owner.onInsertLocation(newLocation);
      }
    },
  });

  @ComponentModel<AtlasEntityDistrict, EntityGroup>({
    type: EntityGroup,
    observe: true,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initEntities(entityGroup);
      }
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        const targetEntityTrait = targetModel !== null ? targetModel.getTrait(EntityTrait) : null;
        this.owner.onInsertEntity(childEntityTrait, targetEntityTrait);
      }
    },
    modelWillRemoveChildModel(childModel: Model, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        this.owner.onRemoveEntity(childEntityTrait);
      }
    },
    modelDidAggregate(modelContext: ModelContext, entityGroup: EntityGroup): void {
      this.owner.onAggregateEntities(entityGroup);
    },
  })
  readonly entities!: ComponentModel<this, EntityGroup>;
}
