import {AnyTiming, Timing} from "@swim/mapping";
import {Look, MoodVector, ThemeMatrix} from "@swim/theme";
import {View, ViewProperty, PositionGestureInput, PositionGesture, PositionGestureDelegate} from "@swim/view";
import {GeoViewController, GeoAreaView} from "@swim/map";
import type {Geographic} from "./Geographic";
import {GeographicArea} from "./GeographicArea";
import type {GeographicView} from "./GeographicView";
import type {GeographicViewObserver} from "./GeographicViewObserver";

export class GeographicAreaView extends GeoAreaView implements GeographicView, PositionGestureDelegate {
  constructor() {
    super();
    Object.defineProperty(this, "gesture", {
      value: this.createGesture(),
      enumerable: true,
      configurable: true,
    });
  }

  override readonly viewController!: (GeoViewController<GeographicAreaView> & GeographicViewObserver<GeographicAreaView>) | null;

  override readonly viewObservers!: ReadonlyArray<GeographicViewObserver<GeographicAreaView>>;

  /** @hidden */
  readonly gesture!: PositionGesture<GeographicAreaView>;

  protected createGesture(): PositionGesture<GeographicAreaView> {
    return new PositionGesture(this, this);
  }

  @ViewProperty({type: Boolean, state: false})
  readonly highlighted!: ViewProperty<this, boolean>;

  highlight(timing?: AnyTiming | boolean): void {
    if (!this.highlighted.state) {
      if (timing === void 0 || timing === true) {
        timing = this.getLookOr(Look.timing, false);
      } else {
        timing = Timing.fromAny(timing);
      }
      this.willHighlight(timing as Timing | boolean);
      this.highlighted.setState(true);
      this.onHighlight(timing as Timing | boolean);
      this.didHighlight(timing as Timing | boolean);
    }
  }

  protected willHighlight(timing: Timing | boolean): void {
    const viewController = this.viewController;
    if (viewController !== null && viewController.geographicWillHighlight !== void 0) {
      viewController.geographicWillHighlight(timing, this);
    }
    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.geographicWillHighlight !== void 0) {
        viewObserver.geographicWillHighlight(timing, this);
      }
    }
  }

  protected onHighlight(timing: Timing | boolean): void {
    const accentColor = this.getLook(Look.accentColor);
    if (accentColor !== void 0) {
      if (this.fill.takesPrecedence(View.Intrinsic)) {
        const opacity = GeographicAreaView.HighlightedFillOpacity;
        this.fill.setState(accentColor.alpha(opacity), timing, View.Intrinsic);
      }
      if (this.stroke.takesPrecedence(View.Intrinsic)) {
        const opacity = GeographicAreaView.HighlightedStrokeOpacity;
        this.stroke.setState(accentColor.alpha(opacity), timing, View.Intrinsic);
      }
    }
  }

  protected didHighlight(timing: Timing | boolean): void {
    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.geographicDidHighlight !== void 0) {
        viewObserver.geographicDidHighlight(timing, this);
      }
    }
    const viewController = this.viewController;
    if (viewController !== null && viewController.geographicDidHighlight !== void 0) {
      viewController.geographicDidHighlight(timing, this);
    }
  }

  unhighlight(timing?: AnyTiming | boolean): void {
    if (this.highlighted.state) {
      if (timing === void 0 || timing === true) {
        timing = this.getLookOr(Look.timing, false);
      } else {
        timing = Timing.fromAny(timing);
      }
      this.willUnhighlight(timing as Timing | boolean);
      this.highlighted.setState(false);
      this.onUnhighlight(timing as Timing | boolean);
      this.didUnhighlight(timing as Timing | boolean);
    }
  }

  protected willUnhighlight(timing: Timing | boolean): void {
    const viewController = this.viewController;
    if (viewController !== null && viewController.geographicWillUnhighlight !== void 0) {
      viewController.geographicWillUnhighlight(timing, this);
    }
    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.geographicWillUnhighlight !== void 0) {
        viewObserver.geographicWillUnhighlight(timing, this);
      }
    }
  }

  protected onUnhighlight(timing: Timing | boolean): void {
    const accentColor = this.getLook(Look.accentColor);
    if (accentColor !== void 0) {
      if (this.fill.takesPrecedence(View.Intrinsic)) {
        const opacity = GeographicAreaView.FillOpacity;
        this.fill.setState(accentColor.alpha(opacity), timing, View.Intrinsic);
      }
      if (this.stroke.takesPrecedence(View.Intrinsic)) {
        const opacity = GeographicAreaView.StrokeOpacity;
        this.stroke.setState(accentColor.alpha(opacity), timing, View.Intrinsic);
      }
    }
  }

  protected didUnhighlight(timing: Timing | boolean): void {
    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.geographicDidUnhighlight !== void 0) {
        viewObserver.geographicDidUnhighlight(timing, this);
      }
    }
    const viewController = this.viewController;
    if (viewController !== null && viewController.geographicDidUnhighlight !== void 0) {
      viewController.geographicDidUnhighlight(timing, this);
    }
  }

  setState(geographic: Geographic, timing?: AnyTiming | boolean): void {
    if (geographic instanceof GeographicArea) {
      if (timing === void 0 || timing === true) {
        timing = this.getLookOr(Look.timing, false);
      } else {
        timing = Timing.fromAny(timing);
      }
      this.geoPath.setState(geographic.geometry, View.Extrinsic);
      if (geographic.fill !== null) {
        this.fill.setState(geographic.fill, timing, View.Extrinsic);
      } else {
        this.fill.setPrecedence(View.Intrinsic);
      }
      if (geographic.stroke !== null) {
        this.stroke.setState(geographic.stroke, timing, View.Extrinsic);
      } else {
        this.stroke.setPrecedence(View.Intrinsic);
      }
      if (geographic.strokeWidth !== null) {
        this.strokeWidth.setState(geographic.strokeWidth, timing, View.Extrinsic);
      } else {
        this.strokeWidth.setPrecedence(View.Intrinsic);
      }
    }
  }

  protected override onApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean): void {
    super.onApplyTheme(theme, mood, timing);
    const accentColor = theme.getOr(Look.accentColor, mood, null);
    if (accentColor !== null) {
      if (this.fill.takesPrecedence(View.Intrinsic)) {
        const opacity = this.highlighted.state
                      ? GeographicAreaView.HighlightedFillOpacity
                      : GeographicAreaView.FillOpacity;
        this.fill.setState(accentColor.alpha(opacity), timing, View.Intrinsic);
      }
      if (this.stroke.takesPrecedence(View.Intrinsic)) {
        const opacity = this.highlighted.state
                      ? GeographicAreaView.HighlightedStrokeOpacity
                      : GeographicAreaView.StrokeOpacity;
        this.stroke.setState(accentColor.alpha(opacity), timing, View.Intrinsic);
      }
    }
    this.strokeWidth.setState(1, timing, View.Intrinsic);
  }

  didMovePress(input: PositionGestureInput, event: Event | null): void {
    const dx = input.dx;
    const dy = input.dy;
    if (dx * dx + dy * dy >= 4 * 4) {
      input.preventDefault();
    }
  }

  didPress(input: PositionGestureInput, event: Event | null): void {
    if (!input.defaultPrevented) {
      const viewObservers = this.viewObservers;
      for (let i = 0, n = viewObservers.length; i < n; i += 1) {
        const viewObserver = viewObservers[i]!;
        if (viewObserver.geographicDidPress !== void 0) {
          viewObserver.geographicDidPress(input, event, this);
        }
      }
      const viewController = this.viewController;
      if (viewController !== null && viewController.geographicDidPress !== void 0) {
        viewController.geographicDidPress(input, event, this);
      }
    }
  }

  didLongPress(input: PositionGestureInput): void {
    if (!input.defaultPrevented) {
      const viewObservers = this.viewObservers;
      for (let i = 0, n = viewObservers.length; i < n; i += 1) {
        const viewObserver = viewObservers[i]!;
        if (viewObserver.geographicDidLongPress !== void 0) {
          viewObserver.geographicDidLongPress(input, this);
        }
      }
      const viewController = this.viewController;
      if (viewController !== null && viewController.geographicDidLongPress !== void 0) {
        viewController.geographicDidLongPress(input, this);
      }
    }
  }

  static fromGeographic(geographic: GeographicArea): GeographicAreaView {
    const view = new GeographicAreaView();
    view.geoPath.setState(geographic.geometry);
    if (geographic.fill !== null) {
      view.fill.setState(geographic.fill);
    }
    if (geographic.stroke !== null) {
      view.stroke.setState(geographic.stroke);
    }
    if (geographic.strokeWidth !== null) {
      view.strokeWidth.setState(geographic.strokeWidth);
    }
    return view;
  }

  /** @hidden */
  static FillOpacity: number = 0.1;
  /** @hidden */
  static StrokeOpacity: number = 0.2;
  /** @hidden */
  static HighlightedFillOpacity: number = 0.25;
  /** @hidden */
  static HighlightedStrokeOpacity: number = 0.5;
}
