import {Component, ComponentManagerService} from "@swim/component";
import type {PrismPlugin} from "./PrismPlugin";
import {PrismManager} from "./PrismManager";

/** @hidden */
export abstract class PrismService<C extends Component> extends ComponentManagerService<C, PrismManager<C>> {
  get plugins(): ReadonlyArray<PrismPlugin> {
    return this.manager.plugins;
  }

  override initManager(): PrismManager<C> {
    return PrismManager.global();
  }
}
