import {Attr, Value, Record} from "@swim/structure";
import type {Uri} from "@swim/uri";
import {ModelMapDownlink} from "@swim/model";
import {LaneModel} from "./LaneModel";

export class MapLaneModel extends LaneModel {
  @ModelMapDownlink<MapLaneModel>({
    consume: true,
    nodeUri(): Uri {
      return this.owner.ownNodeUri;
    },
    laneUri(): Uri {
      return this.owner.ownLaneUri;
    },
    didUpdate(key: Value, newValue: Value, oldValue: Value): void {
      this.owner.onEvent(Attr.of("update", key).concat(newValue));
    },
    didRemove(key: Value, oldValue: Value): void {
      this.owner.onEvent(Record.of(Attr.of("remove", key)));
    },
  })
  readonly data!: ModelMapDownlink<this>;
}
