import {ModelContextType, ModelFlags, Model} from "@swim/model";
import {StatusGroup} from "../status/StatusGroup";
import {IndicatorMap} from "./IndicatorMap";
import {IndicatedTrait} from "./IndicatedTrait";

export class IndicatedGroup extends StatusGroup {
  constructor() {
    super();
    Object.defineProperty(this, "indicatorMap", {
      value: IndicatorMap.empty(),
      enumerable: true,
      configurable: true,
    });
  }

  readonly indicatorMap!: IndicatorMap;

  /** @hidden */
  protected setIndicatorMap(indicatorMap: IndicatorMap): void {
    Object.defineProperty(this, "indicatorMap", {
      value: indicatorMap,
      enumerable: true,
      configurable: true,
    });
  }

  protected override didRequestUpdate(targetModel: Model, updateFlags: ModelFlags, immediate: boolean): void {
    super.didRequestUpdate(targetModel, updateFlags, immediate);
    if (targetModel !== this) {
      this.requireUpdate(updateFlags & (Model.NeedsAggregate | Model.NeedsReconcile));
    }
  }

  override needsRefresh(refreshFlags: ModelFlags, modelContext: ModelContextType<this>): ModelFlags {
    if ((this.modelFlags & Model.NeedsReconcile) === 0) {
      refreshFlags &= ~Model.NeedsReconcile;
    }
    return refreshFlags;
  }

  protected override refreshChildModels(refreshFlags: ModelFlags, modelContext: ModelContextType<this>,
                                        refreshChildModel: (this: this, childModel: Model, refreshFlags: ModelFlags,
                                                            modelContext: ModelContextType<this>) => void): void {
    if ((refreshFlags & Model.NeedsReconcile) !== 0) {
      this.reconcileChildIndicatorMaps(refreshFlags, modelContext, refreshChildModel);
    } else {
      super.refreshChildModels(refreshFlags, modelContext, refreshChildModel);
    }
  }

  protected reconcileChildIndicatorMaps(refreshFlags: ModelFlags, modelContext: ModelContextType<this>,
                                        refreshChildModel: (this: this, childModel: Model, refreshFlags: ModelFlags,
                                                            modelContext: ModelContextType<this>) => void): void {
    // Collect indicator types when reconciling child models.
    let indicatorMap = IndicatorMap.empty();

    type self = this;
    function reconcileChildIndicatorMap(this: self, childModel: Model, refreshFlags: ModelFlags,
                                        modelContext: ModelContextType<self>): void {
      refreshChildModel.call(this, childModel, refreshFlags, modelContext);
      const childIndicatedTrait = childModel.getTrait(IndicatedTrait);
      if (childIndicatedTrait !== null) {
        indicatorMap = indicatorMap.merged(childIndicatedTrait.indicatorMap);
      }
    }
    super.refreshChildModels(refreshFlags, modelContext, reconcileChildIndicatorMap);

    this.setIndicatorMap(indicatorMap);
  }

  static override mountFlags: ModelFlags = StatusGroup.mountFlags | Model.NeedsAggregate | Model.NeedsReconcile;
  static override insertChildFlags: ModelFlags = StatusGroup.insertChildFlags | Model.NeedsAggregate | Model.NeedsReconcile;
  static override removeChildFlags: ModelFlags = StatusGroup.removeChildFlags | Model.NeedsAggregate | Model.NeedsReconcile;
}
