import {Values} from "@swim/util";
import type {ModelContext, Model} from "@swim/model";
import {Feel} from "@swim/theme";
import {View} from "@swim/view";
import type {ViewNode} from "@swim/dom";
import {ComponentModel, ComponentView, ComponentFastener, CompositeComponent} from "@swim/component";
import {TreeRoot, TreeSeed, TreeVein, TreeStem, TreeLimb, TreeView} from "@swim/grid";
import {IndicatorType, EntityTrait, EntityGroup} from "@swim/lens";
import {CatalogEntityLimb} from "./CatalogEntityLimb";

export class CatalogEntityTree extends CompositeComponent {
  protected createTree(): TreeView {
    const treeView = TreeView.create();
    treeView.modifyTheme(Feel.default, [[Feel.primary, 1]]);
    treeView.seed.setState(this.createSeed());

    const treeStem = TreeStem.create();
    treeView.setStem(treeStem);
    this.updateStem(treeStem);

    return treeView;
  }

  protected initTree(treeView: TreeView): void {
    const childComponents = this.childComponents;
    for (let i = 0; i < childComponents.length; i += 1) {
      const childComponent = childComponents[i];
      if (childComponent instanceof CatalogEntityLimb) {
        this.onInsertBranch(childComponent);
      }
    }

    const entityGroup = this.entities.model;
    if (entityGroup !== null) {
      this.onReconcileEntities(entityGroup);
    }
  }

  protected createSeed(indicatorTypes?: ReadonlyArray<IndicatorType>): TreeSeed {
    const roots = new Array<TreeRoot>();
    roots.push(TreeRoot.create("icon", 0, 0, 64));
    roots.push(TreeRoot.create("title", 1, 1, 200));
    if (indicatorTypes !== void 0) {
      for (let i = 0; i < indicatorTypes.length; i += 1) {
        const indicatorType = indicatorTypes[i]!;
        const indicatorRoot = this.createIndicatorRoot(indicatorType);
        if (indicatorRoot !== null) {
          roots.push(indicatorRoot);
        }
      }
    }
    roots.push(TreeRoot.create("disclose", 0, 0, 40));
    return TreeSeed.create(roots);
  }

  protected createIndicatorRoot(indicatorType: IndicatorType): TreeRoot | null {
    return TreeRoot.create(indicatorType.key, 0, 0, 100, true);
  }

  protected updateStem(treeStem: TreeStem, indicatorTypes?: ReadonlyArray<IndicatorType>): void {
    let nameVein = treeStem.getChildView("title") as TreeVein | null;
    if (nameVein === null) {
      nameVein = this.createNameVein();
      if (nameVein !== null) {
        treeStem.append(nameVein, "title");
      }
    }

    if (indicatorTypes !== void 0) {
      for (let i = 0; i < indicatorTypes.length; i += 1) {
        const indicatorType = indicatorTypes[i]!;
        let indicatorVein = treeStem.getChildView(indicatorType.key) as TreeVein | null;
        if (indicatorVein === null) {
          indicatorVein = this.createIndicatorVein(indicatorType);
          if (indicatorVein !== null) {
            treeStem.append(indicatorVein, indicatorType.key);
          }
        }
      }
    }
  }

  protected createNameVein(): TreeVein | null {
    return TreeVein.create().text("Name");
  }

  protected createIndicatorVein(indicatorType: IndicatorType): TreeVein | null {
    return TreeVein.create().text(indicatorType.name);
  }

  protected initEntities(entityGroup: EntityGroup): void {
    const entities = entityGroup.childModels;
    for (let i = 0, n = entities.length; i < n; i += 1) {
      const entity = entities[i]!;
      const entityTrait = entity.getTrait(EntityTrait);
      if (entityTrait !== null) {
        this.onInsertEntity(entityTrait, null);
      }
    }
    this.onReconcileEntities(entityGroup);
  }

  protected onInsertEntity(childEntity: EntityTrait, targetEntity: EntityTrait | null): void {
    const id = childEntity.uri.toString();
    let componentFastener = this.getComponentFastener(id) as ComponentFastener<this, CatalogEntityLimb> | null;
    if (componentFastener === null) {
      componentFastener = new CatalogEntityTree.BranchFastener(this, id, "branch") as ComponentFastener<this, CatalogEntityLimb>;
      this.setComponentFastener(id, componentFastener);
      const branchComponent = new CatalogEntityLimb();
      branchComponent.entity.setTrait(childEntity);
      const targetComponent = targetEntity !== null ? this.getChildComponent(targetEntity.uri.toString()) : null;
      this.insertChildComponent(branchComponent, targetComponent, id);
    }
  }

  protected onRemoveEntity(childEntity: EntityTrait): void {
    const id = childEntity.uri.toString();
    const componentFastener = this.getComponentFastener(id) as ComponentFastener<this, CatalogEntityLimb> | null;
    if (componentFastener !== null) {
      componentFastener.removeComponent();
      this.setComponentFastener(id, null);
    }
  }

  protected onInsertBranch(branchComponent: CatalogEntityLimb): void {
    const treeView = this.tree.view;
    if (treeView !== null) {
      const id = branchComponent.entity.trait!.uri.toString();
      let treeLimb: TreeLimb | null;
      if (this.entities.model!.isSorted()) {
        let index = this.lookupBranch(treeView, id);
        if (index >= 0) {
          treeLimb = (treeView.node.childNodes[index] as ViewNode).view as TreeLimb;
        } else {
          index = -(index + 1);
          treeLimb = branchComponent.limb.createView();
          if (treeLimb !== null) {
            const targetNode = treeView.node.childNodes[index] as ViewNode | undefined;
            const targetView = targetNode !== void 0 ? targetNode.view : void 0;
            treeView.insertChildView(treeLimb, targetView !== void 0 ? targetView : null, id);
          }
        }
        branchComponent.limb.setView(treeLimb);
      } else {
        treeLimb = branchComponent.limb.createView();
        if (treeLimb !== null) {
          const targetBranch = this.nextChildComponent(branchComponent);
          const targetView = targetBranch instanceof CatalogEntityLimb ? targetBranch.limb.view : null;
          treeView.insertChildView(treeLimb, targetView, id);
          branchComponent.limb.setView(treeLimb);
        }
      }
    }
  }

  protected onRemoveBranch(branchComponent: CatalogEntityLimb): void {
    const treeLimb = branchComponent.limb.view;
    if (treeLimb !== null) {
      treeLimb.remove();
    }
  }

  protected lookupBranch(treeView: TreeView, key: unknown): number {
    const childNodes = treeView.node.childNodes;
    let lo = 0;
    let hi = childNodes.length - 1;
    while (lo <= hi && !((childNodes[lo] as ViewNode).view instanceof TreeLimb)) {
      lo += 1;
    }
    while (hi >= lo && !((childNodes[hi] as ViewNode).view instanceof TreeLimb)) {
      hi -= 1;
    }
    while (lo <= hi) {
      const mid = (lo + hi) >>> 1;
      const order = Values.compare(key, (childNodes[mid] as ViewNode).view!.key);
      if (order > 0) {
        lo = mid + 1;
      } else if (order < 0) {
        hi = mid - 1;
      } else {
        return mid;
      }
    }
    return -(lo + 1);
  }

  protected onReconcileEntities(entityGroup: EntityGroup): void {
    const treeView = this.tree.view;
    if (treeView !== null) {
      const indicatorTypes = entityGroup.indicatorMap.indicatorTypes;
      const oldSeed = treeView.seed.state;
      const newSeed = this.createSeed(indicatorTypes);
      if (oldSeed === null || !oldSeed.equivalentTo(newSeed)) {
        treeView.seed.setState(newSeed);
        const treeStem = treeView.stem;
        if (treeStem !== null) {
          this.updateStem(treeStem, indicatorTypes);
        }
        treeView.requireUpdate(View.NeedsResize | View.NeedsLayout);
      }
    }
  }

  @ComponentView<CatalogEntityTree, TreeView>({
    key: "subtree",
    type: TreeView,
    onSetView(treeView: TreeView | null): void {
      if (treeView !== null) {
        this.owner.initTree(treeView);
      }
    },
    createView(): TreeView | null {
      return this.owner.createTree();
    },
  })
  readonly tree!: ComponentView<this, TreeView>;

  static BranchFastener = ComponentFastener.define<CatalogEntityTree, CatalogEntityLimb>({
    type: CatalogEntityLimb,
    onSetComponent(newBranch: CatalogEntityLimb | null, oldBranch: CatalogEntityLimb | null): void {
      if (oldBranch !== null) {
        this.owner.onRemoveBranch(oldBranch);
      }
      if (newBranch !== null) {
        this.owner.onInsertBranch(newBranch);
      }
    },
  });

  @ComponentModel<CatalogEntityTree, EntityGroup>({
    type: EntityGroup,
    observe: true,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initEntities(entityGroup);
      }
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        const targetEntityTrait = targetModel !== null ? targetModel.getTrait(EntityTrait) : null;
        this.owner.onInsertEntity(childEntityTrait, targetEntityTrait);
      }
    },
    modelWillRemoveChildModel(childModel: Model, entityGroup: EntityGroup): void {
      const childEntityTrait = childModel.getTrait(EntityTrait);
      if (childEntityTrait !== null) {
        this.owner.onRemoveEntity(childEntityTrait);
      }
    },
    modelDidReconcile(modelContext: ModelContext, entityGroup: EntityGroup): void {
      this.owner.onReconcileEntities(entityGroup);
    },
  })
  readonly entities!: ComponentModel<this, EntityGroup>;
}
