import type {Uri} from "@swim/uri";
import {
  ModelContextType,
  ModelFlags,
  Model,
  TraitContextType,
  TraitService,
  TraitModelType,
  TraitModel,
  GenericTrait,
} from "@swim/model";
import type {Graphics} from "@swim/graphics";
import type {StatusVector} from "../status/StatusVector";
import {StatusFactor} from "../status/StatusFactor";
import {StatusTrait} from "../status/StatusTrait";
import {IndicatorGroup} from "../indicator/IndicatorGroup";
import {IndicatedTrait} from "../indicator/IndicatedTrait";
import type {EntityTraitObserver} from "./EntityTraitObserver";
import {EntityGroup} from "./EntityGroup";
import {DomainTrait} from "../"; // forward import
import {SelectionOptions, SelectionManager} from "../"; // forward import
import {SelectionService} from "../"; // forward import

export class EntityTrait extends GenericTrait {
  constructor(uri: Uri) {
    super();
    Object.defineProperty(this, "uri", {
      value: uri,
      enumerable: true,
    });
    Object.defineProperty(this, "title", {
      value: void 0,
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "icon", {
      value: null,
      enumerable: true,
      configurable: true,
    });
    Object.defineProperty(this, "selected", {
      value: false,
      enumerable: true,
      configurable: true,
    });
  }

  override readonly traitObservers!: ReadonlyArray<EntityTraitObserver>;

  readonly uri!: Uri;

  readonly title!: string | undefined;

  setTitle(title: string | undefined): void {
    if (this.title !== title) {
      this.willSetTitle(title);
      Object.defineProperty(this, "title", {
        value: title,
        enumerable: true,
        configurable: true,
      });
      this.onSetTitle(title);
      this.didSetTitle(title);
    }
  }

  protected willSetTitle(title: string | undefined): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityWillSetTitle !== void 0) {
        traitObserver.entityWillSetTitle(title, this);
      }
    }
  }

  protected onSetTitle(title: string | undefined): void {
    // hook
  }

  protected didSetTitle(title: string | undefined): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityDidSetTitle !== void 0) {
        traitObserver.entityDidSetTitle(title, this);
      }
    }
  }

  readonly icon!: Graphics | null;

  setIcon(icon: Graphics | null): void {
    if (this.icon !== icon) {
      this.willSetIcon(icon);
      Object.defineProperty(this, "icon", {
        value: icon,
        enumerable: true,
        configurable: true,
      });
      this.onSetIcon(icon);
      this.didSetIcon(icon);
    }
  }

  protected willSetIcon(icon: Graphics | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityWillSetIcon !== void 0) {
        traitObserver.entityWillSetIcon(icon, this);
      }
    }
  }

  protected onSetIcon(icon: Graphics | null): void {
    // hook
  }

  protected didSetIcon(icon: Graphics | null): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityDidSetIcon !== void 0) {
        traitObserver.entityDidSetIcon(icon, this);
      }
    }
  }

  /** @hidden */
  readonly selected!: boolean;

  isSelected(): boolean {
    return this.selected;
  }

  select(options?: SelectionOptions): void {
    if (!this.selected) {
      Object.defineProperty(this, "selected", {
        value: true,
        enumerable: true,
        configurable: true,
      });
      const selectionManager = this.selectionService.manager;
      if (selectionManager !== void 0) {
        selectionManager.selectEntity(this, options);
      }
    }
  }

  /** @hidden */
  willSelect(): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityWillSelect !== void 0) {
        traitObserver.entityWillSelect(this);
      }
    }
  }

  /** @hidden */
  onSelect(): void {
    Object.defineProperty(this, "selected", {
      value: true,
      enumerable: true,
      configurable: true,
    });
  }

  /** @hidden */
  didSelect(): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityDidSelect !== void 0) {
        traitObserver.entityDidSelect(this);
      }
    }
  }

  unselect(): void {
    if (this.selected) {
      Object.defineProperty(this, "selected", {
        value: false,
        enumerable: true,
        configurable: true,
      });
      this.selectionService.manager.unselectEntity(this);
    }
  }

  /** @hidden */
  willUnselect(): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityWillUnselect !== void 0) {
        traitObserver.entityWillUnselect(this);
      }
    }
  }

  /** @hidden */
  onUnselect(): void {
    Object.defineProperty(this, "selected", {
      value: false,
      enumerable: true,
      configurable: true,
    });
  }

  /** @hidden */
  didUnselect(): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.entityDidUnselect !== void 0) {
        traitObserver.entityDidUnselect(this);
      }
    }
  }

  unselectAll(): void {
    this.selectionService.manager.unselectAll();
  }

  protected createSubentities(): EntityGroup | null {
    const entityGroup = new EntityGroup();
    entityGroup.setTrait("status", new StatusTrait());
    return entityGroup;
  }

  protected initSubentities(entityGroup: EntityGroup): void {
    // hook
  }

  /** @hidden */
  protected aggregateStatus(statusFactors: {[statusName: string]: StatusFactor | undefined}): void {
    const statusTrait = this.getTrait(StatusTrait);
    if (statusTrait !== null) {
      for (const statusName in statusFactors) {
        const statusFactor = statusFactors[statusName]!;
        if (statusFactor !== void 0) {
          statusTrait.setStatusFactor(statusName, statusFactor);
        } else {
          statusTrait.setStatusFactor(statusName, null);
        }
      }
    }
  }

  /** @hidden */
  protected aggregateIndicatorsStatus(indicatorGroup: IndicatorGroup, statusVector: StatusVector,
                                      statusFactors: {[statusName: string]: StatusFactor | undefined}): void {
    if (statusVector.isDefined() && indicatorGroup.isConsuming()) {
      statusFactors["indicators"] = StatusFactor.create("Indicators", statusVector);
    } else {
      statusFactors["indicators"] = void 0;
    }
  }

  /** @hidden */
  protected aggregateSubentitiesStatus(entityGroup: EntityGroup, statusVector: StatusVector,
                                       statusFactors: {[statusName: string]: StatusFactor | undefined}): void {
    if (statusVector.isDefined() && entityGroup.isConsuming()) {
      statusFactors["subentities"] = StatusFactor.create("Subentities", statusVector);
    } else {
      statusFactors["subentities"] = void 0;
    }
  }

  protected aggregatedChildModelStatus(childModel: Model, statusFactors: {[statusName: string]: StatusFactor | undefined}): void {
    const childStatusTrait = childModel.getTrait(StatusTrait);
    if (childStatusTrait !== null) {
      if (childModel instanceof IndicatorGroup) {
        const indicated = this.getTrait(IndicatedTrait);
        if (indicated !== null && childModel === indicated.indicators.model) {
          this.aggregateIndicatorsStatus(childModel, childStatusTrait.statusVector, statusFactors);
        }
      } else if (childModel instanceof EntityGroup) {
        this.aggregateSubentitiesStatus(childModel, childStatusTrait.statusVector, statusFactors);
      }
    }
  }

  protected override analyzeChildModels(analyzeFlags: ModelFlags, modelContext: TraitContextType<this>,
                                        analyzeChildModel: (this: TraitModelType<this>, childModel: Model, analyzeFlags: ModelFlags,
                                                            modelContext: TraitContextType<this>) => void,
                                        analyzeChildModels: (this: TraitModelType<this>, analyzeFlags: ModelFlags, modelContext: TraitContextType<this>,
                                                             analyzeChildModel: (this: TraitModelType<this>, childModel: Model, analyzeFlags: ModelFlags,
                                                                                 modelContext: TraitContextType<this>) => void) => void): void {
    if ((analyzeFlags & Model.NeedsAggregate) !== 0) {
      this.aggregateChildStatuses(analyzeFlags, modelContext, analyzeChildModel, analyzeChildModels);
    } else {
      super.analyzeChildModels(analyzeFlags, modelContext, analyzeChildModel, analyzeChildModels);
    }
  }

  protected aggregateChildStatuses(analyzeFlags: ModelFlags, modelContext: TraitContextType<this>,
                                   analyzeChildModel: (this: TraitModelType<this>, childModel: Model, analyzeFlags: ModelFlags,
                                                       modelContext: TraitContextType<this>) => void,
                                   analyzeChildModels: (this: TraitModelType<this>, analyzeFlags: ModelFlags, modelContext: TraitContextType<this>,
                                                        analyzeChildModel: (this: TraitModelType<this>, childModel: Model, analyzeFlags: ModelFlags,
                                                                            modelContext: TraitContextType<this>) => void) => void): void {
    // Accumulate status factors when aggregating child models.
    const statusFactors = {} as {[statusName: string]: StatusFactor | undefined};

    type self = this;
    const self = this;
    function aggregateChildStatus(this: TraitModelType<self>, childModel: Model, analyzeFlags: ModelFlags,
                                  modelContext: ModelContextType<TraitModelType<self>>): void {
      analyzeChildModel.call(this, childModel, analyzeFlags, modelContext);
      self.aggregatedChildModelStatus(childModel, statusFactors);
    }
    analyzeChildModels.call(this.model as TraitModelType<this>, analyzeFlags, modelContext, aggregateChildStatus);

    this.aggregateStatus(statusFactors);
  }

  @TraitModel<EntityTrait, EntityGroup>({
    key: true,
    type: EntityGroup,
    onSetModel(entityGroup: EntityGroup | null): void {
      if (entityGroup !== null) {
        this.owner.initSubentities(entityGroup);
      }
    },
    createModel(): EntityGroup | null {
      return this.owner.createSubentities();
    }
  })
  readonly subentities!: TraitModel<this, EntityGroup>;

  @TraitService<EntityTrait, SelectionManager>({
    type: SelectionManager,
    observe: false,
    modelService: {
      extends: SelectionService,
      type: SelectionManager,
      observe: false,
    },
  })
  readonly selectionService!: TraitService<this, SelectionManager>;

  protected override onMount(): void {
    super.onMount();
    const domain = this.getSuperTrait(DomainTrait);
    if (domain !== null) {
      domain.injectEntity(this);
    }
  }

  protected override willUnmount(): void {
    super.willUnmount();
    const selectionManager = this.selectionService.manager;
    if (selectionManager !== void 0) {
      selectionManager.unselectEntity(this);
    }
  }
}
