import {ModelContextType, ModelFlags, Model, ModelFastener} from "@swim/model";
import type {StatusVector} from "../status/StatusVector";
import {StatusFactor} from "../status/StatusFactor";
import {StatusTrait} from "../status/StatusTrait";
import {DomainGroup} from "../domain/DomainGroup";
import {SelectionModel} from "../selection/SelectionModel";

export class SessionModel extends SelectionModel {
  constructor() {
    super();
    this.initChildModels();
  }

  protected initChildModels(): void {
    this.domains.injectModel();
  }

  /** @hidden */
  protected override aggregateStatus(statusVector: StatusVector): void {
    super.aggregateStatus(statusVector);
    const statusTrait = this.getTrait(StatusTrait);
    if (statusTrait !== null) {
      const oldFactor = statusTrait.getStatusFactor("domains");
      if (!statusVector.isDefined()) {
        statusTrait.setStatusFactor("domains", null);
      } else if (oldFactor === null || !statusVector.equals(oldFactor.vector)) {
        statusTrait.setStatusFactor("domains", StatusFactor.create("Domains", statusVector));
      }
    }
  }

  protected override didRequestUpdate(targetModel: Model, updateFlags: ModelFlags, immediate: boolean): void {
    super.didRequestUpdate(targetModel, updateFlags, immediate);
    if (targetModel !== this) {
      this.requireUpdate(updateFlags & (Model.NeedsAggregate | Model.NeedsReconcile));
    }
  }

  override needsAnalyze(analyzeFlags: ModelFlags, modelContext: ModelContextType<this>): ModelFlags {
    if ((this.modelFlags & Model.NeedsAggregate) !== 0) {
      analyzeFlags &= ~Model.NeedsAggregate;
    }
    return analyzeFlags;
  }

  protected createDomains(): DomainGroup {
    const domainGroup = new DomainGroup();
    domainGroup.setTrait("status", new StatusTrait());
    return domainGroup;
  }

  @ModelFastener<SessionModel, DomainGroup>({
    key: true,
    type: DomainGroup,
    createModel(): DomainGroup {
      return this.owner.createDomains();
    },
  })
  readonly domains!: ModelFastener<this, DomainGroup>;
}
