import {Model, TraitModelType, Trait, TraitFastener, GenericTrait} from "@swim/model";
import {WidgetTrait} from "./WidgetTrait";
import type {WidgetGroupObserver} from "./WidgetGroupObserver";

export class WidgetGroup extends GenericTrait {
  constructor() {
    super();
    Object.defineProperty(this, "widgetFasteners", {
      value: [],
      enumerable: true,
    });
  }

  override readonly traitObservers!: ReadonlyArray<WidgetGroupObserver>;

  insertWidget(widgetTrait: WidgetTrait, targetTrait: Trait | null = null): void {
    const widgetFasteners = this.widgetFasteners as TraitFastener<this, WidgetTrait>[];
    let targetIndex = widgetFasteners.length;
    if (targetTrait !== null) {
      for (let i = 0, n = widgetFasteners.length; i < n; i += 1) {
        const widgetFastener = widgetFasteners[i]!;
        if (widgetFastener.trait === widgetTrait) {
          return;
        } else if (widgetFastener.trait === targetTrait) {
          targetIndex = i;
        }
      }
    }
    const widgetFastener = this.createWidgetFastener(widgetTrait);
    widgetFasteners.splice(targetIndex, 0, widgetFastener);
    widgetFastener.setTrait(widgetTrait, targetTrait);
    if (this.isMounted()) {
      widgetFastener.mount();
    }
  }

  removeWidget(widgetTrait: WidgetTrait): void {
    const widgetFasteners = this.widgetFasteners as TraitFastener<this, WidgetTrait>[];
    for (let i = 0, n = widgetFasteners.length; i < n; i += 1) {
      const widgetFastener = widgetFasteners[i]!;
      if (widgetFastener.trait === widgetTrait) {
        widgetFastener.setTrait(null);
        if (this.isMounted()) {
          widgetFastener.unmount();
        }
        widgetFasteners.splice(i, 1);
        break;
      }
    }
  }

  protected initWidget(widgetTrait: WidgetTrait, widgetFastener: TraitFastener<this, WidgetTrait>): void {
    // hook
  }

  protected willSetWidget(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null,
                          targetTrait: Trait | null, widgetFastener: TraitFastener<this, WidgetTrait>): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.traitWillSetWidget !== void 0) {
        traitObserver.traitWillSetWidget(newWidgetTrait, oldWidgetTrait, targetTrait, this);
      }
    }
  }

  protected onSetWidget(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null,
                        targetTrait: Trait | null, widgetFastener: TraitFastener<this, WidgetTrait>): void {
    if (newWidgetTrait !== null) {
      this.initWidget(newWidgetTrait, widgetFastener);
    }
  }

  protected didSetWidget(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null,
                         targetTrait: Trait | null, widgetFastener: TraitFastener<this, WidgetTrait>): void {
    const traitObservers = this.traitObservers;
    for (let i = 0, n = traitObservers.length; i < n; i += 1) {
      const traitObserver = traitObservers[i]!;
      if (traitObserver.traitDidSetWidget !== void 0) {
        traitObserver.traitDidSetWidget(newWidgetTrait, oldWidgetTrait, targetTrait, this);
      }
    }
  }

  /** @hidden */
  static WidgetFastener = TraitFastener.define<WidgetGroup, WidgetTrait>({
    type: WidgetTrait,
    sibling: false,
    willSetTrait(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null, targetTrait: Trait | null): void {
      this.owner.willSetWidget(newWidgetTrait, oldWidgetTrait, targetTrait, this);
    },
    onSetTrait(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null, targetTrait: Trait | null): void {
      this.owner.onSetWidget(newWidgetTrait, oldWidgetTrait, targetTrait, this);
    },
    didSetTrait(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null, targetTrait: Trait | null): void {
      this.owner.didSetWidget(newWidgetTrait, oldWidgetTrait, targetTrait, this);
    },
  });

  protected createWidgetFastener(widgetTrait: WidgetTrait): TraitFastener<this, WidgetTrait> {
    return new WidgetGroup.WidgetFastener(this, widgetTrait.key, "widget");
  }

  /** @hidden */
  readonly widgetFasteners!: ReadonlyArray<TraitFastener<this, WidgetTrait>>;

  /** @hidden */
  protected mountWidgetFasteners(): void {
    const widgetFasteners = this.widgetFasteners;
    for (let i = 0, n = widgetFasteners.length; i < n; i += 1) {
      const widgetFastener = widgetFasteners[i]!;
      widgetFastener.mount();
    }
  }

  /** @hidden */
  protected unmountWidgetFasteners(): void {
    const widgetFasteners = this.widgetFasteners;
    for (let i = 0, n = widgetFasteners.length; i < n; i += 1) {
      const widgetFastener = widgetFasteners[i]!;
      widgetFastener.unmount();
    }
  }

  protected detectWidget(model: Model): WidgetTrait | null {
    return model.getTrait(WidgetTrait);
  }

  protected detectWidgets(model: TraitModelType<this>): void {
    const childModels = model.childModels;
    for (let i = 0, n = childModels.length; i < n; i += 1) {
      const childModel = childModels[i]!;
      const widgetTrait = this.detectWidget(childModel);
      if (widgetTrait !== null) {
        this.insertWidget(widgetTrait);
      }
    }
  }

  protected override didSetModel(newModel: TraitModelType<this> | null, oldModel: TraitModelType<this> | null): void {
    if (newModel !== null) {
      this.detectWidgets(newModel);
    }
    super.didSetModel(newModel, oldModel);
  }

  protected override onInsertChildModel(childModel: Model, targetModel: Model | null): void {
    super.onInsertChildModel(childModel, targetModel);
    const widgetTrait = this.detectWidget(childModel);
    if (widgetTrait !== null) {
      const targetTrait = targetModel !== null ? this.detectWidget(targetModel) : null;
      this.insertWidget(widgetTrait, targetTrait);
    }
  }

  protected override onRemoveChildModel(childModel: Model): void {
    super.onRemoveChildModel(childModel);
    const widgetTrait = this.detectWidget(childModel);
    if (widgetTrait) {
      this.removeWidget(widgetTrait);
    }
  }

  /** @hidden */
  protected override mountTraitFasteners(): void {
    super.mountTraitFasteners();
    this.mountWidgetFasteners();
  }

  /** @hidden */
  protected override unmountTraitFasteners(): void {
    this.unmountWidgetFasteners();
    super.unmountTraitFasteners();
  }
}
