import type {Trait} from "@swim/model";
import {HtmlView} from "@swim/dom";
import {
  Component,
  ComponentService,
  ComponentFastener,
  ComponentTrait,
  ComponentView,
  CompositeComponent,
} from "@swim/component";
import {EntityTrait} from "@swim/lens";
import {WidgetTrait, WidgetGroup} from "@swim/widget";
import {PrismManager, PrismService} from "@swim/prism";
import {RefractionComponent} from "./RefractionComponent";

export class RefractorComponent extends CompositeComponent {
  protected attachEntity(entityTrait: EntityTrait): void {
    const widgetGroup = entityTrait.getTrait(WidgetGroup);
    if (widgetGroup !== null) {
      this.widgets.setTrait(widgetGroup);
    }
  }

  protected detachEntity(entityTrait: EntityTrait): void {
    this.widgets.setTrait(null);
    const childComponents = this.childComponents;
    for (let i = 0, n = childComponents.length; i < n; i += 1) {
      const childComponent = childComponents[i]!;
      if (childComponent instanceof RefractionComponent) {
        this.setComponentFastener(childComponent.key!, null);
      }
    }
    this.removeAll();
    const fixturesView = this.fixtures.view;
    if (fixturesView !== null) {
      fixturesView.removeAll();
    }
  }

  protected initFixtures(fixturesView: HtmlView): void {
    const childComponents = this.childComponents;
    for (let i = 0; i < childComponents.length; i += 1) {
      const childComponent = childComponents[i];
      if (childComponent instanceof RefractionComponent) {
        this.onInsertRefraction(childComponent);
      }
    }
  }

  protected initWidgets(widgetGroup: WidgetGroup): void {
    const widgetFasteners = widgetGroup.widgetFasteners;
    for (let i = 0, n = widgetFasteners.length; i < n; i += 1) {
      const widgetTrait = widgetFasteners[i]!.trait;
      if (widgetTrait !== null) {
        this.onInsertWidget(widgetTrait, null);
      }
    }
  }

  protected createRefraction(widgetTrait: WidgetTrait): RefractionComponent | null {
    const entityTrait = this.entity.trait;
    const refractionComponent = new RefractionComponent();
    refractionComponent.entity.setTrait(entityTrait);
    refractionComponent.widget.setTrait(widgetTrait);
    return refractionComponent;
  }

  protected onInsertWidget(childWidgetTrait: WidgetTrait, targetTrait: Trait | null): void {
    const id = childWidgetTrait.model!.key!;
    let refractionFastener = this.getComponentFastener(id) as ComponentFastener<this, RefractionComponent> | null;
    if (refractionFastener === null) {
      refractionFastener = new RefractorComponent.RefractionFastener(this, id, "refraction");
      this.setComponentFastener(id, refractionFastener);
      const refractionComponent = this.createRefraction(childWidgetTrait);
      if (refractionComponent !== null) {
        let targetComponent: Component | null = null;
        if (targetTrait !== null) {
          targetComponent = this.getChildComponent(targetTrait.model!.key!);
        }
        this.insertChildComponent(refractionComponent, targetComponent, id);
      }
    }
  }

  protected onRemoveWidget(childWidgetTrait: WidgetTrait): void {
    const id = childWidgetTrait.model!.key!;
    const refractionFastener = this.getComponentFastener(id) as ComponentFastener<this, RefractionComponent> | null;
    if (refractionFastener !== null) {
      refractionFastener.removeComponent();
      this.setComponentFastener(id, null);
    }
  }

  protected onInsertRefraction(refractionComponent: RefractionComponent): void {
    const targetComponent = this.nextChildComponent(refractionComponent);
    const widgetTrait = refractionComponent.widget.trait!;
    const id = widgetTrait.model!.key;

    const fixturesView = this.fixtures.view;
    if (fixturesView !== null) {
      const fixtureView = refractionComponent.widget.createView();
      if (fixtureView !== null) {
        const targetView = targetComponent instanceof RefractionComponent ? targetComponent.widget.view : null;
        fixturesView.insertChildView(fixtureView, targetView, id);
        refractionComponent.widget.setView(fixtureView);
      }
    }
  }

  protected onRemoveRefraction(refractionComponent: RefractionComponent): void {
    refractionComponent.widget.removeView();
  }

  static RefractionFastener = ComponentFastener.define<RefractorComponent, RefractionComponent>({
    type: RefractionComponent,
    onSetComponent(newRefractionComponent: RefractionComponent | null,
                   oldRefractionComponent: RefractionComponent | null): void {
      if (oldRefractionComponent !== null) {
        this.owner.onRemoveRefraction(oldRefractionComponent);
      }
      if (newRefractionComponent !== null) {
        this.owner.onInsertRefraction(newRefractionComponent);
      }
    },
  });

  @ComponentView<RefractorComponent, HtmlView>({
    type: HtmlView,
    onSetView(fixturesView: HtmlView | null): void {
      if (fixturesView !== null) {
        this.owner.initFixtures(fixturesView);
      }
    },
  })
  readonly fixtures!: ComponentView<this, HtmlView>;

  @ComponentTrait<RefractorComponent, EntityTrait>({
    type: EntityTrait,
    onSetTrait(newEntityTrait: EntityTrait | null, oldEntityTrait: EntityTrait | null): void {
      if (oldEntityTrait !== null) {
        this.owner.detachEntity(oldEntityTrait);
      }
      if (newEntityTrait !== null) {
        this.owner.attachEntity(newEntityTrait);
      }
    },
  })
  readonly entity!: ComponentTrait<this, EntityTrait>;

  @ComponentTrait<RefractorComponent, WidgetGroup>({
    type: WidgetGroup,
    observe: true,
    onSetTrait(widgetGroup: WidgetGroup | null): void {
      if (widgetGroup !== null) {
        this.owner.initWidgets(widgetGroup);
      }
    },
    traitDidSetWidget(newWidgetTrait: WidgetTrait | null, oldWidgetTrait: WidgetTrait | null, targetTrait: Trait | null, widgetGroup: WidgetGroup): void {
      if (oldWidgetTrait !== null) {
        this.owner.onRemoveWidget(oldWidgetTrait);
      }
      if (newWidgetTrait !== null) {
        this.owner.onInsertWidget(newWidgetTrait, targetTrait);
      }
    },
  })
  readonly widgets!: ComponentTrait<this, WidgetGroup>;

  @ComponentService<RefractorComponent, PrismManager>({
    type: PrismManager,
    extends: PrismService,
    observe: false,
  })
  readonly prismService!: PrismService<this>;

  protected override onUnmount(): void {
    super.onUnmount();
    this.entity.setTrait(null);
  }
}
