import {ModelFlags, Model, TraitContextType, TraitModel, GenericTrait} from "@swim/model";
import {StatusTrait} from "../status/StatusTrait";
import {IndicatorMap} from "./IndicatorMap";
import {IndicatorTrait} from "./IndicatorTrait";
import {IndicatorGroup} from "./IndicatorGroup";

export class IndicatedTrait extends GenericTrait {
  get indicatorMap(): IndicatorMap {
    const indicators = this.indicators.model;
    return indicators !== null ? indicators.indicatorMap : IndicatorMap.empty();
  }

  protected createIndicators(): IndicatorGroup {
    const indicatorGroup = new IndicatorGroup();
    indicatorGroup.setTrait("status", new StatusTrait());
    return indicatorGroup;
  }

  protected attachIndicators(indicatorGroup: IndicatorGroup): void {
    this.requireUpdate(Model.NeedsAggregate | Model.NeedsReconcile);
  }

  protected detachIndicators(indicatorGroup: IndicatorGroup): void {
    this.requireUpdate(Model.NeedsAggregate | Model.NeedsReconcile);
  }

  protected onInsertIndicator(childIndicator: IndicatorTrait,
                              targetIndicator: IndicatorTrait | null): void {
    this.requireUpdate(Model.NeedsAggregate | Model.NeedsReconcile);
  }

  protected onRemoveIndicator(childIndicator: IndicatorTrait): void {
    this.requireUpdate(Model.NeedsAggregate | Model.NeedsReconcile);
  }

  protected override didRequestUpdate(targetModel: Model, updateFlags: ModelFlags, immediate: boolean): void {
    super.didRequestUpdate(targetModel, updateFlags, immediate);
    if (targetModel !== this.model) {
      this.requireUpdate(updateFlags & (Model.NeedsAggregate | Model.NeedsReconcile));
    }
  }

  override needsAnalyze(analyzeFlags: ModelFlags, modelContext: TraitContextType<this>): ModelFlags {
    if ((this.modelFlags & Model.NeedsAggregate) === 0) {
      analyzeFlags &= ~Model.NeedsAggregate;
    }
    return analyzeFlags;
  }

  override needsRefresh(refreshFlags: ModelFlags, modelContext: TraitContextType<this>): ModelFlags {
    if ((this.modelFlags & Model.NeedsReconcile) === 0) {
      refreshFlags &= ~Model.NeedsReconcile;
    }
    return refreshFlags;
  }

  @TraitModel<IndicatedTrait, IndicatorGroup>({
    key: true,
    type: IndicatorGroup,
    observe: true,
    onSetModel(newIndicatorGroup: IndicatorGroup | null,
               oldIndicatorGroup: IndicatorGroup | null): void {
      if (oldIndicatorGroup !== null) {
        this.owner.detachIndicators(oldIndicatorGroup);
      }
      if (newIndicatorGroup !== null) {
        this.owner.attachIndicators(newIndicatorGroup);
      }
    },
    createModel(): IndicatorGroup | null {
      return this.owner.createIndicators();
    },
    modelDidInsertChildModel(childModel: Model, targetModel: Model | null): void {
      const childIndicator = childModel.getTrait(IndicatorTrait);
      if (childIndicator !== null) {
        const targetIndicator = targetModel !== null ? targetModel.getTrait(IndicatorTrait) : null;
        this.owner.onInsertIndicator(childIndicator, targetIndicator);
      }
    },
    modelWillRemoveChildModel(childModel: Model): void {
      const childIndicator = childModel.getTrait(IndicatorTrait);
      if (childIndicator !== null) {
        this.owner.onRemoveIndicator(childIndicator);
      }
    },
    modelDidStartConsuming(): void {
      this.owner.requireUpdate(Model.NeedsAggregate | Model.NeedsReconcile);
    },
    modelWillStopConsuming(): void {
      this.owner.requireUpdate(Model.NeedsAggregate | Model.NeedsReconcile);
    },
  })
  readonly indicators!: TraitModel<this, IndicatorGroup>;

  static override mountFlags: ModelFlags = GenericTrait.mountFlags | Model.NeedsAggregate | Model.NeedsReconcile;
}
