import type {EntityTrait} from "@swim/lens";
import {ActivityComponent, ActivityPlugin} from "@swim/prism";
import {MetaNodeEntity} from "@swim/fabric";
import {InventoryComponent} from "./InventoryComponent";

export class InventoryPlugin extends ActivityPlugin {
  override get id(): string {
    return "inventory";
  }

  override get title(): string {
    return "Inventory";
  }

  override createActivity(entityTrait: EntityTrait): ActivityComponent | null {
    if (entityTrait instanceof MetaNodeEntity) {
      const activity = new InventoryComponent();
      activity.entity.setTrait(entityTrait);
      return activity;
    }
    return null;
  }
}
